<template>
  <div class="simple-text-editor">
    <div class="editor-wrapper" :style="'height: ' + nRow*1.5 + 'em;'"></div>
  </div>
</template>

<script>
/* eslint-disable no-undef */
import axios from 'axios'
import ImageUploadOverlay from 'assets/js/quill/overlay/image-upload'
import { PadDateTime } from 'assets/js/utils'
import Image from 'assets/js/quill/image'
import { OSS_ENDPOINT, OSS_REGION, OSS_BUCKET } from '@/assets/js/consts/common'

export default {
  name: 'simple-text-editor',
  model: {
    prop: 'content',
    event: 'change',
  },
  props: {
    content: { type: Object, default: () => {} },
    nRow: { type: Number, default: 5 },
    placeholder: { type: String, default: '输入...' },
  },
  data() {
    return {
      quill: null,
    }
  },
  mounted() {
    Quill.register('formats/image', Image, true)
    const editor = this.$el.querySelector('.editor-wrapper')
    const Delta = Quill.import('delta')

    this.quill = new Quill(editor, {
      modules: {
        imageResize: {
          modules: ['Resize', 'DisplaySize'],
        },
        toolbar: {
          container: [
            ['bold', 'italic', 'underline'],
            ['image'],
            // ['image', 'link'],
          ],
          handlers: {
            image: selectAndUploadImage,
          },
        },
      },
      placeholder: this.placeholder,
      theme: 'pad',
      bounds: editor,
      scrollingContainer: editor,
      formats: ['bold', 'italic', 'underline', 'link', 'image', 'width', 'height'],
      // format 缺少一项 占位符
    })
    function selectAndUploadImage() {
      if (!this.quill) return
      const toolbar = this.quill.getModule('toolbar')
      let fileInput = toolbar.container.querySelector('input.ql-image[type=file]')
      if (fileInput == null) {
        fileInput = document.createElement('input')
        fileInput.setAttribute('type', 'file')
        fileInput.setAttribute('accept', 'image/png, image/gif, image/jpeg, image/bmp')
        fileInput.classList.add('ql-image')
        fileInput.addEventListener('change', () => {
          if (!fileInput.files || !fileInput.files[0]) {
            this.$toasted.error('没有选中文件')
            return
          }
          const file_ = fileInput.files[0]
          const ext = file_.name.split('.').pop().toLowerCase()
          if (['jpg', 'jpeg', 'png', 'bmp'].indexOf(ext) < 0 || !(/^image\//.test(file_.type))) {
            this.$toasted.error('仅支持 jpg/jpeg/png/bmp 格式的图片')
          } else {
            // 1. 以dataURL 形式加入 quill editor, 加[上传中]遮罩
            const reader = new FileReader()
            reader.onload = (e) => {
              const range = this.quill.getSelection(true)
              this.quill.updateContents(new Delta()
                .retain(range.index)
                .delete(range.length)
                .insert({ image: e.target.result }), Quill.sources.USER)
              this.quill.setSelection(range.index + 1, Quill.sources.SILENT)
              fileInput.value = ''
              this.quill.disable()
              const [imageLeaf, offset] = this.quill.getLeaf(range.index + 1)
              imageLeaf.domNode.classList.add('image-uploading')
              const imageOverlay = new ImageUploadOverlay(this.quill, this.quill.container)
              imageOverlay.position(imageLeaf.domNode)
              // 2. 上传文件，遮罩层显示上传进度
              axios.get('/pad/j/misc/aliyun/sts').then(response => {
                if (response.data.status === 1) {
                  this.$toasted.error(response.data.detail + ' 请刷新页面后重试', { duration: 2000, position: 'bottom-center' })
                  return
                }
                const ossBucket = new OSS.Wrapper({
                  region: OSS_REGION,
                  bucket: OSS_BUCKET,
                  ...response.data.sts,
                })
                const padDate = new PadDateTime(new Date())
                const fileKey = `richtext_images/${padDate.formatDate('/')}/${Math.random().toString(36).substr(2, 15)}.${ext}`
                ossBucket.multipartUpload(fileKey, file_, {
                  partSize: 1 * 1024 * 128,
                  *progress(p) {
                    imageOverlay.root.innerHTML = `上传中${parseInt(p * 100)}%...`
                  },
                }).then(response => {
                  if (response.res.status === 200) {
                    // 3. 上传成功，图片恢复为正常状态
                    imageLeaf.domNode.classList.remove('image-uploading')
                    imageOverlay.destroy()
                    imageLeaf.domNode.setAttribute('src', `http://${OSS_BUCKET}.${OSS_ENDPOINT}/${fileKey}`)
                    this.quill.enable()
                  }
                }).catch(error => this.handleAliOssException(error))
              }).catch(error => this.handleAliOssException(error))
            }
            reader.readAsDataURL(fileInput.files[0])
          }
        })
        toolbar.container.appendChild(fileInput)
      }
      fileInput.click()
    }

    this.quill.on('text-change', (delta, oldContents, source) => {
      if (source === 'user') {
        this.$emit('change', this.quill.getContents())
      }
    })
    this.quill.setContents(this.content)
  },
  methods: {
    handleAliOssException(error) {
      if (error.name === 'RequestTimeTooSkewedError') {
        this.$toasted.error('请求发起的时间有错。请确保电脑本地时间处于校准状态。校准后刷新页面重新输入富文本。', {
          duration: 2000,
          position: 'bottom-center',
        })
      } else {
        this.$toasted.error(error, { duration: 2000, position: 'bottom-center' })
      }
    },
  }
}
</script>
<style lang="less">
  @import "~assets/css/quill.less";
  .ql-editor {
    overflow-y: unset;
    .image-uploading {
      opacity: .5;
    }
  }

</style>
<style lang="less" scoped>
  .simple-text-editor {
    position: relative;
  }
  .editor-wrapper {
    // height: 200px; // 必须有 height 值
    box-sizing: content-box;
    min-height: 2em;
    max-height: 400px;
    overflow-y: auto;
  }
  .ql-container {
    font-size: 14px;
  }
</style>



