<template>
  <div>
    <div class="container-header">
      <div class="title-section">
        <h2 class="container-title">名校课程-订单列表</h2>
      </div>
      <div class="filter-section">
        <div class="filter-row">
          <span class="filter-row-label">课程：</span>
          <div class="ui selection dropdown" id="course-selector" v-if="courses.length">
            <i class="dropdown icon"></i>
            <span class="text">{{selectedCourse.title}}</span>
            <div class="menu">
              <div
                class="item"
                v-for="course in courses"
                v-if="course.id !== selectedCourse.id"
                @click="selectedCourse=course"
                :key="'course-'+course.id">{{course.title}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="order-list">
      <loader v-if="loading"></loader>
      <div class="content-empty" v-if="!loading && orders.length===0">
        <img :src="require('assets/img/content-empty.png')">
        <p>
          该课程下还没有订单。
        </p>
      </div>
      <div class="flex-1">
        <table class="ui small padded table" v-if="!loading && orders.length"> <thead>
            <tr>
              <th class="left aligned">学生 ID </th>
              <th class="left aligned">姓名 </th>
              <th class="center aligned">校内外</th>
              <th class="center aligned">班级</th>
              <th class="center aligned">地址</th>
              <th class="center aligned">电话</th>
              <th class="center aligned">下单时间</th>
              <th class="center aligned">订单号</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="order of orders">
              <template v-if="order.student">
              <td class="left aligned">{{order.student.id}}</td>
              <td class="left aligned">{{order.student.name}}</td>
              <td class="center aligned">{{order.student.school_id === teacher.school.id ? '校内':'校外'}}</td>
              <td class="center aligned">{{order.student.className}}</td>
              </template>
              <template v-else>
                <td></td>
                <td class="left aligned">{{order.user.name}}</td>
                <td class="center aligned">校外</td>
                <td></td>
              </template>
              <td class="center aligned">{{order.address && order.address.detailAddress}}</td>
              <td class="center aligned">{{order.address && order.address.phone}}</td>
              <td class="center aligned">{{order.createdTime}}</td>
              <td class="center aligned">{{order.id}}</td>
            </tr>
          </tbody>
        </table>
        <pagination
          v-show="!loading"
          class="pagination"
          :totalPage="totalPage"
          :curPage="curPage"
          @update:selected="(page) => {fetchOrders(page)}"></pagination>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import Pagination from 'components/pagination'
import Loader from 'components/loader'
import { HolidayCourse } from '@/assets/js/holiday-course'

const qs = require('qs')

export default {
  name: 'hc-order-list',
  data() {
    return {
      selectedCourse: null,
      courses: [],

      loading: false,
      orders: [],
      curPage: 1,
      totalPage: 0,
    }
  },
  components: { Loader, Pagination },
  mounted() { this.fetchCourses() },
  computed: {
    ...mapState(['school', 'teacher']),
  },
  watch: {
    courses: function(val) {
      if (val) {
        this.$nextTick(() => { $('#course-selector').dropdown({ action: 'hide' }) })
      }
    },
    selectedCourse: function(val) {
      if (val) this.fetchOrders()
    },
  },
  methods: {
    fetchOrders(page = 1) {
      this.loading = true
      axios.get('/pad/j/holiday-course/retrieve/orders', { params: { courseId: this.selectedCourse.id } }).then((response) => {
        if (response.data.status === 0) {
          this.orders = response.data.orders
          this.totalPage = response.data.totalPage
          this.curPage = page
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
        this.loading = false
      })
    },
    fetchCourses() {
      axios.get('/pad/j/holiday-course/retrieve/order-courses').then((response) => {
        if (response.data.status === 0) {
          this.courses = []
          for (const courseData of response.data.courses) {
            courseData.school = this.school
            this.courses.push(new HolidayCourse(courseData))
          }
          this.selectedCourse = this.courses.length && this.courses[0] || null
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
      })
    },
  },
}
</script>

<style lang="less" scoped>
@color-blue: #42A5F5;

.order-list {
  margin: 2rem;
  min-height: 20rem;
  .ui.table {
    color: #666;
  }
}

.ui.padded.table {
  border-radius: 0;
  border-left: none;
  border-right: none;
  td, th {
    padding: .5em 1em;
  }
}

table tr th .text-muted {
  font-weight: normal;
}
.filter-row {
  margin-top: 1rem;
  align-items: center;
}
</style>



