<template>
  <div>
    <loader v-if="loading"></loader>
    <div v-else>
      <div class="content-empty" v-if="!students.length">
        <img :src="require('assets/img/content-empty.png')">
        <p>这里还没有学生...</p>
      </div>
      <table class="pad ui small table" v-else>
        <thead>
          <tr>
            <th>ID</th>
            <th>姓名</th>
            <th v-if="showClasses">正常行政班</th>
            <th v-if="showClasses"><span class="text-muted">归档行政班</span></th>
            <th>学号</th>
            <th>状态</th>
            <th class="right aligned">操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="s of students" :key="'student-row-' + s.id" class="content-row">
            <td>{{s.id}}</td>
            <td class="row-center-title"> {{s.name}} </td>
            <td v-if="showClasses">
              <template v-for="c in s.classes">
                <div v-if="c.isNormal">
                  <span @click="openClassInNewTab(c)" class="link link-primary">{{c.name}}</span>
                  <span class="text-muted">（{{GRADES[c.gradeId]}} - {{c.year}} 学年）</span>
                </div>
              </template>
            </td>
            <td v-if="showClasses">
              <template v-for="c in s.classes">
                <div v-if="c.isArchived">
                  <span @click="openClassInNewTab(c)"class="link link-default">{{c.name}}</span>
                  <span class="text-muted">（{{GRADES[c.gradeId]}} - {{c.year}} 学年）</span>
                </div>
              </template>
            </td>
            <td>{{s.number}}</td>
            <td>{{s.status ? '被封禁' : '正常'}}</td>
            <td class="right aligned" v-if="klass && klass.isArchived">
              <span class="text-muted">已归档班级不能操作</span>
            </td>
            <td class="right aligned" v-else>
              <span
                v-if="isStatusNormal(s) && teacher.isManager"
                class="link link-danger" @click.stop="sToBan=s">封禁</span>
              <span
                v-if="!isStatusNormal(s) && teacher.isManager"
                class="link link-primary" @click.stop="sToRecover=s">解封</span>
              <span
                v-if="klass"
                class="link link-primary"
                @click.stop="sToResetPassword=s">重置密码</span>
              <span class="link link-primary" @click.stop="openEditStudentModal(s)">编辑</span>
              <span v-if="teacher.isManager && klass" class="link link-primary" @click.stop="openTransferModal(s)">转班</span>
              <span v-if="teacher.isManager && klass" class="link link-primary" @click.stop="sToExitClass=s">退班</span>
            </td>
          </tr>
        </tbody>
      </table>
      <pagination
        class="align-left compact"
        :totalPage="totalPage"
        :curPage="curPage"
        @update:selected="(page) => {$emit('update:selected', page)}"
        v-show="students.length"></pagination>
    </div>
    <page-modal
      v-if="sToResetPassword"
      @close="sToResetPassword=null"
      class="modal-small modal-padded">
      <div slot="content">
        <p class="ui small header">重置密码</p>
        <p>确定将 <b>{{sToResetPassword.name}}</b> 的密码重置为班级默认密码吗？</p>
        <button class="ui tiny blue button right floated" @click="postResetPassword($event)">重置</button>
        <button class="ui tiny button right floated" @click="sToResetPassword=null">取消</button>
      </div>
    </page-modal>
    <page-modal
      v-if="sToEdit"
      @close="sToEdit=null"
      class="modal-small modal-padded">
      <div slot="content">
        <p class="ui small header">编辑 {{sToEdit.name}} 基本信息</p>
        <div class="ui small form">
          <div class="field required">
            <label>姓名</label>
            <input type="text" v-model="snameToEdit" placeholder="姓名不能为空">
          </div>
          <div class="field">
            <label>学号</label>
            <input type="text" v-model="snumberToEdit" placeholder="不超过20个字符">
          </div>
          <div class="field">
            <button class="ui tiny blue button" @click="postEditStudent($event)">修改</button>
            <button class="ui tiny button" @click="sToEdit=null">取消</button>
          </div>
        </div>
      </div>
    </page-modal>
    <page-modal
      v-if="sToExitClass && klass"
      @close="sToExitClass=null"
      class="modal-padded modal-small">
      <div slot="content">
        <p class="ui small header">将学生 {{sToExitClass.name}} 退出 {{ klass.name}}</p>
        <p class="text-muted">
          <span class="text-danger">退班后学生将看不到之前在 {{klass.name}} 的作业历史</span>, 适用于一个学年初期的学生调动。<br>
          如果学生是转到其他行政班，并想让学生可以查看之前在 {{klass.name}} 中的作业历史，请使用 <span class="text-danger">转班</span> 功能。
        </p>
        <p>
          <button class="ui tiny blue button right floated" @click="postExitClass($event)">确认退班</button>
          <button class="ui tiny button right floated" @click="sToExitClass=null">取消</button>
        </p>
      </div>
    </page-modal>
    <page-modal
      v-if="sToTransfer && klass"
      @close="sToTransfer=null"
      class="modal-padded modal-small">
      <div slot="content">
        <p class="ui small header">{{sToTransfer.name}} 转班</p>
        <p class="text-muted">
          若学生在{{klass.name}}没什么作业历史，可先退班，在新的行政班处通过ID加入。<br>
          转班学生平板端会列出历史待过的班级，可查看之前班级的作业历史。
        </p>
        <div class="ui small form">
          <div class="field required">
            <label>从 {{klass.name}} 转到同年级行政班</label>
            <div class="ui search selection dropdown">
              <i class="dropdown icon"></i>
              <div class="text"></div>
              <div class="menu">
                <template v-for="c of classesOfSameGrade">
                  <div class="item" v-if="c.id!=klass.id" @click="transferToClassId=c.id"> {{c.name}} </div>
                </template>
              </div>
            </div>
          </div>
          <button class="ui tiny blue button" @click="postTransferClass($event)">确认转班</button>
          <button class="ui tiny button" @click="sToTransfer=null">取消</button>
        </div>
      </div>
    </page-modal>

    <page-modal
      v-if="sToBan"
      @close="sToBan=null"
      class="modal-padded modal-small">
      <div slot="content">
        <p class="ui small header">封禁学生 {{sToBan.name}}</p>
        <p class="text-muted">
          封禁后该账号学生将不能使用系统的所有功能（不能登录平板），确定封禁吗?
        </p>
        <p>
          <button class="ui tiny red button right floated" @click="postBanStudent($event)">确认封禁</button>
          <button class="ui tiny button right floated" @click="sToBan=null">取消</button>
        </p>
      </div>
    </page-modal>
    <page-modal
      v-if="sToRecover"
      @close="sToRecover=null"
      class="modal-padded modal-small">
      <div slot="content">
        <p class="ui small header">解封学生 {{sToRecover.name}}</p>
        <p class="text-muted">
          解封后该账号学生可重新使用系统的所有功能（不能登录平板），确定解封吗?
        </p>
        <p>
          <button class="ui tiny blue button right floated" @click="postRecoverStudent($event)">确认解封</button>
          <button class="ui tiny button right floated" @click="sToRecover=null">取消</button>
        </p>
      </div>
    </page-modal>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import axios from 'axios'
import PageModal from 'components/page-modal'
import Pagination from 'components/pagination'
import Loader from 'components/loader'
import { CLASS_STATUS_NORMAL, GRADES, ACCOUNT_STATUS_NORMAL} from '@/assets/js/consts/common'

const qs = require('qs')

export default {
  name: 'student-paging-list',
  props: {
    klass: { type: Object, default: null }, // 重置密码需要班级信息
    students: { type: Array, required: true },
    loading: { type: Boolean, default: false },
    totalPage: { type: Number, default: 0 },
    curPage: { type: Number, default: 0 },
    showClasses: { type: Boolean, default: false },
  },
  data() {
    return {
      GRADES,
      sToResetPassword: null,

      sToEdit: null,
      snameToEdit: '',
      snumberToEdit: '',

      sToExitClass: null,
      sToTransfer: null,
      transferToClassId: 0,
      classesOfSameGrade: null,

      sToBan: null,
      sToRecover: null,

    }
  },
  components: { Pagination, Loader, PageModal },
  mounted() {
    if (this.teacher.isManager && this.klass) {
      this.fetchClasses()
    }
  },
  computed: {
    ...mapState(['teacher']),
  },
  methods: {
    isStatusNormal: student => student.status === ACCOUNT_STATUS_NORMAL,
    openClassInNewTab(klass) {
      window.open(this.$router.resolve({ name: 'org-class-detail', params: { id: klass.id } }).href, '_blank')
    },
    openEditStudentModal(student) {
      this.snameToEdit = student.name
      this.snumberToEdit = student.number
      this.sToEdit = student
    },
    openTransferModal(student) {
      if (!this.classesOfSameGrade) {
        this.$toasted.info('正在获取转班数据，请稍后1秒后重试...')
        return
      }
      this.transferToClassId = 0
      this.sToTransfer = student
      this.$nextTick(() => { $(this.$el.querySelector('.ui.dropdown')).dropdown() })
    },
    fetchClasses() {
      const params = {
        gradeId: this.klass.gradeId,
        status: CLASS_STATUS_NORMAL,
        fields: ['id', 'name', 'status'],
      }

      axios.get('/pad/j/management/classes/all', { params }).then((response) => {
        if (response.data.status === 0) {
          this.classesOfSameGrade = response.data.classes
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail)
        }
      })
    },
    postTransferClass(event) {
      if (!this.transferToClassId) {
        this.$toasted.error('未指定目标班级')
        return
      }

      const url = `/pad/j/management/students/${this.sToTransfer.id}/transfer/`
      const params = { oldClassId: this.klass.id, newClassId: this.transferToClassId }
      $(event.target).addClass('loading')
      axios.post(url, qs.stringify(params)).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('修改成功')
          this.$emit('studentTransfered', this.sToTransfer)
          this.sToTransfer = null
        }
        $(event.target).removeClass('loading')
      })
    },
    postExitClass(event) {
      const url = `/pad/j/management/classes/${this.klass.id}/students/${this.sToExitClass.id}/exit/`
      $(event.target).addClass('loading')

      axios.post(url).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('修改成功')
          this.$emit('studentExited', this.sToExitClass)
          this.sToExitClass = null
        }
        $(event.target).removeClass('loading')
      })
    },
    postRecoverStudent(event) {
      const url = `/pad/j/management/students/${this.sToRecover.id}/recover/`
      $(event.target).addClass('loading')
      axios.post(url).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('已封禁')
          this.sToRecover = null
          this.$emit('studentUpdated', resp.data.student)
        }
        $(event.target).removeClass('loading')
      })
    },
    postBanStudent(event) {
      const url = `/pad/j/management/students/${this.sToBan.id}/ban/`
      $(event.target).addClass('loading')
      axios.post(url).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('已封禁')
          this.sToBan = null
          this.$emit('studentUpdated', resp.data.student)
        }
        $(event.target).removeClass('loading')
      })
    },
    postEditStudent(event) {
      const url = `/pad/j/management/students/${this.sToEdit.id}/edit/`
      if (!(this.snameToEdit.length && this.snameToEdit.length <= 20)) {
        this.$toasted.error('学生姓名不能为空且不能超过20个字符')
        return
      }
      if (this.snumberToEdit.length > 20) {
        this.$toasted.error('学号不能超过20个字符')
      }

      const params = { name: this.snameToEdit, number: this.snumberToEdit }
      $(event.target).addClass('loading')

      axios.post(url, qs.stringify(params)).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('修改成功')
          this.sToEdit = null
          this.$emit('studentUpdated', resp.data.student)
        }
        $(event.target).removeClass('loading')
      })
    },
    postResetPassword(event) {
      if (!this.klass) {
        this.$toasted.info('请到该学生的行政班页面重置密码。')
      }
      const url = `/pad/j/management/classes/${this.klass.id}/students/${this.sToResetPassword.id}/reset_password/`
      $(event.target).addClass('loading')

      axios.post(url).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('已重置')
          this.sToResetPassword = null
        }
        $(event.target).removeClass('loading')
      })
    },

  },
}
</script>

<style lang="less" scoped>
</style>




