<template>
  <div>
    <div class="container-header">
      <div class="title-section">
        <h2 class="container-title bordered">学生管理
        </h2>
      </div>
    </div>

    <div class="container-section">
      <div class="ui search form">
        <div class="search ui small icon input search-id">
          <input type="text" placeholder="输入学生ID.." v-model.lazy="searchId">
          <i class="ui search icon"></i>
        </div>
        <div class="search ui small icon input search-name">
          <input type="text" placeholder="输入学生姓名，支持模糊搜索.." v-model.lazy="searchName">
          <i class="ui search icon"></i>
        </div>
      </div>
    </div>

    <div class="students-section container-section">
      <span class="text-muted">最多返回50个匹配的结果</span>
      <student-paging-list
        :students="students"
        :loading="loading"
        :curPage="1"
        :totalPage="1"
        :showClasses="true"
        @studentUpdated="student => searchStudent(searchId, searchName)"
        @studentExited="student => searchStudent(searchId, searchName)"
        @studentTransfered="student => searchStudent(searchId, searchName)"></student-paging-list>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import axios from 'axios'
import PageModal from 'components/page-modal'
import Klass from '@/assets/js/class'
import StudentPagingList from './student-paging-list'

const qs = require('qs')

export default {
  name: 'org-class-detail',
  data() {
    return {
      searchName: '',
      searchId: null,

      students: [],
      loading: false,
    }
  },
  components: { StudentPagingList, PageModal },
  mounted() {
  },
  computed: {
    ...mapState(['teacher', 'school']),
  },
  watch: {
    searchName: function(val, oldVal) {
      if (val) {
        this.searchStudent(null, val)
      }
    },
    searchId: function(val, oldVal) {
      if (val) {
        this.searchStudent(val, '')
      }
    }
  },
  methods: {
    searchStudent(id, name) {
      const params = {}
      if (id) params.searchId = id
      else if (name) params.searchName = name

      const url = '/pad/j/management/students/search'
      this.loading = true
      axios.get(url, { params }).then(resp => {
        if (resp.data.status === 0) {
          this.students = []
          for (const data of resp.data.students) {
            data.classes = data.classes.map(classData => new Klass(classData))
            this.students.push(data)
          }
          this.students = resp.data.students
          this.searchId = id
          this.searchName = name
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
        this.loading = false
      })

    }
  },
}
</script>

<style lang="less" scoped>
.container-section {
  margin: 0 2rem 1rem 2rem;
  .ui.form {
    .search-id {
      width: 15rem;
      margin-right: 1rem;
    }
    .search-name {
      width: 20rem
    }
  }
}

</style>



