<template>
  <page-modal
    key="create-class-modal"
    @close="$emit('close')">
    <div slot="content">
      <h3 class="ui header">创建通知</h3>
      <div class="ui left small form">
        <div class="field required">
          <label>标题</label>
          <input type="text" v-model="newNotice.title" placeholder="不超过 50 个字符...">
        </div>
        <div class="field required">
          <label>内容</label>
          <textarea rows="5" v-model="newNotice.content" placeholder="不超过 500 个字符..."></textarea>
        </div>
        <div class="inline field attachment">
          <label>添加附件</label>
          <template v-if="newNotice.attachmentFileKey">
            <span class="file-uploaded-label">{{newNotice.attachmentFileName}} 已上传 <i class="teal check circle icon"></i></span>
            <span class="link link-danger" @click="deleteAttachment()">删除文件重新上传</span>
          </template>
          <span class="file-input-wrapper" v-show="!newNotice.attachmentFileKey">
            <button class="ui mini button">选择文件上传</button>
            <label for="btn-add-attachment"></label>
            <input type="file" id="btn-add-attachment" @click="e => e.target.value = null">
            <span class="text-muted note">非必传，支持 doc,docx,ppt,pptx,pdf,jpg,jpeg,bmp,png,mp3,mp4,xls,xlsx，不超过100M</span>
            <span class="text-muted progress"></span>
          </span>
        </div>
        <div class="inline field">
          <label>发布时间</label>
          <time-picker place-holder-text="立即发送"
            key="res-publish-time"
            v-model="newNotice.publishTime"
            :minDate="minDate"
            :maxDate="maxDate"
            ></time-picker>
            <span class="text-muted">只支持15天之内的定时设置</span>
        </div>
        <br>
        <div class="inline field required">
          <label>发送给</label>
          <span>
            <div class="ui radio checkbox" v-if="teacher.isManager">
              <input type="radio" v-model="newNotice.sendType" :value="ORG_SCHOOL" id="radio-for-school">
              <label for="radio-for-school">全校</label>
            </div>
            <div class="ui radio checkbox" v-if="teacher.isManager || teacher.isHeadTeacher">
              <input type="radio" v-model="newNotice.sendType" :value="ORG_CLASS" id="radio-for-class">
              <label for="radio-for-class">行政班</label>
            </div>
            <div class="ui radio checkbox" v-if="teacher.isSubjectMaster || teacher.isCourseTeacher">
              <input type="radio" v-model="newNotice.sendType" :value="ORG_COURSE" id="radio-for-course">
              <label for="radio-for-course">教学班</label>
            </div>
          </span>
          <a class="link link-default" id="notice-permission-btn">发送权限说明</a>
          <div class="ui flowing popup top right transition hidden" id="notice-permission-popup">
            <p>
              任课老师：可给所教的教学班级发通知。<br>
              班主任：可给所带行政班级发通知。<br>
              学科组长: 还可给本年级本学科的所有<strong>教学班</strong>发通知。<br>
              管理员: 还给全校学生或者本校所有的<strong>行政班</strong>发送通知。<br>
              ----------<br>
              以上教学班和行政班不包括已归档的班级。
            </p>
          </div>
          <div class="target-area" v-if="newNotice.sendType===ORG_CLASS">
            <div class="ui mini inline active loader" v-if="targetData.loading"></div>
            <div v-else v-for="[gid, gclasses] of classesGrouped" class="section">
              <h4>
                {{GRADES[gid]}}
                <a class="link link-default" @click="checkAllItems(gclasses)">全部选中</a>
                <a class="link link-default" @click="uncheckAllItems(gclasses)">全部取消</a>
              </h4>
              <div class="ui mini checkbox" v-for="c of gclasses">
                <input type="checkbox" class="hidden" :value="c.id" v-model="newNotice.targetIds" :id="'target-class-'+c.id">
                <label :for="'target-class-'+c.id">{{c.name}}</label>
              </div>
            </div>
          </div>
          <div class="target-area" v-if="newNotice.sendType===ORG_COURSE">
            <div class="ui mini inline active loader" v-if="targetData.loading"></div>
            <div v-else class="section">
              <h4>
                教学班
                <a class="link link-default" @click="checkAllItems(targetData.courses)">全部选中</a>
                <a class="link link-default" @click="uncheckAllItems(targetData.courses)">全部取消</a>
              </h4>
              <div class="ui mini checkbox" v-for="c of targetData.courses">
                <input type="checkbox" class="hidden" :value="c.id" v-model="newNotice.targetIds" :id="'target-course-'+c.id">
                <label :for="'target-course-'+c.id">{{c.name}}</label>
              </div>
            </div>
          </div>
        </div>
        <button class="ui small blue button" @click="postCreateNotice($event)">创建</button>
      </div>
    </div>
  </page-modal>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
import { mapState } from 'vuex'
import PageModal from 'components/page-modal'
import TimePicker from 'components/time-picker'
import {
  GRADES,
  SORTED_GRADES,
  SUPPORTED_FILE_EXTS,
  OSS_REGION,
  OSS_BUCKET,
  ORG_SCHOOL,
  ORG_CLASS,
  ORG_COURSE,
} from '@/assets/js/consts/common'

export default {
  name: 'create-notice-modal',
  data() {
    return {
      ORG_SCHOOL,
      ORG_CLASS,
      ORG_COURSE,
      GRADES,
      targetData: {
        classes: [],
        classFetched: false,
        courses: [],
        courseFetched: false,
        loading: false,
      },
      newNotice: {
        title: '',
        content: '',
        sendType: '', // 可以是 ORG_SCHOOL, ORG_CLASS, ORG_COURSE,
        targetIds: [],
        publishTime: null,
        attachmentFileKey: '',
        attachmentFileName: '',
      },
    }
  },
  components: { PageModal, TimePicker },
  mounted() {
    $('#notice-permission-btn').popup({
      popup: $('#notice-permission-popup'),
      on: 'click',
    })
    this.$el.querySelector('#btn-add-attachment').addEventListener('change', (event) => {
      this.uploadAttachmentToTmp(event)
    })
  },
  watch: {
    'newNotice.sendType': function(val) {
      this.newNotice.targetIds = []
      if (val === ORG_CLASS && !this.targetData.classFetched) {
        this.fetchClasses()
      } else if (val === ORG_COURSE && !this.targetData.courseFetched) {
        this.fetchCourses()
      }
    },
  },
  computed: {
    minDate: () => new Date(),
    maxDate: () => {
      const max = new Date()
      max.setDate(max.getDate() + 15)
      return max
    },
    classesGrouped() {
      if (!this.targetData.classes.length) {
        return []
      }

      const groupedDict = {}
      for (const c of this.targetData.classes) {
        if (c.gradeId in groupedDict) {
          groupedDict[c.gradeId].push(c)
        } else {
          groupedDict[c.gradeId] = [c]
        }
      }
      const groupedArr = []

      for (const gid of SORTED_GRADES) {
        if (gid in groupedDict) {
          groupedArr.push([gid, groupedDict[gid]])
        }
      }
      return groupedArr
    },
    ...mapState(['teacher']),
  },
  methods: {
    deleteAttachment() {
      this.newNotice.attachmentFileKey = ''
      this.newNotice.attachmentFileName = ''
    },
    uploadAttachmentToTmp(event) {
      const files = event.target.files
      if (!files.length) return
      if (files.length > 1) {
        this.$toasted.error('只能选择上传一个文件', { duration: 800, position: 'bottom-center' })
        return
      }
      const button = event.target.parentElement.querySelector('button')
      const note = event.target.parentElement.querySelector('.note')
      const progress = event.target.parentElement.querySelector('.progress')
      const f = files[0]
      if (f.size > 100 * 1024 * 1024) {
        this.$toasted.error('附件大小不能超过100M')
        return
      }
      const ext = f.name.split('.').pop().toLowerCase()
      if (SUPPORTED_FILE_EXTS.indexOf(ext) < 0) {
        this.$toasted.error('文件格式不支持')
        return
      }

      axios.get('/pad/j/misc/aliyun/sts').then(resp => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail, { duration: 2000, position: 'bottom-center' })
          return
        }
        const ossBucket = new OSS.Wrapper({ region: OSS_REGION, bucket: OSS_BUCKET, ...resp.data.sts })

        const now = new Date()
        const prefix = `media/pad/notice-temp/${now.getFullYear()}/${now.getMonth() + 1}/${now.getDate()}/`
        const fileKey = `${prefix + Math.random().toString(36).substr(2, 15)}.${ext}`

        button.classList.add('loading')
        $(note).hide()

        ossBucket.multipartUpload(fileKey, f, {
          partSize: 1 * 1024 * 128,
          *progress(p) {
            progress.textContent = `${parseInt(p * 100)}%..`
          },
        }).then((response) => {
          if (response.res.status === 200) {
            this.newNotice.attachmentFileKey = fileKey
            this.newNotice.attachmentFileName = f.name
            button.classList.remove('loading')
            $(note).show()
            progress.textContent = ''
          }
        }).catch(error => this.handleAliOssException(error, button))
      }).catch(error => this.handleAliOssException(error, button))
    },
    handleAliOssException(error, button) {
      if (error.name === 'RequestTimeTooSkewedError') {
        this.$toasted.error('请求发起的时间有错。请确保电脑本地时间处于校准状态。', {
          duration: 2000,
          position: 'bottom-center',
        })
      } else {
        this.$toasted.error(error, { duration: 2000, position: 'bottom-center' })
      }
      button.classList.remove('loading')
    },
    uncheckAllItems(items) {
      for (const item of items) {
        const index = this.newNotice.targetIds.indexOf(item.id)
        if (index >= 0) {
          this.newNotice.targetIds.splice(index, 1)
        }
      }
    },
    checkAllItems(items) {
      for (const item of items) {
        if (!this.newNotice.targetIds.includes(item.id)) {
          this.newNotice.targetIds.push(item.id)
        }
      }
    },
    canPostCreateNotice() {
      const n = this.newNotice
      n.title = n.title.trim()
      if (n.title.length <= 0 || n.title.length >= 50) {
        this.$toasted.error('标题不能为空且不能超过50个字符。')
        return
      }
      n.content = n.content.trim()
      if (n.content.length <= 0 || n.content.length >= 500) {
        this.$toasted.error('内容不能为空且不能超过500个字符。')
        return
      }
      if (![ORG_SCHOOL, ORG_CLASS, ORG_COURSE].includes(n.sendType)) {
        this.$toasted.error('请选择发送目标')
        return
      }
      if (n.sendType !== ORG_SCHOOL && !n.targetIds.length) {
        this.$toasted.error('请选择发送目标')
        return
      }
      return true
    },
    postCreateNotice(event) {
      const canPost = this.canPostCreateNotice()
      if (!canPost) return

      const url = '/pad/j/management/notices/create/'
      const params = { ...this.newNotice }
      params.targetIds = JSON.stringify(this.newNotice.targetIds)
      delete params.attachmentFileName
      params.publishTime = this.newNotice.publishTime && new Date(this.newNotice.publishTime)

      $(event.target).addClass('loading')
      axios.post(url, qs.stringify(params)).then((response) => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail)
        } else if (response.data.status === 0) {
          this.$toasted.success('已创建通知')
          this.$emit('success')
          this.$emit('close')
        }
        $(event.target).removeClass('loading')
      })
    },
    fetchClasses() {
      const url = '/pad/j/management/classes/accessible/normal'
      this.targetData.loading = true
      return axios.get(url).then(resp => {
        if (resp.data.status === 0) {
          this.targetData.classes = resp.data.classes
          this.targetData.classFetched = true
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
        this.targetData.loading = false
      })
    },
    fetchCourses() {
      const url = '/pad/j/management/courses/accessible/normal'
      this.targetData.loading = true
      return axios.get(url).then(resp => {
        if (resp.data.status === 0) {
          this.targetData.courses = resp.data.courses
          this.targetData.courseFetched = true
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
        this.targetData.loading = false
      })
    },
  },
}
</script>

<style lang="less" scoped>
.target-area {
  background-color: #f8f8f8;
  margin-top: .5em;
  border-radius: .5em;
  padding: .5em 1em;
  min-height: 2em;

  .ui.table {
    .grade-label {
      width: 6em;
    }
  }

  h4 {
    font-size: 14px;
    margin: .2em 0 .6em 0;
  }
  .link.link-default {
    font-weight: normal;
    margin-left: 1em;
    font-size: 12px;
  }

  .ui.mini.checkbox  {
    min-width: 10em;
    margin-bottom: .3em;
    font-size: 12px;

    label {
      color: #505050;
    }
  }
  .section {
    padding: 1em 0;
    border-bottom: solid 1px #e6e6e6;
  }
}

.target-area .section:last-child {
  border-bottom: none;
}

.ui.popup {
  font-size: 13px;
  color: #666;
}

.attachment {
  .file-row {
    margin: .8em 0;
  }
  .file-uploaded-label {
    font-weight: bold;
    font-size: .9em;
    color: #666;
  }

  .file-input-wrapper {
    display: inline-block;
    position: relative;
    input {
      display: none;
    }
    label {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      width: 100%;
    }
  }
}
</style>



