<template>
  <div class="weekly-schedule" :class="editMode?'in-editing':''">
    <div class="section-empty schedule-empty" v-if="!schedule">
      <div class="ui small indeterminate text active loader" v-if="scheduleLoading">获取课表信息</div>
      <p v-else>
      本教室还没有课表 <button class="ui small blue button" @click="openTimetable">创建周课表</button>
      </p>
    </div>

    <div v-else>
      <div class="status-header">
        <span class="text">{{s.startDate}} - {{s.endDate}}</span>
        <div class="actions" v-if="editMode">
          <span class="text-danger">课表编辑中...</span>
          <button class="ui tiny button" @click="exitEditing">退出编辑</button>
          <button class="ui tiny primary button" @click="updateScheduleGrids">保存更改</button>
        </div>
        <div class="actions" v-else>
          <span class="link link-default" @click="openTimetable"> <i class="clock outline icon"></i>修改课时时间</span>
          <span class="link link-primary" @click="editMode=true"> <i class="edit icon"></i>编辑排课</span>
        </div>
      </div>
      <table class="schedule-table" v-if="schedule">
        <thead>
          <tr>
            <th></th>
            <th v-for="weekday in 7"> {{WEEKDAY_ID_LABEL_MAP[weekday]}} </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="seq in s.dayGridCount">
            <td class="seq-col">
              第 {{seq}} 节<br>
              <span class="text-muted">
                {{s.gridAt(1, seq).startTime}}<br>{{s.gridAt(1, seq).endTime}}
              </span>
            </td>
            <td v-for="weekday in 7" class="lesson-holder">
              <div
                :class="getGridClasses(weekday, seq)"
                @click="editGrid(s.gridAt(weekday, seq))">
                <div
                  v-if="s.grids[weekday][seq].course"
                  class="lesson-filled">
                  <h4 class="text-large">{{SUBJECTS[s.gridAt(weekday, seq).course.subjectId]}}</h4>
                  <span class="text-secondary course-name">{{s.gridAt(weekday, seq).course.name}}</span>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <keep-alive>
      <page-modal
        v-if="gridToEdit"
        @close="gridToEdit=null"
        class="modal-padded course-selector-modal">
        <div slot="content">
          <div class="cascader-header">
            <h4 class="ui header">设置 {{WEEKDAY_ID_LABEL_MAP[gridToEdit.weekday]}}-第{{gridToEdit.seq}}节 的教学班：
              <span
                v-if="gridToEdit.course"
                class="ui course-label label">
                {{gridToEdit.course.name}}
                <span class="action-icon" @click="gridToEdit.course=null"><icon name="slim-remove"></icon></span>
              </span>
            </h4>
            <span class="text-muted">鼠标移动到年级和学科上自动筛选，无需点击。</span>
          </div>
          <course-cascader
            :school="school"
            v-model="gridToEdit.course"
            :value="gridToEdit.course"
            @input="course => selectedCourseForGrid(course)"
            ></course-cascader>
        </div>
      </page-modal>
    </keep-alive>

    <page-modal
      v-if="timetableOpened"
      @close="timetableOpened = false">
      <div slot="content" class="timetable-modal">
        <div>
          <h4>设置课表应用的起止日期</h4>
          <el-date-picker
            v-model="scheduleDateRange"
            type="daterange"
            size="small"
            range-separator="至"
            value-format="yyyy-MM-dd"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </div>
        <day-timetable
          ref="timetable"
          v-model="timeTable">
        </day-timetable>
        <p>
          <button
            class="ui primary button"
            v-if="!schedule"
            @click="preCreateSchedule($event)">创建周课表</button>
          <button
            class="ui primary button"
            v-else
            @click="preUpdateScheduleTimeTable($event)">更新课表时间</button>
        </p>
      </div>
    </page-modal>
  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
import { mapState } from 'vuex'
import ClassroomSchedule from 'assets/js/classroom-schedule'
import {
  WEEKDAY_ID_LABEL_MAP,
  SUBJECTS,
} from 'assets/js/consts/common'

import CourseCascader from 'components/course-cascader'
import Icon from 'components/icons/Icon'
import 'components/icons/slim-remove'
import PageModal from 'components/page-modal'
import DayTimetable from './day-timetable'

export default {
  name: 'weekly-schedule',
  props: {
    classroom: { type: Object, required: true },
  },
  data() {
    return {
      WEEKDAY_ID_LABEL_MAP,
      SUBJECTS,
      timetableOpened: false,
      scheduleDateRange: [],
      timeTable: [],
      scheduleLoading: true,
      gridToEdit: null,
      dupSchedule: null,
      schedule: null,
      editMode: false,
    }
  },
  components: { CourseCascader, Icon, PageModal, DayTimetable },
  created() {
    this.fetchSchedule()
  },
  watch: {
    editMode: function (val) {
      // 每次启用编辑的时候，请求获得 schedule 的最新数据，并做一份拷贝。
      if (val) this.fetchSchedule()
    },
  },
  computed: {
    s() {
      return this.editMode && this.dupSchedule || this.schedule
    },
    ...mapState(['school']),
  },
  methods: {
    getGridClasses(weekday, seq) {
      const grid = this.s.gridAt(weekday, seq)
      const eleClasses = ['lesson-grid']
      if (grid.course) eleClasses.push('grid-filled')
      if (!this.editMode) eleClasses.push('read-only')
      return eleClasses
    },
    openTimetable() {
      this.timetableOpened = true
    },
    preCreateSchedule(event) {
      if (!(this.scheduleDateRange && this.scheduleDateRange.length === 2)) {
        this.$toasted.error('请设置课表的起止日期')
        return
      }
      const result = this.$refs.timetable.validate()
      if (result) {
        this.createSchedule(event)
      }
    },
    preUpdateScheduleTimeTable(event) {
      if (!(this.scheduleDateRange && this.scheduleDateRange.length === 2)) {
        this.$toasted.error('请设置课表的起止日期')
        return
      }
      const result = this.$refs.timetable.validate()
      if (result) {
        this.updateScheduleTimeTable(event)
      }
    },
    createSchedule(event) {
      const url = `/classboard/j/classrooms/${this.$route.params.id}/schedule/create/`
      const durationArr = this.timeTable.map(lesson => lesson.duration)
      const params = {
        startDate: this.scheduleDateRange[0],
        endDate: this.scheduleDateRange[1],
        durations: JSON.stringify(durationArr),
      }

      $(event.target).addClass('loading')
      return axios.post(url, qs.stringify(params)).then(resp => {
        if (resp.data.status === 0) {
          this.fetchSchedule()
          this.timetableOpened = false
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
        $(event.target).removeClass('loading')
      })
    },
    updateScheduleTimeTable(event) {
      const url = `/classboard/j/classrooms/${this.$route.params.id}/schedule/timetable/update/`
      const durationArr = this.timeTable.map(lesson => lesson.duration)
      const params = {
        startDate: this.scheduleDateRange[0],
        endDate: this.scheduleDateRange[1],
        durations: JSON.stringify(durationArr),
      }
      $(event.target).addClass('loading')
      return axios.post(url, qs.stringify(params)).then(resp => {
        if (resp.data.status === 0) {
          this.fetchSchedule()
          this.timetableOpened = false
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
        $(event.target).removeClass('loading')
      })
    },
    updateScheduleGrids(event) {
      const url = `/classboard/j/classrooms/${this.$route.params.id}/schedule/grids/update/`
      const dayGridCount = this.dupSchedule.dayGridCount

      const gridsData = []

      for (let weekday = 1; weekday <= 7; weekday++) {
        for (let seq = 1; seq <= dayGridCount; seq++) {
          const newCourseId = this.dupSchedule.courseIdAt(weekday, seq)
          if (this.schedule.courseIdAt(weekday, seq) !== newCourseId) {
            gridsData.push([weekday, seq, newCourseId])
          }
        }
      }
      if (!gridsData.length) {
        this.$toasted.show('还没有任何修改')
        return
      }
      const params = { gridsData: JSON.stringify(gridsData) }
      $(event.target).addClass('loading')
      return axios.post(url, qs.stringify(params)).then(resp => {
        if (resp.data.status === 0) {
          this.$toasted.info('更新成功')
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
        $(event.target).removeClass('loading')
      })
    },
    fetchSchedule() {
      const url = `/classboard/j/classrooms/${this.classroom.id}/schedule`
      return axios.get(url).then(resp => {
        if (resp.data.status === 0) {
          if (resp.data.schedule) {
            this.schedule = new ClassroomSchedule(resp.data.schedule)
            this.dupSchedule = new ClassroomSchedule(resp.data.schedule)
            this.scheduleDateRange = [this.schedule.startDate, this.schedule.endDate]
            this.timeTable = this.schedule.timeTable
          }
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
        this.scheduleLoading = false
      })
    },
    selectedCourseForGrid(course) {
      this.gridToEdit.course = course
      this.gridToEdit = null
    },
    editGrid(grid) {
      this.gridToEdit = grid
    },
    exitEditing() {
      this.fetchSchedule().then(() => {
        this.editMode = false
      })
    },
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";
.schedule-empty {
  padding: 4em 0;
}
.timetable-modal {
  padding: 1em 5em 2em 5em;
}
.weekly-schedule.in-editing {
  .schedule-table{
    box-shadow: 1px 7px 12px 8px rgba(0,0,0,.1);
  }
}
.weekly-schedule {
  .status-header {
    display: flex;
    .text {
      flex: 1;
    }
    .actions {
      text-align: right;
    }
  }
}
.schedule-table {
  margin-top: 1em;
  width: 100%;
  border-spacing: 0;
  tr th, tr td {
    text-align: center;
  }
  tr td.lesson-holder {
    width: 13%;
  }
  tr td {
    padding: 4px;
    border-bottom: solid 1px #f0f0f0;
  }

  tr th {
    background-color: #f0f0f2;
    line-height: 2;
  }

  tbody tr:last-child td {
    border-bottom: none;
  }
  .lesson-grid {
    height: 6em;
    border-radius: .4px;
    background-image: repeating-linear-gradient(-45deg, transparent, transparent 15px, #f0f0f0 15px, #f0f0f0 16px);
    background-color: #fcfcfc;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    &.grid-filled {
      background-color: #ebf3f6;
      border: solid 1px @color-blue-50;
      background-image: none;
    }
    &.read-only {
      pointer-events: none;
    }

    .lesson-filled {
      width: 100%;
      padding: .3em;
      text-align: center;
      h4 {
        margin: .3em 0 .5em 0;
        color: #222;
      }
      .course-name {
        font-size: .9rem;
        height: 2rem;
        line-height: 1rem;

        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        display: -webkit-box;

      }
    }
  }

}

.course-selector-modal {
  .header {
    margin-bottom: 1em;
    .ui.course-label.label {
      background-color: @color-blue-50;
      border: solid 1px @color-blue-200;
      margin-bottom: .3em;
      margin-right: 1em;
      margin-left: 0;
      border-radius: 2px;
      padding: 3px;
      font-size: .9em;
      color: rgba(102, 102, 102, .9);

      .action-icon {
        padding-left: .2em;
        font-size: .8em;
        display: inline-block;
        color: @color-red;
        vertical-align: top;
        cursor: pointer;
        pointer-events: auto;
        svg {
          margin: 0;
        }
      }
    }
  }
}
</style>

<style lang="less">
.page-modal.course-selector-modal{
  .modal-container {
    max-width: 45em;
  }
}

</style>



