
export class ClassroomScheduleGrid {
  constructor(sourceData) {
    this.id = sourceData.id
    this.scheduleId = sourceData.scheduleId
    this.weekday = sourceData.weekday
    this.seq = sourceData.seq
    this.course = sourceData.course
    this.startTime = sourceData.startTime
    this.endTime = sourceData.endTime
  }

  attachCourse(course) {
    this.course = course
  }
}

export default class ClassroomSchedule {
  constructor(sourceData) {
    this.id = sourceData.id
    this.classroomId = sourceData.classroomId
    this.startDate = sourceData.startDate
    this.endDate = sourceData.endDate

    this.grids = {}
    for (let i = 1; i <= 7; i++) {
      this.grids[i] = {}
    }

    for (const gridData of sourceData.grids) {
      const grid = new ClassroomScheduleGrid(gridData)
      this.grids[grid.weekday][grid.seq] = grid
    }
  }

  gridAt(weekday, seq) {
    return this.grids[weekday][seq]
  }

  get dayGridCount() {
    return Object.keys(this.grids[1]).length
  }

  get timeTable() {
    const arr = []
    const dayGridCount = this.dayGridCount
    for (let seq = 1; seq <= dayGridCount; seq++) {
      const grid = this.gridAt(1, seq)
      arr.push({ duration: [grid.startTime, grid.endTime] })
    }
    return arr
  }

  courseIdAt(weekday, seq) {
    const grid = this.grids[weekday][seq]
    return grid.course && grid.course.id || 0
  }
}



