<template>
  <div>
    <div class="container-header">
      <div class="title-section">
        <h2 class="container-title">名校课程</h2>
        <div class="actions">
          <button
            v-if="teacher.isAdmin"
            @click="openDupCourseModal"
            class="ui primary tiny blue button">Admin 复制课程</button>
          <button
            v-if="courses.length"
            @click="$router.push({ name: 'hc-order-list' })"
            class="ui primary tiny blue button">查看订单</button>
          <button
            @click="$router.push({ name: 'hc-create-course' })"
            class="ui primary tiny blue button">创建课程</button>
        </div>
      </div>
      <div class="filter-section">
        <div class="pill-filter filter-row" v-if="school.qingbeiPeiyouOpened">
          <span class="filter-row-label">课程：</span>
          <div>
            <span
              class="filter-label"
              @click="selectedCourseType=0"
              :class="selectedCourseType===0?'active':''">全部</span>
            <span
              v-for="(label, key) in COURSE_TYPE_MAP"
              class="filter-label"
              @click="selectedCourseType=parseInt(key)"
              :class="selectedCourseType===parseInt(key)?'active':''">{{label}}</span>
          </div>
        </div>
        <div class="pill-filter filter-row">
          <span class="filter-row-label">学科：</span>
          <div>
            <span
              class="filter-label"
              @click="selectedSubjectId=0"
              :class="selectedSubjectId===0?'active':''">全部</span>
            <span
              class="filter-label"
              v-for="(label, sid) in subjects"
              @click="selectedSubjectId=sid"
              :class="selectedSubjectId===sid?'active':''">{{label}}</span>
          </div>
        </div>
        <div class="pill-filter filter-row">
          <span class="filter-row-label">状态：</span>
          <div>
            <span
              class="filter-label"
              @click="selectedState=0"
              :class="selectedState===0?'active':''">全部</span>
            <template v-for="(label, state) in states">
              <span
                class="filter-label"
                v-if="parseInt(state)!==hc_state_deleted"
                @click="selectedState=parseInt(state)"
                :class="selectedState===parseInt(state)?'active':''">{{label}}</span>
            </template>
          </div>
        </div>
        <div class="filter-row">
          <span class="filter-row-label">日期：</span>
          <time-picker
            place-holder-text="只显示包含选中日期的课程"
            :can-clear="true"
            :enable-time="false"
            date-format="Y-m-d"
            alt-format="Y-m-d"
            v-model="includeDate"></time-picker>
        </div>
        <div class="filter-row input-align">
          <span class="filter-row-label">搜索：</span>
          <div class="search ui small icon input">
            <input
              type="text"
              placeholder="搜索老师或课程名.."
              v-model="searchText"
              @keyup.enter="fetchCourses()">
            <i class="ui search icon" @click="fetchCourses()"></i>
          </div>
        </div>
      </div>
    </div>
    <div class="course-list">
      <loader v-if="loading"></loader>
      <div class="content-empty" v-if="!loading && courses.length===0">
        <img :src="require('assets/img/content-empty.png')">
        <p>
          什么也没有，试试更改筛选条件或者
          <span
            class="link link-primary"
            @click=" $router.push({ name: 'hc-create-course' })">创建课程</span>
        </p>
      </div>
      <div class="flex-1">
        <table class="ui small padded table" v-if="!loading && courses.length"> <thead>
            <tr>
              <th class="left aligned">课程<br>
                <span class="text-muted">点击可查看/编辑</span>
              </th>
              <th class="center aligned" v-if="school.qingbeiPeiyouOpened">课程类型</th>
              <th class="center aligned">展示设备</th>
              <th class="center aligned">课时数</th>
              <th class="center aligned">学科</th>
              <th class="center aligned">主讲老师</th>
              <th class="center aligned">课堂资料</th>
              <th class="center aligned">状态</th>
              <th class="right aligned">操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="course of courses">
              <td
                class="left aligned course-title"
                @click=" $router.push({ name: 'hc-edit-course', params: { courseId: course.id } })">
                <p>{{course.title}}</p>
                <span class="text-muted">课程时间: {{course.startDate}} ~ {{course.endDate}}</span><br>
                <span class="text-muted">参与权限: {{course.applyConditionDisplay}}</span>
              </td>
              <td class="center aligned" v-if="school.qingbeiPeiyouOpened">{{course.courseTypeLabel}}</td>
              <td class="center aligned">{{course.deviceTypeLabel}}</td>
              <td class="center aligned">{{course.lessonCount}}</td>
              <td class="center aligned">{{subjects[course.subjectId]}}</td>
              <td class="center aligned">{{course.mainTeacherName}}</td>
              <td class="center aligned">{{course.materialFilled ? '全' : '未补全'}}</td>
              <td class="center aligned">
                <div class="actions">
                  <span class="label pad-state" :class="courseStateClass(course)">
                    <i class="circle icon"></i>{{course.stateDisplay}}
                  </span>
                </div>
              </td>
              <td class="right aligned">
                <span
                  class="link link-primary"
                  v-if="canPublish(course)"
                  @click="publishPrecheck(course)">发布</span>
                <span
                  class="link link-danger"
                  v-if="canSuspend(course)"
                  @click="courseToSuspend=course">停课</span>
                <span
                  class="link link-danger"
                  v-if="canDelete(course)"
                  @click="courseToDelete=course">删除</span>
              </td>
            </tr>
          </tbody>
        </table>
        <pagination
          v-show="!loading"
          class="pagination"
          :totalPage="totalPage"
          :curPage="curPage"
          @update:selected="(page) => {fetchCourses(page)}"></pagination>
      </div>
    </div>

    <confirm-modal v-if="courseToPublish" @close="courseToPublish=null">
      <template slot="title">确定要发布此课程吗？</template>
      <template slot="text">一经发布，学生和老师即可看到。课程将不能删除（可以暂停），部分信息将不能进行编辑。</template>
      <template slot="actions">
        <button class="ui button" @click="courseToPublish=null">取消</button>
        <button class="ui orange button" @click="publishCourse(courseToPublish)">发布</button>
      </template>
    </confirm-modal>

    <confirm-modal v-if="courseToDelete" @close="courseToDelete=null">
      <template slot="title">确定要删除此课程吗？</template>
      <template slot="text">删除后，数据将不能恢复。</template>
      <template slot="actions">
        <button class="ui button" @click="courseToDelete=null">取消</button>
        <button class="ui red button" @click="deleteCourse(courseToDelete)">删除</button>
      </template>
    </confirm-modal>

    <confirm-modal v-if="courseToSuspend" @close="courseToSuspend=null">
      <template slot="title">确定要暂停此课程吗？</template>
      <template slot="text">暂定后此课程将对未购买的学生隐藏。</template>
      <template slot="actions">
        <button class="ui button" @click="courseToSuspend=null">取消</button>
        <button class="ui red button" @click="suspendCourse(courseToSuspend)">停课</button>
      </template>
    </confirm-modal>

    <page-modal
      v-if="allSchools.length && dupCourseModalOpened"
      @close="closeDupCourseModal"
      class="modal-padded modal-small">
      <div slot="content">
        <p class="ui small header">复制课程</p>
        <div class="ui small form">
          <div class="field required">
            <label class="text-muted">要复制的课程ID</label>
            <input type="number" v-model="dupCourseData.fromCourseId" placeholder="课程ID必填">
            <span class="text-muted">{{dupCourseData.courseMsg}}</span>
          </div>
          <div class="field required">
            <label>复制到学校</label>
            <div class="ui search selection dropdown">
              <input type="hidden" v-model="dupCourseData.toSchoolId">
              <i class="dropdown icon"></i>
              <div class="text"></div>
              <div class="menu">
                <div
                  class="item"
                  v-for="s of allSchools"
                  @click="dupCourseData.toSchoolId=s.id"
                  :data-value="s.id"> {{s.name}} </div>
              </div>
            </div>
          </div>
          <button class="ui tiny blue button" @click="postDupCourse">复制课程</button>
          <button class="ui tiny button" @click="closeDupCourseModal">取消</button>
        </div>
      </div>
    </page-modal>

  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import ConfirmModal from 'components/confirm-modal'
import PageModal from 'components/page-modal'
import Pagination from 'components/pagination'
import 'components/icons/university'
import 'components/icons/sign-out'
import Icon from 'components/icons/Icon'
import TimePicker from 'components/time-picker'
import Loader from 'components/loader'
import {
  NORMAL_SUBJECTS,
} from '@/assets/js/consts/common'

import {
  HC_STATES_NAME_MAP,
  HC_STATE_EDITING,
  HC_STATE_PUBLISHED,
  HC_STATE_SUSPENDED,
  HC_STATE_DELETED,
  COURSE_TYPE_MAP,
} from '@/assets/js/consts/holiday-course'

import { HolidayCourse } from '@/assets/js/holiday-course'

const qs = require('qs')
const subjects = Object.assign({99: '综合'}, NORMAL_SUBJECTS)

export default {
  name: 'hc-course-list',
  data() {
    return {
      subjects,
      states: HC_STATES_NAME_MAP,
      selectedSubjectId: 0,
      selectedCourseType: 0,
      selectedState: 0,
      includeDate: null,
      searchText: '',
      courses: [],
      curPage: 1,
      totalPage: 0,
      hc_state_deleted: HC_STATE_DELETED,
      loading: false,
      courseToPublish: null,
      courseToDelete: null,
      courseToSuspend: null,
      COURSE_TYPE_MAP,
      dupCourseModalOpened: false,
      dupCourseData: {
        fromCourseId: 0,
        toSchoolId: 0,
        courseMsg: '未指定源课程',
      },
      allSchools: [],
    }
  },
  components: { Icon, TimePicker, Pagination, Loader, ConfirmModal, PageModal },
  mounted() {
    this.fetchCourses()
    if (this.teacher.isAdmin) this.fetchAllSchools()
  },
  watch: {
    'dupCourseData.fromCourseId': function (val) {
      if (!val) this.dupCourseData.courseMsg = '未指定源课程'
      else {
        const url = `/pad/j/holiday-course/${val}`
        axios.get(url).then(resp => {
          if (resp.data.status === 0) {
            const course = resp.data.course
            if (course.state === HC_STATE_DELETED) {
              this.$toasted.error(`ID为${val}的课程已被删除，不能由此复制课程`)
              this.dupCourseData.fromCourseId = 0
            } else {
              this.dupCourseData.courseMsg = `课程名：${resp.data.course.title}`
            }
          } else {
            this.$toasted.error(resp.data.detail)
            this.dupCourseData.courseMsg = '找不到指定课程'
          }
        })
      }
    },
    selectedState: function () {
      this.fetchCourses()
    },
    selectedCourseType: function () {
      this.fetchCourses()
    },
    selectedSubjectId: function () {
      this.fetchCourses()
    },
    includeDate: function () {
      this.fetchCourses()
    },
  },
  computed: {
    ...mapState(['school', 'teacher']),
  },
  methods: {
    postDupCourse() {
      if (!(this.dupCourseData.fromCourseId && this.dupCourseData.toSchoolId)) {
        this.$toasted.error('课程ID和学校必填')
        return
      }

      const url = `/pad/j/holiday-course/${this.dupCourseData.fromCourseId}/duplicate/`
      const params = { toSchoolId: this.dupCourseData.toSchoolId }
      axios.post(url, qs.stringify(params)).then(resp => {
        if (resp.data.status === 0) {
          this.$toasted.success('复制成功，请到对应学校查看')
          this.closeDupCourseModal()
          this.fetchCourses()
        } else {
          this.$toasted.error(resp.data.detail)
        }
      })
    },
    openDupCourseModal() {
      if (!this.allSchools.length) return
      this.dupCourseModalOpened = true
      this.$nextTick(() => {
        $(this.$el.querySelectorAll('.dropdown')).dropdown()
      })
    },
    closeDupCourseModal() {
      this.dupCourseData.fromCourseId = 0
      this.dupCourseData.toSchoolId = 0
      this.dupCourseData.courseMsg = '未指定源课程'
      this.dupCourseModalOpened = false
    },
    fetchAllSchools() {
      if (!this.teacher.isAdmin) return
      const url = '/pad/j/manage/school/list'
      const params = { fields: ['id', 'name'] }
      axios.get(url, { params }).then(resp => {
        if (resp.data.status === 0) {
          this.allSchools = resp.data.schools
        } else {
          this.$toasted.error(resp.data.detail)
        }
      })
    },
    courseStateClass(course) {
      if (course.state === HC_STATE_EDITING) return 'grey'
      if (course.state === HC_STATE_PUBLISHED) return 'teal'
      return 'red'
    },
    canPublish: course => [HC_STATE_EDITING, HC_STATE_SUSPENDED].includes(course.state),
    canSuspend: course => course.state === HC_STATE_PUBLISHED,
    canDelete: course => course.state === HC_STATE_EDITING,
    publishPrecheck(course) {
      const url = `/pad/j/holiday-course/${course.id}/precheck`
      axios.get(url).then(response => {
        if (response.data.status === 0) {
          this.courseToPublish = course
        } else {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
      })
    },
    replaceCourse(oldCourse, newCourse = null) {
      const index = this.courses.indexOf(oldCourse)
      if (index >= 0) {
        if (newCourse) this.courses.splice(index, 1, newCourse)
        else this.courses.splice(index, 1)
      }
    },
    publishCourse(course) {
      const params = {
        action: 'change-state',
        state: HC_STATE_PUBLISHED,
      }
      axios.post(`/pad/j/holiday-course/${course.id}/edit/`, qs.stringify(params)).then(response => {
        if (response.data.status === 0) {
          this.$toasted.info('已发布', { duration: 1000, position: 'bottom-center' })
          const newCourse = new HolidayCourse({ school: this.school, ...response.data.course})
          this.replaceCourse(course, newCourse)
          this.courseToPublish = null
        } else {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
      })
    },
    deleteCourse(course) {
      const params = {
        action: 'change-state',
        state: HC_STATE_DELETED,
      }
      const url = `/pad/j/holiday-course/${course.id}/edit/`
      axios.post(url, qs.stringify(params)).then(response => {
        if (response.data.status === 0) {
          this.$toasted.info('已删除', { duration: 1000, position: 'bottom-center' })
          this.replaceCourse(course, null)
          this.courseToDelete = null
        } else {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
      })
    },
    suspendCourse(course) {
      const params = {
        action: 'change-state',
        state: HC_STATE_SUSPENDED,
      }
      axios.post(`/pad/j/holiday-course/${course.id}/edit/`, qs.stringify(params)).then(response => {
        if (response.data.status === 0) {
          this.$toasted.info('已停课', { duration: 1000, position: 'bottom-center' })
          const newCourse = new HolidayCourse({ school: this.school, ...response.data.course })
          this.replaceCourse(course, newCourse)
          this.courseToSuspend = null
        } else {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
      })
    },
    fetchCourses(page = 1) {
      const params = {
        subjectId: this.selectedSubjectId,
        courseType: this.selectedCourseType,
        state: this.selectedState,
        searchText: this.searchText,
        includeDate: this.includeDate,
        page: page,
      }
      this.loading = true
      axios.get('/pad/j/holiday-course/retrieve', { params }).then((response) => {
        if (response.data.status === 0) {
          this.courses = []
          for (const courseData of response.data.courses) {
            courseData.school = this.school
            this.courses.push(new HolidayCourse(courseData))
          }
          this.totalPage = response.data.totalPage
          this.curPage = page
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
        this.loading = false
      })
    },
  },
}
</script>

<style lang="less" scoped>
@color-blue: #42A5F5;
.ui.search.icon.input {
  >i.search.icon {
    cursor: pointer;
    pointer-events: auto;
  }
}
.course-list {
  position: relative;
  margin: 1rem 2rem;
  min-height: 20rem;
  .ui.table {
    color: #666;
  }
  .course-title {
    color: #444;
    transition: .3s color linear;
    p {
      vertical-align: middle;
      font-size: 1.1rem;
      margin: .3rem 0;
      font-weight: bold;
      min-width: 15rem;
    }
    &:hover, &:focus, &:active {
      cursor: pointer;
      color: @color-blue;
    }
  }
}

.ui.padded.table {
  border-radius: 0;
  border-left: none;
  border-right: none;
  td, th {
    padding: .5em 1em;
  }
}

table tr th .text-muted {
  font-weight: normal;
}
</style>



