<template>
  <div class="classroom-calendar">
    <div class="status-header">
      <span class="text"></span>
      <div class="actions">
        <el-date-picker
          v-model="selectedDate"
          type="week"
          placeholder="选择周查看"
          format="yyyy 年 WW 周"
          :picker-options="pickerOption"
          :clearable="false"></el-date-picker>
      </div>
    </div>

    <div class="weekly-calendar-table">
      <div class="weekday-col" v-for="d of weekDates">
        <div class="col-title">{{d.formatStr.slice(5)}}<br>{{d.weekdayLabel}}</div>
        <div class="col-content">
          <div class="lesson-grid" v-for="grid of events[d.formatStr]" :data-start-time="grid.startTime">
            {{SUBJECTS[grid.course.subjectId]}}<br>
            {{grid.course.name}}<br>
            <span class="text-muted">{{grid.startTime}} - {{grid.endTime}}</span>
          </div>

        </div>
      </div>

    </div>

  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
import { mapState } from 'vuex'
import ClassroomSchedule from 'assets/js/classroom-schedule'
import {
  WEEKDAY_ID_LABEL_MAP,
  SUBJECTS,
} from 'assets/js/consts/common'
import dayjs from 'dayjs'
import weekOfYear from 'dayjs/plugin/weekOfYear'
dayjs.extend(weekOfYear)

import Icon from 'components/icons/Icon'
import 'components/icons/slim-remove'
import PageModal from 'components/page-modal'

export default {
  name: 'classroom-calendar',
  props: {
    classroom: { type: Object, required: true },
  },
  data() {
    const now = new Date()
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())
    const thisSunday = new Date(today.getFullYear(), today.getMonth(), today.getDate() + 7 - today.getDay())

    return {
      SUBJECTS,
      selectedDate: new Date(2018, 11, 25),
      events: {},
      calendarStartMinutes: 360,
      calendarEndMinutes: 1140,
      pickerOption: {
        firstDayOfWeek: 1,
        disabledDate: (d) => d > thisSunday,
      },
    }
  },
  components: { Icon, PageModal },
  created() {
    this.fetchEvents()
  },
  watch: {
    selectedDate: function (val, oldVal) {
      if (dayjs(val).week() !== dayjs(oldVal).week()) {
        this.fetchEvents()
      }
    },
  },
  computed: {
    monday() {
      const d = this.selectedDate
      return new Date(d.getFullYear(), d.getMonth(), d.getDate() - d.getDay())
    },
    sunday() {
      const d = this.selectedDate
      return new Date(d.getFullYear(), d.getMonth(), d.getDate() + 7 - d.getDay())
    },
    weekDates() {
      const dates = []
      const curDay = dayjs(this.monday)
      for (let i = 0; i <= 7; i++) {
        dates.push({
          formatStr: curDay.add(i, 'day').format('YYYY-MM-DD'),
          weekdayLabel: WEEKDAY_ID_LABEL_MAP[curDay.add(i, 'day').day()],
        })
      }
      return dates
    },
    ...mapState(['school']),
  },
  methods: {
    getTotalMinutesByTimeStr(timeStr) {
      const [hours, minutes] = timeStr.split(':')
      return parseInt(hours) * 60 + parseInt(minutes)
    },
    processEventsData(events) {
      this.events = {}
      for (const d of this.weekDates) {
        this.events[d.formatStr] = []
      }

      this.calendarStartTime = 360
      this.calendarEndTime = 1140
      for (const e of events) {
        const arr = this.events[e.date]
        let index = 0
        for (; index < arr.length; index++) {
          if (arr[index].startTime > e.startTime) break
        }
        const startMinutes = this.getTotalMinutesByTimeStr(e.startTime)
        const endMinutes = this.getTotalMinutesByTimeStr(e.endTime)
        if (startMinutes < this.calendarStartTime) {
          this.calendarStartTime = startMinutes - 10 > 0 && startMinutes - 10 || 0
        }
        if (endMinutes > this.calendarEndTime) {
          this.calendarEndTime = endMinutes + 10 < 1440 && endMinutes + 10 || 1440
        }
        arr.splice(index, 0, e)
      }
      this.$nextTick(() => {
        const gridEls = this.$el.querySelectorAll('.lesson-grid')
        const colContentHeight = this.$el.querySelector('.col-content').clientHeight - 50;
        for (const el of gridEls) {
          const startTime = el.dataset.startTime
          const [hours, minutes] = startTime.split(':')
          const minutesOffset = parseInt(hours) * 60 + parseInt(minutes) - this.calendarStartMinutes
          const calendarMinutesSpan = this.calendarEndMinutes - this.calendarStartTime
          el.style.top = colContentHeight * minutesOffset / calendarMinutesSpan + 'px'
        }
      })
    },
    fetchEvents() {
      const params = {
        startDate: dayjs(this.monday).format('YYYY-MM-DD'),
        endDate: dayjs(this.sunday).format('YYYY-MM-DD'),
      }

      const url = `/classboard/j/classrooms/${this.classroom.id}/calendar/events`
      return axios.get(url, { params }).then(resp => {
        if (resp.data.status === 0) {
          this.processEventsData(resp.data.events)
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
      })
    },
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";
.schedule-empty {
  padding: 4em 0;
}

.classroom-calendar {
  .status-header {
    display: flex;
    .text {
      flex: 1;
    }
    .actions {
      text-align: right;
    }
  }
}

.weekly-calendar-table {
  margin-top: 1em;
  display: flex;
  width: 100%;
  .weekday-col {
    width: 15%;
    text-align: center;
    border: solid 1px #eee;
    border-left: none;
    .col-title {
      font-weight: bold;
      background-color: #f9f9f9;
      border-bottom: solid 1px #eee;
      padding-top: .2em;
      padding-bottom: .2em;
    }
    .col-content {
      height: 960px;
      position: relative;
      background-image: repeating-linear-gradient(-45deg, transparent, transparent 15px, #f0f0f0 15px, #f0f0f0 16px);
      .lesson-grid {
        background-color: #ebf3f6;
        position: absolute;
        width: ~"calc(100% - 2px)";
        margin-left: 1px;
        font-size: 12px;
      }
    }
  }
}
.weekly-calendar-table .weekday-col:first-child {
    border-left: solid 1px #eee;
}

</style>




