<template>
  <div>
    <div class="container-header">
      <div class="title-section">
        <h2 class="container-title">我发布的通知</h2>
        <div class="actions">
          <button class="ui primary tiny blue button" @click="openCreateNoticeModal()">创建通知</button>
        </div>
      </div>
    </div>
    <div class="notice-list">
      <loader v-if="loading"></loader>
      <div class="content-empty" v-if="!loading && notices.length===0">
        <img :src="require('assets/img/content-empty.png')">
        <p>
          什么也没有，试试更改筛选条件
        </p>
      </div>
      <div class="flex-1">
        <table class="pad ui small table" v-if="!loading && notices.length"> <thead>
            <tr>
              <th class="left aligned notice-content">通知</th>
              <th class="center aligned">状态</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="n of notices"
              :key="'notice-row-' + n.id"
              class="notice-row">
              <td class="left aligned notice-content">
                <h3>{{n.title}}
                  <span
                    v-if="n.attachmentFType"
                    @click="previewAttachment(n)"
                    class="link link-primary"> <i class="attach icon"></i>查看附件</span>
                </h3>
                <p>{{n.content}}</p>
                <span class="text-muted">发送目标：{{n.targets.map(t=>t.name).join('、')}}</span><br>
                <span class="text-muted">创建时间：{{n.createdTime}}</span>
                <span class="text-muted">发布时间：{{n.publishTime}}</span>
              </td>
              <td class="center aligned notice-action">
                <p>
                  {{NOTICE_STATES_NAME_MAP[n.status]}}
                  <a
                    v-if="n.status === NOTICE_STATE_TIMING"
                    @click="noticeToDelete=n"
                    class="link link-danger">删除通知</a>
                </p>
                <p v-if="n.status === NOTICE_STATE_TIMING">
                  <span>将在 {{n.publishTime}} 发布</span>
                </p>
              </td>
            </tr>
          </tbody>
        </table>
        <pagination
          v-show="!loading"
          class="pagination"
          :totalPage="totalPage"
          :curPage="curPage"
          @update:selected="(page) => {fetchNotices(page)}"></pagination>
      </div>
    </div>
    <create-notice-modal
      v-if="createNoticeModalOpened"
      key="create-class-modal"
      @close="createNoticeModalOpened=false"
      @success="fetchNotices()"
      class="modal-large modal-padded">
    </create-notice-modal>
    <resource-preview :previewItem="previewItem" @close="previewItem=null"></resource-preview>

    <confirm-modal
      v-if="noticeToDelete"
      @close="noticeToDelete=null">
      <template slot="title">确定要删除这条定时中的通知吗？</template>
      <template slot="text">删除后，学生将不会收到这条通知，且无法恢复。
      </template>
      <template slot="actions">
        <button class="ui button" @click="noticeToDelete=null">取消</button>
        <button class="ui red button" @click="postDeleteNotice($event)">删除</button>
      </template>
    </confirm-modal>

  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import ConfirmModal from 'components/confirm-modal'
import Pagination from 'components/pagination'
import Loader from 'components/loader'
import {
  NOTICE_STATES_NAME_MAP,
  NOTICE_STATE_TIMING,
  GRADES,
  K_PAD_DOCUMENT,
} from '@/assets/js/consts/common'
import ResourcePreview from '@/components/resource-preview'

import CreateNoticeModal from './create-notice-modal'


export default {
  name: 'feat-notice-list',
  data() {
    return {
      NOTICE_STATES_NAME_MAP,
      NOTICE_STATE_TIMING,
      GRADES,
      notices: [],

      curPage: 1,
      totalPage: 0,
      loading: false,

      createNoticeModalOpened: false,
      previewItem: null,

      noticeToDelete: null,
    }
  },
  components: { Pagination, Loader, ConfirmModal, CreateNoticeModal, ResourcePreview },
  mounted() {
    this.fetchNotices()
  },
  watch: {
    'targetData.sendType': function (val) {
      if (val === 'class' && !this.targetData.classFetched) {
        this.fetchClasses()
      } else if (val === 'course' && !this.targetData.courseFetched) {
        this.fetchCourses()
      }
    },
  },
  computed: {
    ...mapState(['teacher']),
  },
  methods: {
    postDeleteNotice() {
      if (!this.noticeToDelete) this.$toasted.error('找不到要删除的通知。')
      const url = `/pad/j/management/notices/${this.noticeToDelete.id}/delete/`

      $(event.target).addClass('loading')
      axios.post(url).then(response => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail)
        } else if (response.data.status === 0) {
          this.$toasted.success('已删除')
          if (this.notices.length > 1) {
            const index = this.notices.indexOf(this.noticeToDelete)
            this.notices.splice(index, 1)
          } else {
            const page = this.curPage - 1 || 1
            this.fetchNotices(page)
          }
          this.noticeToDelete = null
        }
        $(event.target).removeClass('loading')
      })
    },
    openCreateNoticeModal() {
      const t = this.teacher
      if (t.isManager || t.isSubdjectMaster || t.isHeadTeacher || t.isCourseTeacher) {
        this.createNoticeModalOpened = true
      } else {
        this.$toasted.error('你当前没有权限发送通知。')
      }
    },
    previewAttachment(notice) {
      this.previewItem = {
        fileUrl: notice.attachmentUrl,
        kind: K_PAD_DOCUMENT,
        id: notice.id,
        fileType: notice.attachmentFType,
        name: `${notice.title}的附件`,
      }
    },
    fetchNotices(page = 1) {
      const url = '/pad/j/management/notices'
      const params = { page }
      this.loading = true
      return axios.get(url, { params }).then(resp => {
        if (resp.data.status === 0) {
          this.notices = resp.data.notices
          this.curPage = page
          this.totalPage = resp.data.totalPage
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
        this.loading = false
      })
    },
  },
}
</script>

<style lang="less" scoped>
.notice-list {
  position: relative;
  margin: 2rem 2rem;
  min-height: 20rem;
  .ui.table {
    color: #555;
    .content {
      width: 50%;
    }
  }
}
.ui.small.table .notice-row {
  .text-muted {
    font-size: .95em;
  }
  .notice-content {
    h3 {
      font-size: 1.1rem;
      margin: .6em 0;
      color: #333;
    }
    p {
      font-size: 1rem;
      margin: .5em 0;
      line-height: 1.5;
    }
  }
  .notice-action {
    width: 20em;
  }
}
</style>



