<template>
  <div class="course-cascader">
    <div class="list-panel">
      <ul>
        <li
          v-for="gid of school.gradeIds"
          @mouseenter="selectedGradeId=gid"
          @click="selectedGradeId=gid"
          class="expandable"
          :class="selectedGradeId===gid?'active':''"
          ><span>{{GRADES[gid]}}</span><icon name="angle-right"></icon></li>
      </ul>
    </div>
    <div class="list-panel" v-if="selectedGradeId">
      <ul>
        <li
          v-for="sid of SUBJECT_EXTENDED_ORDER"
          v-if="sid !== 0"
          @mouseenter="selectedSubjectId=sid"
          @click="selectedSubjectId=sid"
          class="expandable"
          :class="selectedSubjectId===sid?'active':''"
          ><span>{{SUBJECTS[sid]}}</span><icon name="angle-right"></icon></li>
      </ul>
    </div>
    <div class="list-panel course-list" v-if="selectedGradeId && selectedSubjectId">
      <div
        v-if="loading"
        class="ui tiny indeterminate text active loader">获取教学班...</div>
      <template v-else>
        <ul v-if="courses.length">
          <li
            v-for="course of courses"
            @click="selectCourse(course)"
            ><span>{{course.name}}</span></li>
        </ul>
        <div class="content-empty" v-else>
          <img :src="require('assets/img/content-empty.png')">
          <p>该条件下没有教学班</p>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import _ from 'lodash'
import {
  SUBJECT_EXTENDED_ORDER,
  SUBJECTS,
  GRADES,
  COURSE_STATUS_NORMAL,
} from 'assets/js/consts/common'

import Icon from 'components/icons/Icon'
import 'components/icons/angle-right'
import 'components/icons/angle-down'

export default {
  name: 'course-cascader',
  props: {
    school: { type: Object, required: true },
    value: { type: Object },
  },
  data() {
    return {
      SUBJECT_EXTENDED_ORDER,
      SUBJECTS,
      GRADES,
      selectedGradeId: null,
      selectedSubjectId: null,
      courses: [],
      loading: false,
      coursesCache: {},
    }
  },
  components: { Icon },
  watch: {
    selectedSubjectId(val) {
      if (!val) return
      const cacheKey = `${this.selectedGradeId}_${val}`
      if (!this.coursesCache[cacheKey]) {
        this.debounceFecthCourses(this.selectedGradeId, val, this)
      } else {
        this.courses = this.coursesCache[cacheKey]
      }
    },
  },
  methods: {
    selectCourse(course) {
      this.$emit('input', course)
    },
    debounceFecthCourses: _.debounce((gradeId, subjectId, vm) => {
      vm.fetchCourses(gradeId, subjectId)
    }, 300),
    fetchCourses(gradeId, subjectId) {
      if (!(gradeId && subjectId)) {
        this.$toasted.error('请先选择年级和学科')
        return
      }
      const params = {
        gradeId,
        subjectId,
        status: COURSE_STATUS_NORMAL,
        fields: ['id', 'name', 'subjectId', 'gradeId', 'teacher'],
      }
      const url = '/pad/j/management/courses/all'
      this.loading = true
      return axios.get(url, { params }).then(resp => {
        if (resp.data.status === 0) {
          this.courses = resp.data.courses
          const cacheKey = `${gradeId}_${subjectId}`
          this.coursesCache[cacheKey] = this.courses
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
        this.loading = false
      })
    },
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";

.course-cascader {
  display: flex;
  align-items: flex-start;

  .list-panel {
    position: relative;
    overflow-y: scroll;
    height: 28em;
    width: 12em;
    min-width: 8em;
    border-right: solid 1px #f0f0f0;
    .content-empty {
      img {
        max-width: 60px;
      }
    }
  }
  .list-panel.course-list {
    min-width: 16em;
    flex: 1;
  }

  ul {
    color: #666;
    list-style-type: none;
    padding: .5em 0;
    margin: 0;
    li {
      padding: .4em .5em .4em 1em;
      cursor: pointer;
      display: flex;
      span {
        width: max-content;
        min-width: 6em;
        margin-right: .5em;
        flex: 1;
      }
      &:hover, &:focus, &:active {
        background-color: #f4f4f4;
      }
      &.active {
        color: @color-blue;
      }
      .fa-icon {
        margin-left: 1em;
      }
    }
  }
}
.course-cascader .list-panel:last-child {
  border-right: none;
}
</style>



