import Vue from 'vue'
import Vuex from 'vuex'
import VueRouter from 'vue-router'
import Toasted from 'vue-toasted'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'
import qs from 'qs'
import axios from 'axios'

import School from 'assets/js/school'
import Navbar from 'components/navbar'

import {
  Radio,
  DatePicker,
  TimePicker,
} from 'element-ui'
import '@/theme/element-variables.scss'

import VerticalNavbar from './vertical-navbar'
import Breadcrumb from './breadcrumb'
import HCCourseList from './holiday-courses/course-list'
import HCCreateCourse from './holiday-courses/create-course'
import HCEditCourse from './holiday-courses/edit-course'
import HCOrderList from './holiday-courses/order-list'

import OrgClassList from './organization/class-list'
import OrgStudentList from './organization/student-list'
import OrgClassDetail from './organization/class-detail'

import ClassroomList from './classroom/classroom-list'
import ClassroomDetail from './classroom/classroom-detail'

import NoticeList from './features/notice-list'

require('./index.less')

Vue.config.productionTip = false
Vue.use(DatePicker)
Vue.use(TimePicker)
Vue.use(Radio)

axios.defaults.headers.common.X_REQUESTED_WITH = 'XMLHttpRequest'
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN'
axios.defaults.xsrfCookieName = 'csrftoken'

if (process.env.NODE_ENV === 'production') {
  Vue.prototype.$raven = Raven
  Raven.config('http://216e3d581cd9424fad7f5d8c046b2f64@sentry.xdf100.com/6').addPlugin(RavenVue, Vue).install()
}

Vue.use(Vuex)
Vue.use(Toasted, { duration: 1000, position: 'bottom-center' })
Vue.use(VueRouter)

Vue.prototype.$ELEMENT = { size: 'mini', zIndex: 3000 }

const router = new VueRouter({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/pad/management' : '/static/w/pages/management.html',
  routes: [
    {
      path: '',
      name: 'management-index',
      redirect: { name: 'org-class-list' },
      meta: {
        title: '管理主页',
        bcAncestors: [],
        bcDynamic: false,
      },
    },
    {
      path: '/notices',
      component: NoticeList,
      name: 'feat-notice-list',
      meta: {
        title: '通知列表',
        bcAncestors: ['management-index'],
        bcDynamic: false,
      },
    },
    {
      path: '/students',
      component: OrgStudentList,
      name: 'org-student-list',
      meta: {
        title: '学生列表',
        bcAncestors: ['management-index'],
        bcDynamic: false,
      },
    },
    {
      path: '/classrooms',
      component: ClassroomList,
      name: 'classroom-list',
      meta: {
        title: '教室列表',
        bcAncestors: ['management-index'],
        bcDynamic: false,
      },
    },
    {
      path: '/classrooms/:id',
      component: ClassroomDetail,
      name: 'classroom-detail',
      meta: {
        title: '教室详情',
        bcAncestors: ['management-index'],
        bcDynamic: false,
      },
    },
    {
      path: '/classes',
      component: OrgClassList,
      name: 'org-class-list',
      meta: {
        title: '行政班列表',
        bcAncestors: ['management-index'],
        bcDynamic: false,
      },
    },
    {
      path: '/classes/:id',
      component: OrgClassDetail,
      name: 'org-class-detail',
      meta: {
        title: '行政班详情',
        bcAncestors: ['management-index', 'org-class-list'],
        bcDynamic: true,
      },
    },
    {
      path: '/holiday-courses/create',
      component: HCCreateCourse,
      name: 'hc-create-course',
      meta: {
        title: '创建名校课程',
        bcAncestors: ['management-index', 'hc-course-list'],
        bcDynamic: false,
        bcText: '新建课程',
      },
    },
    {
      path: '/holiday-courses',
      component: HCCourseList,
      name: 'hc-course-list',
      meta: {
        title: '名校课程列表',
        bcAncestors: ['management-index'],
        bcDynamic: false,
      },
    },
    {
      path: '/holiday-courses/order-list',
      component: HCOrderList,
      name: 'hc-order-list',
      meta: {
        title: '名校课程订单列表',
        bcAncestors: ['management-index', 'hc-course-list'],
        bcDynamic: false,
        bcText: '订单列表',
      },
    },
    {
      path: '/holiday-courses/:courseId',
      component: HCEditCourse,
      name: 'hc-edit-course',
      meta: {
        title: '编辑名校课程',
        bcAncestors: ['management-index', 'hc-course-list'],
        bcDynamic: true,
        bcText: '编辑课程',
      },
    },
  ],
  linkActiveClass: 'active',
})

const store = new Vuex.Store({
  state: {
    teacher: null,
    school: null,
  },
  mutations: {
    updateTeacher(state, payload) {
      state.teacher = payload.teacher
    },
    updateSchool(state, payload) {
      state.school = payload.school
    },
  },
})


router.beforeEach((to, from, next) => {
  document.title = to.meta.title
  if (!store.state.teacher) {
    axios.get('/pad/j/teacher_info').then((response) => {
      const teacher = response.data.teacher
      store.commit('updateTeacher', { teacher })
      if (process.env.NODE_ENV === 'production') {
        Raven.setUserContext({ username: teacher.name, id: teacher.id })
      }
      const paramsStr = qs.stringify(
        { field: ['classes', 'id', 'name', 'programLength', 'hasPrimaryGrade', 'holidayCourseOpened', 'qingbeiPeiyouOpened'] },
        { indices: false },
      )
      axios.get(`/pad/j/fetch/school?${paramsStr}`).then((resp) => {
        if (resp.data.status === 0) {
          const school = new School(resp.data.school)
          store.commit('updateSchool', { school })
        }
        next()
      })
    })
  } else {
    next()
  }
})


new Vue({
  delimiters: ['${', '}'],
  el: '#app',
  store,
  router,
  data: {},
  components: { Navbar, VerticalNavbar, Breadcrumb },
})



