<template>
<modal @close="$emit('close')" class="modal-default modal-large modal-material-uploader" key="material-uploader-modal" :withFooter="false">
  <h3 slot="header">上传课时资料</h3>
  <div slot="body" class="modal-body-container">
    <form class="upload-form ui form">
      <div class="field">
        <div class="pad-dnd text-muted" id="dropzone" >
          <p class="instruction">
            <i class="ui cloud upload icon"></i>
          </p>
          <p class="instruction">将文件拖到此处上传（支持多个文件），或 <span class="link link-primary">点击选择文件</span>上传。<br>支持的格式：doc, docx, ppt, pptx, pdf, jpg, jpeg, bmp, png, mp3, mp4, xls, xlsx</p>
          <label for="file-selector"></label>
          <input type="file" id="file-selector" multiple>
        </div>
        <table class="ui small table" v-show="files.length">
          <thead>
            <tr>
              <th>文件名(不带扩展名)<br><span class="text-muted">编辑文件名</span></th>
              <th class="center aligned">格式 / 大小</th>
              <th class="center aligned">类型</th>
              <th class="center aligned">上传<br>进度</th>
              <th class="center aligned">操作<br></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="fw of files">
              <td>
                <input type="text" v-model="fw.displayName" maxlength="50" required="">
              </td>
              <td class="center aligned">{{fw.ext}} / {{sizeDisplay(fw.file.size)}}</td>
              <th class="center aligned">
                <div class="ui slection dropdown">
                  <i class="dropdown icon"></i>
                  <span class="text">{{classKindMap[fw.classKind]}}</span>
                  <div class="menu">
                    <div
                      v-for="(label, key) in classKindMap"
                      v-if="parseInt(key) !== fw.classKind"
                      @click="selectClassKind(fw, parseInt(key), $event)"
                      :key="'classkind-'+key"
                      class="item">{{label}}</div>
                  </div>
                </div>
              </th>
              <td class="center aligned negative" v-if="fw.status === 2">上传失败，请重试</td>
              <td class="center aligned" :class="fw.progress === 100?'positive':''" v-else>{{progressDisplay(fw)}}</td>
              <td class="center aligned"><span class="link link-danger" @click="removeFromFiles(fw)">删除</span></td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <th class="note" id="foot-note">选好文件后再点击开始上传，请勿重复上传文件。</th>
              <th></th>
              <th></th>
              <th></th>
              <th class="right aligned"><button class="ui small primary button" @click.prevent="startUploading">开始上传</button></th>
            </tr>
          </tfoot>
        </table>
      </div>
    </form>
  </div>
</modal>
</template>

<script>
import Modal from 'components/modal'
import axios from 'axios'
import { AFTER_CLASS, OSS_REGION, OSS_BUCKET, CLASS_KIND_MAP } from '@/assets/js/consts/common'
import { HCLessonMaterial } from '@/assets/js/holiday-course'

const qs = require('qs')

export default {
  name: 'hc-material-uploader',
  props: {
    course: { type: Object, required: true },
    lesson: { type: Object, required: true },
  },
  data() {
    return {
      uploader: null,
      uploaderDnd: null,
      files: [],
      messages: [],
      classKindMap: CLASS_KIND_MAP,
    }
  },
  components: { Modal },
  mounted() {
    const dropZone = this.$el.querySelector('#dropzone')
    const fileInput = this.$el.querySelector('#file-selector')

    dropZone.addEventListener('dragover', (e) => {
      dropZone.classList.add('drag-over')
      e.stopPropagation()
      e.preventDefault()
      e.dataTransfer.dropEffect = 'copy'
    })
    dropZone.addEventListener('drop', (e) => {
      dropZone.classList.remove('drag-over')
      e.stopPropagation()
      e.preventDefault()
      for (const f of e.dataTransfer.files) {
        this.validateAndPushFile(f)
      }
      for (const m of this.messages) {
        this.$toasted.error(m, {
          duration: 1200,
          position: 'bottom-center',
        })
      }
      this.messages = []
    })
    fileInput.addEventListener('change', () => {
      if (fileInput.files.length) {
        for (const f of fileInput.files) {
          this.validateAndPushFile(f)
        }
      }
      for (const m of this.messages) {
        this.$toasted.error(m, {
          duration: 1200,
          position: 'bottom-center',
        })
      }
      this.messages = []
    })
  },
  methods: {
    selectClassKind(f, classKind) {
      f.classKind = classKind
      $(this.$el).find('.ui.dropdown').dropdown('hide')
    },
    validateAndPushFile(f) {
      // 名字相同的文件不会重复进入队列
      for (const fileWrapper of this.files) {
        if (fileWrapper.file.name === f.name) return null
      }
      const ext = f.name.split('.').pop().toLowerCase()
      if (f.name.length <= (ext + 1)) { return null }
      if (['jpg', 'jpeg', 'png', 'bmp', 'mp3', 'mp4', 'pdf', 'doc', 'docx', 'ppt', 'pptx', 'xls', 'xlsx'].indexOf(ext) < 0) {
        this.messages.push(`${f.name} 文件格式不支持, 未加入上传列表`)
        return
      }
      const fileWrapper = {
        ext,
        file: f,
        progress: 0,
        displayName: f.name.slice(0, -(ext.length + 1)),
        status: 0,
        classKind: AFTER_CLASS,
      }
      // 0 未上传 1 上传成功 2 上传失败 这里的值和文档创建相关，不表示阿里云是否上传成功
      this.files.push(fileWrapper)
      this.$nextTick(() => {
        $(this.$el).find('.ui.dropdown').dropdown({
          action: 'hide',
        })
      })
    },
    removeFromFiles(f) {
      const index = this.files.indexOf(f)
      if (index >= 0) {
        this.files.splice(index, 1)
      }
    },
    sizeDisplay(size) {
      if (size > 1000000) return `${(size / 1000000).toFixed(1)}MB`
      if (size > 1000) return `${(size / 1000).toFixed(1)}KB`
      return `${size}B`
    },
    progressDisplay(fileWrapper) {
      if (fileWrapper.progress === 100 && fileWrapper.status === 1) return '上传成功'
      return `${fileWrapper.progress}%`
    },
    handleAliOssException(error) {
      if (error.name === 'RequestTimeTooSkewedError') {
        this.$toasted.error('请求发起的时间有错。请确保电脑本地时间处于校准状态。', {
          duration: 2000,
          position: 'bottom-center',
        })
      } else {
        this.$toasted.error(error, { duration: 2000, position: 'bottom-center' })
      }
      this.$emit('close')
    },
    startUploading(event) {
      let fileCount = this.files.length
      for (const f of this.files) {
        if (f.displayName.length <= 0 || f.displayName.length >= 100) {
          this.$toasted.error('文件名必填且不能超过100个字符', { duration: 800, position: 'bottom-center' })
          return
        }
      }
      axios.get('/pad/j/misc/aliyun/sts').then(response => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 2000, position: 'bottom-center' })
          return
        }

        $(this.$el).find('.pad-dnd').hide()
        const footNote = document.getElementById('foot-note')
        footNote.textContent = '正在上传文件，请不要关闭当前窗口...'
        footNote.classList.add('text-danger')
        footNote.classList.remove('note')
        $(event.target).addClass('loading')
        const ossBucket = new OSS.Wrapper({ region: OSS_REGION, bucket: OSS_BUCKET, ...response.data.sts })

        const now = new Date()
        const prefix = `holidaycourse/${now.getFullYear()}/${now.getMonth() + 1}/${now.getDate()}/`
        for (const f of this.files) {
          f.key = `${prefix + Math.random().toString(36).substr(2, 15)}.${f.ext}`
          ossBucket.multipartUpload(f.key, f.file, {
            partSize: 1 * 1024 * 256,
            *progress(p) {
              f.progress = parseInt(p * 100)
            },
          }).then((response) => {
            if (response.res.status === 200) {
              const params = {
                title: `${f.displayName}.${f.ext}`,
                ext: f.ext,
                filePath: f.key,
                lessonId: this.lesson.id,
                classKind: f.classKind,
              }
              axios.post('/pad/j/holiday-course/material/create/', qs.stringify(params)).then((res) => {
                if (res.data.status === 1) {
                  f.status = 2
                  this.$toasted.error(`${f.displayName}.${f.ext}上传失败，${res.data.detail}`, { duration: 800, position: 'bottom-center' })
                } else if (res.data.status === 0) {
                  f.status = 1
                  this.lesson.materials.push(new HCLessonMaterial(res.data.addon))
                }
                fileCount--
                if (!fileCount) {
                  footNote.textContent = '上传结束，请关闭弹窗。'
                  footNote.classList.remove('text-danger')
                  footNote.classList.add('note')
                  $(event.target).removeClass('loading')
                  $(event.target).hide()
                }
              })
            }
          }).catch(error => this.handleAliOssException(error))
        }
      }).catch(error => this.handleAliOssException(error))
    },
  },
}
</script>

<style lang="less">
@import "~assets/css/base-ui-component.less";
.modal-material-uploader {
  .modal-body-container {
    display: flex;
    margin: -1.2em -1em;
  }
  .upload-form {
    flex: 1;
    padding: 1.5em 1em;
    box-shadow: 1px -1px 2px rgba(0, 0, 0, .2);
    min-height: 30em;
    table tr th, table tr td {
      min-width: 4em;
    }
  }
}

</style>

<style lang="less" scoped>
.ui.form input[type=text] {
  padding: .5em 1em;
}

.ui.table tfoot th.text-danger {
  color: #dd4b39;
}

</style>



