<template>
  <div class="day-timetable">
    <h4>设置每天的上课时间</h4>
    <p v-for="(lesson, index) in value" :key="'grid-duration-' + index">
    <span class="lesson-label">第 {{index + 1}} 节</span>
    <el-time-picker
      is-range
      v-model="lesson.duration"
      size="small"
      align="center"
      :clearable="false"
      value-format="HH:mm"
      format="HH:mm"
      range-separator="至"
      start-placeholder="开始时间"
      end-placeholder="结束时间"
      placeholder="选择时间范围">
    </el-time-picker>
    <span
      title="删除本课时"
      v-if="value.length > 1"
      class="link icon text-danger">
      <i class="trash icon" @click="deleteLesson(lesson)"></i>
    </span>
    <span
      class="link icon text-primary"
      title="在下方插入新课时"
      @click="addLesson(index)"><i class="plus icon"></i></span>
    </p>
  </div>
</template>

<script>
/*
 v-model value 格式: 数组里的每一个元素包了一层键值映射，因为 we can not bind v-model direactly to a v-for iteration alias
  [
    { duration: ['08:00', '08:45'] },
    { duration: ['08:55', '09:35'] },
  ]
*/

export default {
  name: 'day-timetable',
  props: {
    value: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {}
  },
  mounted() {
    if (this.value.length === 0) {
      const durationArr = [
        ['08:00', '08:45'],
        ['08:55', '09:40'],
        ['10:10', '10:45'],
        ['10:55', '11:40'],
      ]
      this.$emit('input', durationArr.map(duration => ({ duration })))
    }
  },
  methods: {
    addLesson(index) {
      this.value.splice(index + 1, 0, { duration: ['', ''] })
    },
    deleteLesson(lesson) {
      if (this.value.length <= 1) {
        this.$toasted.error('至少应有一个课时设置')
      }
      const index = this.value.indexOf(lesson)
      if (index >= 0) {
        this.value.splice(index, 1)
      }
    },
    validate() {
      if (this.value.length === 0) {
        this.$toasted.error('至少应有1个课时设置')
        return false
      }
      if (this.value.length > 16) {
        this.$toasted.error('一天最多可设置16个课时')
        return false
      }
      let curTime = '00'
      for (let i = 0; i < this.value.length; i++) {
        const duration = this.value[i].duration
        if (!(duration && duration[0] && duration[0])) {
          this.$toasted.error(`第${i + 1}课时的起止时间不能有空`)
          return false
        }
        if (!(curTime <= duration[0] && duration[0] < duration[1])) {
          this.$toasted.error(`第${i + 1}课时的起止时间设置错误`)
          return false
        }
        curTime = duration[1]
      }
      return true
    },
  },
}
</script>

<style lang="less" scoped>
.lesson-label {
  display: inline-block;
  width: 5em;
}
.link.icon {
  margin-left: .5em;
  font-size: 1.1em;
  i.icon {
    margin-right: 0;
  }
}

.day-timetable {
  margin-bottom: 2em;
}
</style>



