<template>
  <page-modal
    @close="$emit('close')"
    class="modal-large modal-padded html-preview-modal"
    :class="'size-' + device"
    v-if="previewHtml!==null">
    <template slot="content">
      <div class="header">
        <h4 class="title">预览</h4>
        <div>
          <el-radio v-model="device" label="phone">手机尺寸</el-radio>
          <el-radio v-model="device" label="pad">平板尺寸</el-radio>
        </div>
      </div>

      <div class="preview-block">
        <span class="richtext" v-html="previewHtml" v-if="previewHtml"></span>
        <div class="content-empty" v-else>
          <img :src="require('assets/img/content-empty.png')">
          <p>无内容</p>
        </div>
      </div>
    </template>
  </page-modal>
</template>

<script>
import PageModal from 'components/page-modal'

export default {
  name: 'html-preview-modal',
  props: {
    previewHtml: { required: true },
  },
  data() {
    return {
      device: 'phone',
    }
  },
  components: { PageModal },
}
</script>

<style lang="less">
.html-preview-modal.size-phone .modal-container{
  max-width: 380px;
}
.html-preview-modal.size-tab .modal-container{
  max-width: 900px;
}
</style>

<style lang="less" scoped>
.html-preview-modal .modal-container {
  border-radius: 5px;
  .modal-body {
    max-height: 480px;
    min-height: 350px;
  }

  .header {
    display: flex;
    .title {
      flex: 1;
      margin: 0;
    }
    margin-bottom: 1em;
  }
  .preview-block {
    line-height: 1.5;
  }
}
</style>



