<template>
  <div v-if="course">
    <div class="container-header">
      <div class="title-section">
        <h2 class="container-title">课程：{{course.title}}
          <span class="label pad-state" :class="courseStateClass">
            <i class="circle icon"></i>{{course.stateDisplay}}
          </span>
        </h2>
      </div>
    </div>
    <div class="ui secondary pointing menu">
      <a class="item" @click="tabIndex=1" :class="{active: tabIndex === 1}">基础信息</a>
      <a class="item" @click="tabIndex=2" :class="{active: tabIndex === 2}">课时列表</a>
    </div>
    <div class="ui form preview-course" v-show="tabIndex===1">
      <span
        @click="openEditBasicModal=true"
        class="link link-primary open-modal top-right">编辑课程基础信息</span>
      <div class="inline field">
        <label>学科</label>
        <span class="text">{{subjects[course.subjectId]}}</span>
      </div>
      <div class="inline field">
        <label>展示设备</label>
        <span class="text">{{course.deviceTypeLabel}}</span>
      </div>
      <div class="inline field" v-if="course.isQingbeiPeiyou || school.qingbeiPeiyouOpened">
        <label>课程类型</label>
        <span class="text">{{course.courseTypeLabel}}</span>
      </div>
      <div class="inline field">
        <label>主讲老师</label>
        <span class="text">{{course.mainTeacherName}}</span>
      </div>
      <div class="inline field">
        <label>起止日期</label>
        <span class="text">{{course.startDate}} 至 {{course.endDate}}</span>
      </div>
      <div class="inline field">
        <label>上课年级</label>
        <span class="text">{{grades[course.gradeId]}}</span>
      </div>
      <div class="inline field">
        <label>参与权限</label>
        <span class="text">{{course.applyConditionDisplay}}</span>
      </div>
      <div class="inline field">
        <label>课程说明
          <span class="line-wrap link link-primary" @click="previewHtml=course.descriptionHtml">预览</span>
        </label>
        <p v-html="course.descriptionHtml" class="richtext"></p>
      </div>
      <div class="inline field">
        <label>纸质教辅</label>
        <span class="text">{{course.paperMaterialDisplay}}</span>
      </div>
      <div class="inline field">
        <label>回放设置</label>
        <span class="text">{{course.playbackDisplay}}</span>
      </div>
      <div class="inline field">
        <label>价格</label>
        <span class="text">{{course.originalPriceDisplay}}</span>
      </div>
      <h4 class="ui horizontal divider header">
        试听课程（选填）
      </h4>

      <div class="inline field">
        <label>试听视频</label>
        <div class="demo-video-section">
          <div class="video-section">
            <video controls controlsList="nodownload" v-if="course.demoVideoPath" :key="course.demoVideoPath">
              <source :src="course.demoVideoPath" type="video/mp4">
              您的浏览器版本过低，请更新
            </video>
            <div v-else class="pad-dnd text-muted" id="demo-video-dropzone">
              <p class="instruction"><i class="file huge video icon"></i></p>
              <p class="instruction">
                将视频拖到此处或<span class="link link-primary">点击选择文件</span>上传。
                <br>
                要求h264.编码的mp4格式，平均每10分钟大小不超过120MB。<br>（可换算，如50分钟的视频不超过600MB）
              </p>
              <label for="demo-video-selector"></label>
              <input type="file" id="demo-video-selector" accept="video/mp4" @click="e => e.target.value = null">
            </div>
          </div>
          <modal
            v-if="showProgressBar"
            :withFooter="false"
            :withHeader="false">
            <div slot="body">
              <progress-bar
                class="info"
                :percent="percent"
                :showPercent="true"
                :label="progressLabel"></progress-bar>
            </div>
          </modal>
          <div class="action-section">
            <span class="pad-dnd" v-show="course.demoVideoPath">
              <span class="link link-primary">更改视频</span>
              <label for="demo-video-changer"></label>
              <input type="file" id="demo-video-changer" accept="video/mp4" @click="e => e.target.value = null">
            </span>
            <span
              v-if="course.demoVideoPath"
              class="link link-danger"
              @click="openDeleteModal=true">删除视频</span>
          </div>
        </div>
      </div>
      <modal v-if="openDeleteModal" class="item-danger" :withHeader="false" @close="openDeleteModal=null">
        <div slot="body">
          <span class="ui text-center circular label">
            <i class="ui red warning icon"></i>
          </span>
          <p>确定要删除试听课程的视频吗？</p>
        </div>
        <div slot="footer">
          <button class="ui basic button" @click="openDeleteModal=null">取消</button>
          <button class="ui red button" @click="deleteDemoVideo()">删除</button>
        </div>
      </modal>
      <div class="inline field" v-if="course.demoVideoPath">
        <label>试听简介
          <span class="line-wrap link link-primary" @click="previewHtml=course.demoDescriptionHtml">预览</span>
        </label>
        <div>
          <text-editor v-model="course.demoDescriptionRich" class="textarea"></text-editor>
          <button class="ui tiny primary button demo-preview" @click="saveDemoDescription">保存</button>
        </div>
      </div>
    </div>
    <div class="ui form edit-course" v-show="tabIndex===2">
      <span class="line-wrap text-muted">罗列有课时的日期，选中日期只看当天的课时，再次点击取消选中即为查看全部</span>
      <div class="lesson-dates">
        <button
          @click="openCreateLessonModal=true"
          class="ui default small button add-lesson"
          ><i class="plus icon"></i>课时</button>
        <button
          class="ui default small button"
          :class="selectedDate === null && showAll?'active':''"
          @click="fetchLesson(1, null)" >全部</button>
        <button
          class="ui default small button"
          :class="selectedDate === lessonDate?'active':''"
          @click="fetchLesson(1, lessonDate)"
          v-for="lessonDate of lessonDates">{{lessonDate.slice(5)}}</button>
      </div>
      <br>
      <br>
      <br>
      <div class="lesson-list">
        <loader v-if="loading"></loader>
        <div class="content-empty" v-if="!loading && lessons.length===0">
          <img :src="require('assets/img/content-empty.png')">
          <p>
            什么也没有，试试更改筛选条件或者<span class="link link-primary" @click="openCreateLessonModal=true">增加课时</span>
          </p>
        </div>
        <div v-if="!loading && lessons.length" class="lessons-section d-flex">
          <div>
            <div class="ui secondary vertical pointing menu">
              <a
                class="item lesson-entry"
                @click="lessonOnDisplay=lesson"
                v-for="(lesson, index) in lessons"
                :class="lesson===lessonOnDisplay?'active':''">
                <span
                  v-if="checkLesson(lesson, index).length"
                  :data-tooltip="checkLesson(lesson, index).join('；')"
                  data-inverted=""
                  data-position="top center"
                  class="tooltip right floated">
                  <i class="exclamation triangle orange circle icon"></i>
                </span>
                <span class="lesson-title">{{lesson.title}}</span>
                <span class="text-muted">{{lesson.startTime}}</span>
              </a>
            </div>
            <pagination class="pagination"
                :totalPage="totalPage" :curPage="curPage"
                @update:selected="(page) => {fetchLesson(page, selectedDate)}"></pagination>
          </div>
          <edit-lesson
            @lessonDeleted="oldLessonObj=>postDeleteLesson(oldLessonObj)"
            :course="course"
            :lesson="lessonOnDisplay"
            :key="course.id + '-' + lessonOnDisplay.id"
            @updateLesson="lessonData=>updateLessonOnDisPlay(lessonData)"
            class="flex-1"
            v-if="lessonOnDisplay"
            ></edit-lesson>
        </div>
      </div>
    </div>
    <edit-course-modal
      @courseUpdated="courseData=>postUpdateCourse(courseData)"
      @close="openEditBasicModal=false"
      :courseSource="course"
      v-if="openEditBasicModal"></edit-course-modal>
    <html-preview-modal
      :preview-html="previewHtml"
      @close="previewHtml=null"
      ></html-preview-modal>
    <create-lesson-modal
      @close="openCreateLessonModal=false"
      :course="course"
      @lessonCreated="lessonData=>postCreateLesson(lessonData)"
      v-if="openCreateLessonModal"></create-lesson-modal>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import axios from 'axios'
import Modal from 'components/modal'
import ProgressBar from 'components/progress-bar'
import {
  NORMAL_SUBJECTS,
  GRADES,
  OSS_REGION,
  OSS_BUCKET,
} from '@/assets/js/consts/common'
import {
  HC_STATE_EDITING,
  HC_STATE_PUBLISHED,
} from '@/assets/js/consts/holiday-course'
import { HolidayCourse, HCLesson } from '@/assets/js/holiday-course'
import TimePicker from 'components/time-picker'
import TextEditor from 'components/simple-text-editor'
import Pagination from 'components/pagination'
import Loader from 'components/loader'
import { PadDateTime } from 'assets/js/utils'
import HtmlPreviewModal from 'components/html-preview-modal'
import EditCourseModal from './edit-course-modal'
import CreateLessonModal from './create-lesson-modal'
import EditLessonModal from './edit-lesson-modal'
import EditLesson from './edit-lesson'

const qs = require('qs')
const subjects = Object.assign({99: '综合'}, NORMAL_SUBJECTS)

export default {
  name: 'hc-edit-course',
  data() {
    return {
      subjects,
      grades: GRADES,
      course: null,
      loading: false,
      previewHtml: null,
      openEditBasicModal: false,
      openCreateLessonModal: false,
      lessonToEdit: null,
      tabIndex: 1,
      lessonDates: [], // 所有有课时的日期
      selectedDate: null, // 只展示选定日期的课时
      lessons: [],
      lessonOnDisplay: null,
      totalPage: 0,
      curPage: 1,
      showAll: true,
      showProgressBar: false,
      percent: 0,
      progressLabel: '',
      video: {
        seconds: 0,
        file: null,
        sizeM: null,
      },
      openDeleteModal: false,
    }
  },
  components: {
    TimePicker,
    ProgressBar,
    Modal,
    Loader,
    TextEditor,
    EditCourseModal,
    HtmlPreviewModal,
    CreateLessonModal,
    EditLessonModal,
    EditLesson,
    Pagination,
  },
  mounted() {
    this.fetchCourse()
  },
  computed: {
    courseStateClass() {
      if (this.course.state === HC_STATE_EDITING) return 'grey'
      if (this.course.state === HC_STATE_PUBLISHED) return 'teal'
      return 'red'
    },
    ...mapState(['teacher', 'school']),
  },
  watch: {
    tabIndex: function(val) {
      if (val === 2) this.fetchLesson()
    },
  },
  methods: {
    checkLesson(lesson, index) {
      const msg = []
      if (!lesson.videoPath) msg.push('缺少课时视频')
      if (!lesson.materials.length) msg.push('缺少课时资料')
      if (index > 0) {
        const preLesson = this.lessons[index - 1]
        if (new Date(lesson.startTime) - new Date(preLesson.startTime) < 3600 * 100) {
          msg.push('距离前一节课的上课时间不能小于一小时')
        }
      }
      return msg
    },
    saveDemoDescription() {
      let des = '[]'
      if (this.course.demoDescriptionRich.hasOwnProperty('ops')) {
        des = JSON.stringify(this.course.demoDescriptionRich.ops)
      }
      const params = {
        action: 'update-demo-description',
        demoDescription: des,
      }
      axios.post(`/pad/j/holiday-course/${this.course.id}/edit/`, qs.stringify(params)).then(response => {
        if (response.data.status === 0) {
          this.$toasted.info('已保存')
          this.postUpdateCourse(response.data.course)
        } else if (response.data.status === 1){
          this.$toasted.error(response.data.detail)
        }
        this.openDeleteModal = false
      })
    },
    deleteDemoVideo() {
      const params = {
        action: 'update-demo-video',
        demoVideoPath: '',
      }
      axios.post(`/pad/j/holiday-course/${this.course.id}/edit/`, qs.stringify(params)).then(response => {
        if (response.data.status === 0) {
          this.$toasted.info('已删除')
          this.postUpdateCourse(response.data.course)
        } else if (response.data.status === 1){
          this.$toasted.error(response.data.detail)
        }
        this.openDeleteModal = false
      })
    },
    addFileListener() {
      const vDropZone = this.$el.querySelector('#demo-video-dropzone')
      const vFileInput = this.$el.querySelector('#demo-video-selector')
      const btnFileInput = this.$el.querySelector('#demo-video-changer')

      if (vDropZone) {
        vDropZone.addEventListener('dragover', (e) => {
          vDropZone.classList.add('drag-over')
          e.stopPropagation()
          e.preventDefault()
          e.dataTransfer.dropEffect = 'copy'
        })
        vDropZone.addEventListener('drop', (e) => {
          vDropZone.classList.remove('drag-over')
          e.stopPropagation()
          e.preventDefault()
          this.validateAndUploadVideo(e.dataTransfer.files)
        })
      }
      if (vFileInput) {
        vFileInput.addEventListener('change', () => {
          this.validateAndUploadVideo(vFileInput.files)
        })
      }
      if (btnFileInput) {
        btnFileInput.addEventListener('change', () => {
          this.validateAndUploadVideo(btnFileInput.files)
        })
      }
    },
    handleAliOssException(error) {
      if (error.name === 'RequestTimeTooSkewedError') {
        this.$toasted.error('请求发起的时间有错。请确保电脑本地时间处于校准状态。', {
          duration: 2000,
          position: 'bottom-center',
        })
      } else {
        this.$toasted.error(error, { duration: 2000, position: 'bottom-center' })
      }
      this.showProgressBar = false
      this.$nextTick(() => {
        this.percent = 0
        this.progressLabel = ''
      })
    },
    uploadToAliyun() {
      axios.get('/pad/j/misc/aliyun/sts').then(resp => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail, { duration: 2000, position: 'bottom-center' })
          return
        }
        const ext = this.video.file.name.split('.').pop().toLowerCase()
        this.showProgressBar = true
        this.progressLabel = '正在上传课时视频，请不要关闭本页。'
        const ossBucket = new OSS.Wrapper({ region: OSS_REGION, bucket: OSS_BUCKET, ...resp.data.sts })
        const padDate = new PadDateTime(new Date())
        const fileKey = `holidaycourse/${padDate.formatDate('/')}/${Math.random().toString(36).substr(2, 15)}.${ext}`

        const that = this
        ossBucket.multipartUpload(fileKey, this.video.file, {
          parallel: 4,
          partSize: 1024 * 1025,
          *progress(p) {
            that.percent = parseInt(p * 100)
          },
        }).then(response => {
          if (response.res.status === 200) {
            this.progressLabel = '上传成功'
            const params = {
              action: 'update-demo-video',
              demoVideoPath: fileKey,
            }
            axios.post(`/pad/j/holiday-course/${this.course.id}/edit/`, qs.stringify(params)).then(response => {
              if (response.data.status === 0) {
                this.$toasted.info('成功上传视频')
                this.postUpdateCourse(response.data.course)
              } else if (response.data.status === 1) {
                this.$toasted.error(response.data.detail)
              }
              this.video = { seconds: 0, file: null, sizeM: null }
            })
          } else {
            this.$toasted.error('上传视频失败')
            this.video = { seconds: 0, file: null, sizeM: null }
          }
          this.showProgressBar = false
          this.$nextTick(() => {
            this.percent = 0
            this.progressLabel = ''
          })
        }).catch(error => this.handleAliOssException(error))
      }).catch(error => this.handleAliOssException(error))
    },
    checkVideoSize() {
      const videoEle = document.createElement('video')
      const fileUrl = URL.createObjectURL(this.video.file)
      videoEle.preload = 'metadata'
      videoEle.addEventListener('loadedmetadata', () => {
        this.video.seconds = videoEle.duration
        const avgSize = this.video.sizeM / (this.video.seconds / 60)
        if (this.video.sizeM > 50 && avgSize > 15) {
          this.$toasted.error('文件体积过大，请注意大小限制说明')
          this.video = { seconds: 0, file: null, sizeM: 0 }
        } else {
          this.uploadToAliyun()
        }
      })
      videoEle.src = fileUrl
    },
    validateAndUploadVideo(files) {
      if (!files.length) return
      if (files.length > 1) {
        this.$toasted.error('一个课时只能有一个视频文件。')
        return
      }
      const v = files[0]
      const ext = v.name.split('.').pop().toLowerCase()
      if (v.name.length <= (ext + 1)) { return null }
      if (ext !== 'mp4') {
        this.$toasted.error('仅支持 mp4 文件。')
        return
      }
      this.video.file = v
      this.video.sizeM = v.size / 1024 / 1024
      this.checkVideoSize()
    },
    postUpdateCourse(courseData) {
      courseData.school = this.school
      this.course = new HolidayCourse(courseData)
      this.$nextTick(() => { this.addFileListener() })
    },
    updateLessonOnDisPlay(lessonData) {
      const lesson = new HCLesson(lessonData)
      const index = this.lessons.indexOf(this.lessonOnDisplay)
      // 更改了一个课时的上课时间，该课时可能不应该在当前的过滤条件（分页/日期）列表中
      // 逻辑简化为若更改了时间，取消所有筛选条件，整个课时列表只有当前更新的可是
      if (this.lessonOnDisplay.startTime !== lesson.startTime) {
        this.postLessonDateChange(lesson)
      } else {
        this.lessons.splice(index, 1, lesson)
        this.lessonOnDisplay = lesson
      }
    },
    postCreateLesson(lessonData) {
      const l = new HCLesson(lessonData)
      this.postLessonDateChange(l)
    },
    postDeleteLesson() {
      if (this.lessons.length > 1) {
        const index = this.lessons.indexOf(this.lessonOnDisplay)
        this.lessons.splice(index, 1)
      } else {
        this.fetchLesson(1, null)
        this.fetchCourse()
      }
    },
    postLessonDateChange(lesson) {
      this.lessons = [lesson]
      this.lessonOnDisplay = lesson
      this.selectedDate = null
      this.curPage = 1
      this.totalPage = 0
      this.showAll = false
      this.fetchCourse() // lessonCount 和 dates 都要变化
    },
    fetchLesson(page = 1, selectedDate=null) {
      const url = '/pad/j/holiday-course/lessons'
      const params = {
        courseId: this.course.id,
        page,
        date: selectedDate,
      }
      this.loading = true
      return axios.get(url, { params }).then((response) => {
        if (response.data.status === 0) {
          this.lessons = []
          for (const lessonData of response.data.lessons) {
            this.lessons.push(new HCLesson(lessonData))
          }
          if (this.lessons.length) this.lessonOnDisplay = this.lessons[0]
          this.totalPage = response.data.totalPage
          this.curPage = page
          this.selectedDate = selectedDate
          this.loading = false
          if (!selectedDate && !this.showAll) this.showAll = true
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail)
          this.loading = false
        }
      })
    },
    fetchCourse() {
      // 需要重新获取课程+课时日期表信息的情况
      // 1. 首先自然是编辑了课程本身的信息
      // 2. 创建/删除了一个新课时（影响 lessonCount）
      // 3. 更改了一个课时的上课日期（影响了整个的课时时间表）
      const url = `/pad/j/holiday-course/${this.$route.params.courseId}`
      return axios.get(url).then((resp) => {
        if (resp.data.status === 0) {
          let courseData = resp.data.course
          courseData.school = this.school
          this.course = new HolidayCourse(courseData)
          this.$nextTick(() => { this.addFileListener() })
          axios.get(`/pad/j/holiday-course/${this.$route.params.courseId}/dates`).then(response => {
            if (response.data.status === 0) {
              this.lessonDates = response.data.lessonDates
            } else if (response.data.status === 1) {
              this.$toasted.error(response.data.detail)
            }
          })
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
      })
    },
  },
}
</script>

