      line-height: 1;
    }
  }

  .textarea {
    max-width: 50rem;
  }
  .ui.dropdown {
    min-width: 20em;
  }
  .classes-selector, .grades-selector {
    margin-top: 1em;
    padding: 1em 1em .5em 1em;
    background-color: #f8f8f8
  }
  .ui.checkbox label {
    min-width: 6em;
  }
  .ui.mini.checkbox {
    margin-bottom: .5em;
    label {
      color: #777;
      padding-left: 1.5em;
    }
  }
}

.ui.form.preview-course {
  .field {
    margin-bottom: .5em;
  }
  .richtext {
    max-width: 50rem;
    max-height: 15em;
    overflow-y: auto;
    background-color: #f8f8f8;
    color: #789;
    padding: 1em;
  }
  padding-bottom: 1rem;
}

.demo-video-section {
  max-width: 50rem;
  video {
    background-color: #282A36;
    width: 100%;
    max-height: 30rem;
    box-shadow: 1px 1px 7px 0px rgba(0, 0, 0, 0.3);
  }
  .pad-dnd {
    padding: 2rem 05;
    height: 15rem;
  }
}

.action-section {
  margin-bottom: 1.5rem;
  .pad-dnd {
    border: none;
    &:hover, &:focus, &:active {
      .link.link-primary {
        color: white;
        background-color: @color-blue;
      }
    }
  }
}
.demo-preview {
  margin-top: .5rem;
}
.ui.divider {
  margin: 2rem 0;
}
.tooltip.right.floated {
  float: right;
  &[data-tooltip][data-inverted]:after {
    font-size: .85rem;
    padding: .5rem;
  }

}
</style>



