<template>
    <div class="pad breadcrumb">
      <template v-for="route of crumbAncestors">
        <span class="selected section" v-if="route.name === 'management-index'">管理主页</span>
        <router-link
          v-else
          :key="route.name"
          :to="{name: route.name}"
          tag="span"
          class="link link-primary section"
          >{{route.meta.bcText || route.meta.title}}</router-link>
        <span class="divider" :key="route.name"> / </span>
      </template>
      <span class="selected section">{{this.$route.meta.bcText || this.$route.meta.title }}</span>
    </div>
</template>


<script>
export default {
  name: 'breadcrumb-nav',
  data() {
    return {}
  },
  computed: {
    crumbAncestors() {
      const path = []
      if (this.$route.meta && this.$route.meta.bcAncestors) {
        for (const p of this.$route.meta.bcAncestors) {
          path.push(this.findRoute(p))
        }
      }
      return path
    },
  },
  methods: {
    findRoute(name) {
      if (!name) return
      for (const r of this.$router.options.routes) {
        if (r.name === name) {
          return r
        }
      }
    },
  },
}
</script>

<style lang="less" scoped>
.pad.breadcrumb {
  color: #789;
  font-size: 12px;
  line-height: 1rem;
  padding: .5rem 2rem .5rem 2rem;
  .section, .divider {
    display: inline-block;
  }
  .selected {
    font-weight: bold;
  }
}

</style>



