<template>
  <div v-if="klass">
    <div class="container-header">
      <div class="title-section">
        <h2 class="container-title bordered">行政班：{{klass.name}}
          <span class="label pad-state" :class="klass.statusColor">
            <i class="circle icon"></i>{{klass.statusLabel}}
          </span>
        </h2>
      </div>
    </div>
    <div class="container-section basic-info">
      <span class="info-row">班主任：{{klass.teacher.name}}</span>
      <span class="info-row">年级：{{GRADES[klass.gradeId]}}</span>
      <span class="info-row">创建学年：{{klass.year}}年</span>
    </div>

    <div class="container-header">
      <div class="title-section" v-if="klass.courses.length">
        <p class="container-title ui small header">
          教学班
          <span
            v-if="teacher.isManager && klass.isNormal"
            class="link link-primary" @click="openKlassCoursesModal()">修改教学班分配</span>
        </p>
      </div>
      <button v-else-if="teacher.isManager && klass.isNormal" class="ui tiny blue button" @click="openKlassCoursesModal()">修改教学班分配</button>
    </div>

    <div class="courses-flex container-section" v-if="klass.courses.length">
      <div class="subject card" v-for="course of klass.courses" @click="openCourseInNewTab(course)">
        <div class="image">
          <img :src="require(`assets/img/subject/l_${course.subjectId}.png`)">
        </div>
        <div class="card-title">{{SUBJECTS[course.subjectId]}}</div>
        <div class="card-note">{{course.teacher.name}}</div>
      </div>
    </div>

    <div class="container-header">
      <div class="title-section">
        <p class="container-title ui small header">学生列表
          <span class="text-muted"> 共 {{klass.nStudent}} 人，默认班级密码</span>
          <span class="code-label text-muted">{{klass.defaultPassword}}</span>
        </p>
        <div class="actions" v-if="teacher.isManager && klass.isNormal">
          <button class="ui item tiny button" @click="openExcelImportModal()">Excel 批量创建学生</button>
          <button class="ui item tiny blue button" @click="openAddStudentModal()">添加学生</button>
        </div>
      </div>
    </div>
    <div class="students-section container-section">
      <student-paging-list
        :students="students"
        :loading="loading"
        :curPage="page"
        :totalPage="totalPage"
        :klass="klass"
        @studentUpdated="student => refreshStudents(student, 'update')"
        @studentExited="student => refreshStudents(student, 'exit')"
        @studentTransfered="student => refreshStudents(student, 'exit')"
        @update:selected="page=>fetchStudents(page)"></student-paging-list>
    </div>
    <page-modal
      v-if="klassCourseModalOpened"
      key="edit-klass-course"
      @close="klassCourseModalOpened=false"
      class="modal-large">
      <div slot="content" class="klass-course-modal">
        <h3>编辑 {{klass.name}} 的教学班</h3>
        <div>
          <div class="d-flex">
            <table class="ui small table flex-1" v-for="subjectArr of [orderedSubjectIds.slice(0, 9), orderedSubjectIds.slice(9)]">
              <thead>
                <tr>
                  <th>学科/教学班</th>
                  <th>启用/关闭</th>
                  <th>老师（点击修改）</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="subjectId in subjectArr">
                  <td class="subject-col">{{SUBJECTS[subjectId]}}</td>
                  <td>
                    <div class="ui toggle checkbox">
                      <input
                        type="checkbox" tabindex="0" class="hidden"
                        @change="chooseTeacherForSubject(subjectId)"
                        v-model="klassCourseAssign[subjectId].active"><label></label>
                    </div>
                  </td>
                  <td v-if="!klassCourseAssign[subjectId].active" class="action-td">
                    <span class="text-muted hover-visible">启用该学科教学班后可修改</span>
                  </td>
                  <td v-else-if="klassCourseAssign[subjectId].editing" class="action-td">
                    <div class="ui search selection dropdown" v-if="tGroups[subjectId].length">
                      <i class="dropdown icon"></i>
                      <div class="text">{{getAssignedTeacher(subjectId) && getAssignedTeacher(subjectId).name || '未分配'}}</div>
                      <div class="menu">
                        <div
                          class="item"
                          v-for="t of tGroups[subjectId]"
                          @click="setAssignedTeacher(subjectId, t)"
                          :key="'t-id-'-t.id"> {{t.name}} </div>
                      </div>
                    </div>
                    <span v-else class="text-muted">无对应科目老师</span>
                  </td>
                  <td v-else class="action-td" @click="chooseTeacherForSubject(subjectId)">
                    <span v-if="klassCourseAssign[subjectId].teacher">{{klassCourseAssign[subjectId].teacher.name}}</span>
                    <span class="text-muted hover-visible">点击修改</span>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="modal-actions">
            <button class="ui tiny button" @click="klassCourseModalOpened=false">取消</button>
            <button class="ui tiny blue button" @click="postChangeKlassCourses($event)">保存修改</button>
          </div>
        </div>
      </div>
    </page-modal>
    <page-modal
      v-if="addStudentModalOpened"
      key="add-student-modal"
      @close="addStudentModalOpened=false"
      class="modal-large">
      <div slot="content" class="add-student-modal">
        <div class="inner-panel">
          <p class="ui header">新建学生帐号</p>
          <p class="text-muted">创建全新的账号并加入此行政班。若要加入以前使用过智慧课堂的学生，请在右侧通过帐号 ID 加入。</p>
          <div class="ui small form">
            <div class="field required">
              <label>姓名</label>
              <input type="text" v-model="studentToCreate.name" placeholder="姓名不能为空">
            </div>
            <div class="field">
              <label>学号</label>
              <input type="text" v-model="studentToCreate.number" placeholder="不超过20个字符">
            </div>
            <button class="ui tiny blue button" @click="postCreateStudent($event)">新建学生</button>
          </div>
        </div>
        <div class="inner-panel">
          <p class="ui header">添加已有帐号的学生</p>
          <p class="text-muted">通过学生登录系统的ID（非学号）添加学生到此行政班，一次可添加不多于80个ID，用空格或者换行符分隔。</p>
          <div class="ui small form" v-show="!showAddByIdsResult">
            <div class="field required">
              <label>学生登录 ID</label>
              <textarea rows="15" v-model="studentIdsText"></textarea>
            </div>
            <button class="ui tiny blue button" @click="postAddStudentIds($event)">添加学生</button>
          </div>
          <div class="ui small form" v-show="showAddByIdsResult">
            <p>{{addByIdResult}}</p>
            <button class="ui tiny blue button" @click="addStudentModalOpened=false">关闭弹窗</button>
          </div>
        </div>
        <div class="ui vertical divider">或</div>
      </div>
    </page-modal>
    <page-modal
      v-if="excelImportModalOpened"
      key="add-student-modal"
      @close="excelImportModalOpened=false"
      class="modal-padded excel-import-modal">
      <div slot="content">
        <h3 class="ui header">批量创建新帐号</h3>
        <p class="text-muted">若要加入的学生已使用过本系统，请使用 <span class="text-danger">添加学生-通过帐号 ID 加入</span> 功能（可以保留历史的学习记录）<br>
        系统会根据你上传的数据文件（.xls或.xlsx，要求数据格式和模版一致）批量创建 <span class="text-danger">全新的账号</span> 并加入此行政班。一次创建不多于150个学生。
          <a class="link link-primary" href="https://padmedia.oss-cn-beijing.aliyuncs.com/excel/batch-import-student.xlsx"><i class="download icon"></i> 点此下载导入模版</a>
        </p>

        <div v-if="!showImportResult">
          <span class="file-input-wrapper">
            <button class="ui tiny button">选择要导入的文件</button>
            <label for="btn-excel-import"></label>
            <input type="file" id="btn-excel-import" @change="processExcelFile($event)">
            <span class="text-muted">{{excelFile ? excelFile.name : ''}}</span>
          </span>
          <button class="ui tiny primary button right floated" v-if="excelFile" @click="postExcelImportStudents($event)">上传并导入</button>
        </div>
        <div v-else>
          <p>导入成功！ <span v-if="repeatedNameInfo.length"> 以下是 行号-重复的名字，需单独确认是否添加（需手动添加）：</span> </p>
          <p v-if="repeatedNameInfo.length">
            <span v-for="repeatedItem of repeatedNameInfo" class="code-label"> {{repeatedItem.row}}-{{repeatedItem.name}} </span>
          </p>
          <button class="ui tiny blue button" @click="excelImportModalOpened=false">关闭弹窗</button>
        </div>
      </div>
    </page-modal>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import axios from 'axios'
import PageModal from 'components/page-modal'
import {
  SUBJECT_EXTENDED_ORDER,
  SUBJECTS,
  GRADES,
} from '@/assets/js/consts/common'
import Klass from '@/assets/js/class'
import StudentPagingList from './student-paging-list'

const qs = require('qs')

export default {
  name: 'org-class-detail',
  data() {
    return {
      SUBJECTS,
      GRADES,
      orderedSubjectIds: SUBJECT_EXTENDED_ORDER.slice(1),
      klass: null,
      students: [],
      tGroups: {},
      page: 1,
      totalPage: 1,
      loading: false,

      klassCourseModalOpened: false,
      klassCourseAssign: {},

      addStudentModalOpened: false,
      studentToCreate: { name: '', number: '' },
      studentIdsText: [],
      showAddByIdsResult: false,
      addByIdResult: '',

      excelImportModalOpened: false,
      excelFile: null,
      showImportResult: false,
      repeatedNameInfo: [],
    }
  },
  components: { StudentPagingList, PageModal },
  mounted() {
    this.fetchClass().then(() => { this.fetchStudents() })
  },
  computed: {
    ...mapState(['teacher', 'school']),
  },
  methods: {
    openCourseInNewTab(course) {
      window.open(`/pad/manage/courses/${course.id}`, '_blank')
    },
    processExcelFile(event) {
      const file = event.target.files[0]
      const ext = file.name.split('.').pop().toLowerCase()
      if (!['xls', 'xlsx'].includes(ext)) {
        this.$toasted.error('文件格式不支持')
        return
      }
      this.excelFile = file
    },
    postExcelImportStudents(event) {
      if (!this.excelFile) return
      const formData = new FormData()
      formData.append('excel', this.excelFile)
      const url = `/pad/j/management/classes/${this.$route.params.id}/students/batch_create/`
      $(event.target).addClass('loading')
      axios.post(url, formData, { headers: { 'Content-Type': 'multipart/form-data' } }).then(resp => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('已更新')
          this.fetchStudents()
          this.showImportResult = true
          this.repeatedNameInfo = resp.data.repeatedNameInfo
        }
        $(event.target).removeClass('loading')
        this.fetchClass()
      })
    },
    openExcelImportModal() {
      this.excelFile = null
      this.excelImportModalOpened = true
      this.showImportResult = false
      this.repeatedNameInfo = []
    },
    openAddStudentModal() {
      this.studentToCreate.name = ''
      this.studentToCreate.number = ''
      this.studentIdsText = []
      this.showAddByIdsResult = false
      this.addByIdResult = ''
      this.addStudentModalOpened = true
    },
    setAssignedTeacher(subjectId, teacher) {
      this.klassCourseAssign[subjectId].teacher = teacher
    },
    getAssignedTeacher(subjectId) {
      return this.klassCourseAssign[subjectId].teacher
    },
    chooseTeacherForSubject(subjectId) {
      if (!(subjectId in this.tGroups)) {
        const url = '/pad/j/management/teachers'
        const params = { gradeId: this.klass.gradeId, subjectId }
        axios.get(url, { params }).then(resp => {
          if (resp.data.status === 0) {
            this.$set(this.tGroups, subjectId, resp.data.teachers)
            this.klassCourseAssign[subjectId].editing = true
            this.$nextTick(() => {
              $(this.$el).find('.ui.dropdown').dropdown({ action: 'activate' })
            })
          } else if (resp.data.status === 1) {
            this.$toasted.error(resp.data.detail)
          }
        })
      } else {
        this.klassCourseAssign[subjectId].editing = true
        this.$nextTick(() => {
          $(this.$el).find('.ui.dropdown').dropdown({ action: 'activate' })
        })
      }
    },
    openKlassCoursesModal() {
      for (const subjectId of this.orderedSubjectIds) {
        this.$set(this.klassCourseAssign, subjectId, { teacher: null, active: false, editing: false })
      }
      for (const course of this.klass.courses) {
        this.$set(
          this.klassCourseAssign,
          course.subjectId,
          { teacher: course.teacher, active: course.isNormal, editing: false },
        )
      }
      this.klassCourseModalOpened = true
      this.$nextTick(() => {
        $(this.$el.querySelectorAll('.toggle.checkbox')).checkbox()
      })
    },
    teacherSelection(subjectId) {
      if (subjectId in this.tGroups) return this.tGroups[subjectId]
    },
    fetchTeachers(subjectId) {
      if (subjectId in this.tGroups) return
      const url = '/pad/j/management/teachers'
      const params = { gradeId: this.klass.gradeId, subjectId }
      return axios.get(url, { params }).then(resp => {
        if (resp.data.status === 0) {
          this.tGroups[subjectId] = resp.data.teachers
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
      })
    },
    fetchStudents(page = 1) {
      const url = `/pad/j/management/classes/${this.$route.params.id}/students`
      const params = { page }
      axios.get(url, { params }).then(resp => {
        if (resp.data.status === 0) {
          this.totalPage = resp.data.totalPage
          this.students = resp.data.students
          this.page = resp.data.page
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
      })
    },
    refreshStudents(studentData, op = 'update') {
      let replaceIndex = -1
      for (const [index, s] of this.students.entries()) {
        if (s.id === studentData.id) {
          replaceIndex = index
          break
        }
      }
      if (replaceIndex >= 0) {
        if (op === 'update') this.students.splice(replaceIndex, 1, studentData)
        else if (op === 'exit') {
          if (this.students.length <= 1) {
            this.fetchStudents()
          } else {
            this.students.splice(replaceIndex, 1)
          }
        }
      }
      this.fetchClass()
    },
    fetchClass() {
      const url = `/pad/j/management/classes/${this.$route.params.id}`
      return axios.get(url).then(resp => {
        if (resp.data.status === 0) {
          this.klass = new Klass(resp.data.class)
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
      })
    },
    postChangeKlassCourses(event) {
      let params = {
        coursesData: JSON.stringify(this.klassCourseAssign),
      }
      const url = `/pad/j/management/classes/${this.$route.params.id}/edit_courses/`
      $(event.target).addClass('loading')
      axios.post(url, qs.stringify(params)).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('已更新')
          this.fetchClass()
          this.klassCourseModalOpened = false
        }
        $(event.target).removeClass('loading')
      })
    },
    postAddStudentIds(event) {
      const reSeparator = /\s+/
      const items = this.studentIdsText.trim().split(reSeparator)

      const reNumber = /^\d+$/
      const ids = items.filter(item => item.match(reNumber))
      if (ids.length > 80) {
        this.$toasted.error('一次不能多于 80 个 ID')
        return
      } else if (ids.length === 0) {
        this.$toasted.error('请填写有效的学生登录的数字 ID')
        return
      }

      const params = { studentIds: ids.join(',') }
      const url = `/pad/j/management/classes/${this.$route.params.id}/students/join_by_ids/`

      $(event.target).addClass('loading')
      axios.post(url, qs.stringify(params)).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('已更新')
          const { errorIds, students } = resp.data
          this.addByIdResult = '操作完成。'
          if (errorIds.length) {
            this.addByIdResult += errorIds.join(', ')
            this.addByIdResult += ' 无法加入（ID 错误或者不是本校学生）'
          }
          this.showAddByIdsResult = true
          this.students.splice(0, 0, ...students)
        }
        $(event.target).removeClass('loading')
        this.fetchClass()
      })
    },
    postCreateStudent(event) {
      this.studentToCreate.name = this.studentToCreate.name.trim()
      this.studentToCreate.number = this.studentToCreate.number.trim()
      const nameLength = this.studentToCreate.name.length
      if (nameLength <= 0 || nameLength > 20 || this.studentToCreate.number.length > 20) {
        this.$toasted.error('学生姓名不能为空，不能超过20个字符；学号不能超过20个字符。')
        return
      }
      const url = `/pad/j/management/classes/${this.$route.params.id}/students/create/`
      $(event.target).addClass('loading')
      axios.post(url, qs.stringify(this.studentToCreate)).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('已更新')
          this.addStudentModalOpened = false
          this.students.splice(0, 0, resp.data.student)
        }
        $(event.target).removeClass('loading')
        this.fetchClass()
      })
    },
  },
}
</script>

<style lang="less" scoped>
.container-section {
  margin: 0 2rem;
}

.basic-info {
  font-size: .9rem;
  margin-bottom: 1rem;
  .info-row {
    color: #666;
    display: block;
    line-height: 1.8;
  }
}

.courses-flex {
  display: flex;
  flex-wrap: wrap ;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 2rem;
  .subject.card {
    margin-right: 1rem;
    margin-bottom: 1rem;
    padding-bottom: .6rem;
    width: 9rem;
    background-color: #f7f9fa;
    text-align: center;
    border-radius: .4rem;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    cursor: pointer;

    &:hover {
      box-shadow: 0 6px 10px 0 rgba(0,0,0,0.2);
    }
    .image {
      text-align: center;
      padding: .4rem;
      img {
        width: 5rem;
      }
    }
    .card-title {
      font-size: 1.3rem;
      color: black;
      line-height: 1.3;
    }
    .card-note {
      font-size: .9rem;
      color: #888;
    }

  }
  .ui.card:first-child {
    margin-top: unset;
  }
}

.add-student-modal {
  display: flex;
  position: relative;
  .inner-panel {
    flex: 1;
    padding: 2rem 4rem;
  }
}
.excel-import-modal {
  .file-input-wrapper {
    display: inline-block;
    position: relative;
    input {
      display: none;
    }
    label {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      width: 100%;
    }
  }
}

.klass-course-modal {
  padding: 0 1rem;
  .ui.table {
    width: 50%;
    min-width: 23rem;
    margin: 0 1rem;
    border-radius: 0;
    .subject-col {
      background-color: #f9fafb;
    }
    .text-muted {
      font-size: .9em;
    }
    tr td {
      height: 3rem;
    }
    .action-td {
      .ui.dropdown .menu>.item {
        font-size: 12px;
      }
      width: 14rem;
      cursor: pointer;
      &:hover, &:focus, &:active {
        background-color: #f9fafb;
        .hover-visible {
          display: inline;
        }
      }
      .hover-visible {
        display: none;
      }

    }
  }
  .ui.table:first-child {
    margin-left: 0
  }
  .ui.table:last-child {
    margin-right: 0
  }
  .modal-actions {
    text-align: right;
    margin: 2rem 0;
  }
}

</style>



