<template>
  <div v-if="classroom">
    <div class="container-header">
      <div class="title-section">
        <h2 class="container-title">{{classroom.name}}</h2>
        <div class="actions"></div>
      </div>
      <div class="ui secondary pointing menu">
        <a class="item" @click="viewMode=1" :class="viewMode===1?'active':''">
          班级详情及周课表
        </a>
        <a class="item" @click="viewMode=2" :class="viewMode===2?'active':''">
          上课日历/历史
        </a>
      </div>
    </div>
    <!-- 课表 -->
    <div class="section-area" v-show="viewMode === 1">
      <div class="section-body">
        <weekly-schedule
          :classroom="classroom"
          v-if="classroom"></weekly-schedule>
      </div>
    </div>
    <div class="section-area" v-show="viewMode === 2">
      <div class="section-body">
        <classroom-calendar
          :classroom="classroom"
          v-if="classroom"></classroom-calendar>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import PageModal from 'components/page-modal'
import WeeklySchedule from './weekly-schedule'
import ClassroomCalendar from './classroom-calendar'

export default {
  name: 'classroom-detail',
  data() {
    return {
      classroom: null,
      popupGradeId: 0,
      popupSubjectId: 0,
      selectedCourse: null,
      gridToEdit: null,
      viewMode: 1,
    }
  },
  components: { PageModal, WeeklySchedule, ClassroomCalendar },
  mounted() {
    this.fetchClassroom()
  },
  computed: {
    ...mapState(['teacher', 'school']),
  },
  methods: {
    selectedCourseForGrid(course) {
      this.gridToEdit.course = course
      this.gridToEdit = null
    },
    fetchClassroom() {
      const url = `/pad/j/classrooms/${this.$route.params.id}`
      return axios.get(url).then(resp => {
        if (resp.data.status === 0) {
          this.classroom = resp.data.classroom
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
      })
    },
  },
}
</script>

<style lang="less" scoped>

</style>



