import {
  PROGRAM_LENGTH_63,
  PROGRAM_LENGTH_54,
  PROGRAM_LENGTH_MAP,

  GRADE_HIGH_III,
  GRADE_HIGH_II,
  GRADE_HIGH_I,
  GRADE_JUNIOR_IV,
  GRADE_JUNIOR_III,
  GRADE_JUNIOR_II,
  GRADE_JUNIOR_I,
  GRADE_PRIMARY_VI,
  GRADE_PRIMARY_V,
  GRADE_PRIMARY_IV,
  GRADE_PRIMARY_III,
  GRADE_PRIMARY_II,
  GRADE_PRIMARY_I,
} from '@/assets/js/consts/common'


export default class School {
  constructor(sourceData) {
    this.id = sourceData.id
    this.name = sourceData.name
    this.programLength = sourceData.programLength
    this.classes = sourceData.classes || [] // classes 逻辑待整理
    this.hasPrimaryGrade = sourceData.hasPrimaryGrade || true
    this.holidayCourseOpened = sourceData.holidayCourseOpened || false
    this.qingbeiPeiyouOpened = sourceData.qingbeiPeiyouOpened || false
  }

  get programLengthLabel() {
    return PROGRAM_LENGTH_MAP[this.programLength]
  }

  // 根据学制和是否有小学获得有序的年级分布
  get gradeIds() {
    const ids = [
      GRADE_HIGH_I, GRADE_HIGH_II, GRADE_HIGH_III,
      GRADE_JUNIOR_I, GRADE_JUNIOR_II, GRADE_JUNIOR_III,
    ]
    if (this.programLength === PROGRAM_LENGTH_54) {
      ids.push(GRADE_JUNIOR_IV)
    }
    if (!this.hasPrimaryGrade) return ids

    const primaryIds = [
      GRADE_PRIMARY_I, GRADE_PRIMARY_II, GRADE_PRIMARY_III,
      GRADE_PRIMARY_IV, GRADE_PRIMARY_V,
    ]
    if (this.programLength === PROGRAM_LENGTH_63) {
      primaryIds.push(GRADE_PRIMARY_VI)
    }
    return ids.concat(primaryIds)
  }
}



