<template>
  <page-modal
    class="modal-small confirm-modal"
    @close="$emit('close')" >
    <template slot="content">
      <div>
        <h2 class="confirm-title"><slot name="title"></slot></h2>
        <p class="confirm-text">
          <slot name="text"></slot>
        </p>
        <div class="confirm-actions">
          <slot name="actions"></slot>
        </div>
      </div>
    </template>
  </page-modal>
</template>

<script>
import PageModal from './page-modal'

export default {
  name: 'confirm-modal',
  data() {
    return {
    }
  },
  components: { PageModal },
}
</script>

<style lang="less">
.page-modal.confirm-modal.modal-mask {
  align-items: center;
  justify-content: center;
}

.page-modal.confirm-modal {
  .confirm-actions {
    .link {
      font-size: 1em;
      padding: .3em .8em;
      font-weight: bold;
    }
    .ui.button {
      opacity: .95;
      font-size: .86rem;
    }
  }
}
</style>

<style lang="less" scoped>
.page-modal.confirm-modal {
  .confirm-title {
    font-size: 1.1rem;
    color: #333;
    padding: 0 1.5rem;
    margin-top: 1.5rem;
  }
  .confirm-text {
    color: #666;
    padding: 0 1.5rem;
    font-size: .9rem;
    line-height: 1.6;
  }
  .confirm-actions {
    text-align: right;
    padding: 1rem 1.5rem;
    letter-spacing: .3rem;
  }
}

</style>



