<template>
  <modal
    @close="$emit('close')"
    class="modal-default create-lesson-modal"
    :with-footer="false">
    <h3 slot="header">添加课时</h3>
    <div slot="body">
      <div class="ui form">
        <div class="inline field required" v-if="course.isMixed">
          <label for="lesson-subject-id">学科</label>
          <div>
            <div class="ui selection dropdown" id="lesson-subject-id">
              <i class="dropdown icon"></i>
              <span class="text">{{subjects[lesson.subjectId]}}</span>
              <div class="menu">
                <div
                  class="item"
                  v-for="(subjectLabel, subjectId) in subjects"
                  v-if="parseInt(subjectId) !== lesson.subjectId"
                  :data-value="subjectId"
                  :key="'subject-'+subjectId">{{subjects[subjectId]}}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="inline field required">
          <label for="lesson-title">课时名称</label>
          <input type="text" id="lesson-title" v-model="lesson.title" placeholder="不超过100个字符">
        </div>
        <div class="inline field required">
          <label for="teacher-name">讲课老师</label>
          <input type="text" id="teacher-name" v-model="lesson.teacherName" placeholder="不超过30个字符">
        </div>
        <div class="inline field required">
          <label for="lesson-start-time">上课时间</label>
          <div>
            <time-picker place-holder-text="选择日期"
              key="lesson-start-time"
              v-model="lesson.startTime"
              :can-clear="false"
              :min-date="minDate"
              :max-date="course.endDate + ' ' + '23:59'"
              ></time-picker>
            <p class="text-muted">
              必须在课程的开始和结束时间之间，且距离现在15分钟以上。<br>
              相邻课时需要间隔一小时及以上（此处填写不严格限制），发布课程的时候统一检查。
            </p>
          </div>

        </div>
        <div class="inline field action-section">
          <label></label>
          <div>
            <button class="ui small primary button" @click="createLesson()">添加课时</button>
          </div>
        </div>
      </div>
    </div>
  </modal>
</template>

<script>
import axios from 'axios'
import Modal from 'components/modal'
import { NORMAL_SUBJECTS, SUBJECT_MIXED } from '@/assets/js/consts/common'
import { PadDateTime } from 'assets/js/utils'
import TimePicker from 'components/time-picker'
import { HCLesson } from '@/assets/js/holiday-course'

const qs = require('qs')
const subjects = Object.assign({[SUBJECT_MIXED]: '综合'}, NORMAL_SUBJECTS)

export default {
  name: 'hc-create-lesson-modal',
  data() {
    const lessonData = {
      teacherName: this.course.mainTeacherName,
      subjectId: this.course.subjectId,
      title: `第${this.course.lessonCount + 1}课时`,
      seqId: this.course.lessonCount + 1,
      courseId: this.course.id,
    }
    return {
      lesson: new HCLesson(lessonData),
      subjects,
      currentTime: new Date(),
    }
  },
  props: {
    course: { type: Object, required: true },
  },
  components: { TimePicker, Modal },
  mounted() {
    $(this.$el).find('.ui.dropdown').dropdown({
      action: 'hide',
      onChange: value => { this.lesson.subjectId = parseInt(value) },
    })
  },
  computed: {
    minDate() {
      const offsetTime = new Date(this.currentTime)
      let minutes = offsetTime.getMinutes()
      minutes = minutes + 20 - (minutes % 5)
      offsetTime.setMinutes(minutes)
      const offsetTimeStr = new PadDateTime(offsetTime).formatDateTime()
      return offsetTimeStr > this.course.startDate ? offsetTimeStr : this.course.startDate
    },
  },
  methods: {
    createLesson() {
      const [result, msg] = this.lesson.validate(this.course)
      if (!result) {
        this.$toasted.error(msg, { duration: 800, position: 'bottom-center' })
        return
      }
      const url = '/pad/j/holiday-course/lesson/create/'
      axios.post(url, qs.stringify({ lessonData: JSON.stringify(this.lesson) })).then((response) => {
        if (response.data.status === 0) {
          this.$toasted.info('创建成功', { duration: 800, position: 'bottom-center' })
          this.$emit('lessonCreated', response.data.lesson)
          this.$emit('close')
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
      })
    },
  },
}
</script>

<style lang="less" scoped>
.ui.form {
  padding: .5rem 0;
  min-height: 320px;
  .field.action-section {
    margin-top: 2.5rem;
  }
  .text-muted {
    font-size: .9rem;
  }
}
.ui.form .inline.field {
  display: flex;
  > label {
    position: relative;
    min-width: 5em;
    max-width: 5em;
    .line-wrap {
      position: absolute;
      top: 1.5em;
      left: 0;
    }
  }
  > div {
    flex: 1;
    flex-start: left;
  }

  input {
    min-width: 24rem;
  }

  .ui.dropdown {
    min-width: 24em;
  }
}

</style>



