<template>
  <div class="lesson-detail">
    <div class="video-section">
      <video controls controlsList="nodownload" v-if="lesson.videoPath" :key="lesson.videoPath">
        <source :src="lesson.videoPath" type="video/mp4">
        您的浏览器版本过低，请更新
      </video>
      <div v-else class="pad-dnd text-muted" id="video-dropzone">
        <p class="instruction"><i class="file huge video icon"></i></p>
        <p class="instruction">
          将课时视频拖到此处或<span class="link link-primary">点击选择文件</span>上传。
          <br>
          要求h264.编码的mp4格式，平均每10分钟大小不超过120MB。<br>（可换算，如50分钟的视频不超过600MB）
        </p>
        <label for="video-selector"></label>
        <input type="file" id="video-selector" accept="video/mp4" @click="e => e.target.value = null">
      </div>
    </div>
    <modal
      v-if="showProgressBar"
      :withFooter="false"
      :withHeader="false">
      <div slot="body">
        <progress-bar
          class="info"
          :percent="percent"
          :showPercent="true"
          :label="progressLabel"></progress-bar>
      </div>
    </modal>
    <div class="info-section">
      <div class="lesson-title">
        <span class="subject-label" v-if="course.isMixed">{{subjects[lesson.subjectId].slice(0,1)}}</span>
        <span>{{lesson.title}}</span>
        <span class="text-muted" v-if="lesson.videoPath">视频解析时长{{lesson.seconds}}秒</span>
      </div>
      <div>
        <span>{{lesson.teacherName}} / {{lesson.startTime}}</span>
        <span class="text-muted" v-if="!course.isStateEditing && lesson.hasExpired">已发布的课程中过时间的课时不能更改基本信息和视频</span>
        <span
          v-if="course.isStateEditing || !lesson.hasExpired"
          class="link link-primary open-modal"
          @click="openEditBasicModal=true"
          >编辑基本信息</span>
        <span class="pad-dnd" v-show="(course.isStateEditing || !lesson.hasExpired) && lesson.videoPath">
          <span class="link link-primary">更改课时视频</span>
          <label for="btn-video-selector"></label>
          <input type="file" id="btn-video-selector" accept="video/mp4" @click="e => e.target.value = null">
        </span>
        <span
          v-if="lesson.materials.length === 0"
          class="link link-primary open-modal top-right"
          @click="openMaterialUploader=true">上传课时资料</span>
        <span
          v-if="course.isStateEditing || !lesson.hasExpired"
          class="link link-danger open-modal"
          @click="openDeleteModal=true"
          >删除课时</span>
        <span
          v-if="course.isStateEditing || !lesson.hasExpired"
          class="link link-default open-modal"
          @click="toOpenVideoUrlModal()"
          >关联视频链接</span>
      </div>
    </div>
    <modal v-if="openDeleteModal" class="item-danger" :withHeader="false" @close="openDeleteModal=false">
      <div slot="body">
        <span class="ui text-center circular label">
          <i class="ui red warning icon"></i>
        </span>
        <p>确定要删除 <strong>{{lesson.title}}</strong> 这一课时吗？</p>
        <p>删除后不可恢复。</p>
      </div>
      <div slot="footer">
        <button class="ui basic button" @click="openDeleteModal=false">取消</button>
        <button class="ui red button" @click="deleteLesson(lesson)">删除</button>
      </div>
    </modal>
    <div class="lesson-materials flex-1" v-if="lesson.materials.length">
      <div class="action-section">
        <span
          v-if="lesson.materials.length > 0"
          class="link link-primary"
          @click="openEditMaterialListModal=true">编辑课时资料</span>
        <span
          v-if="lesson.materials.length > 0"
          class="link link-primary"
          @click="openMaterialUploader=true">上传课时资料</span>
      </div>
      <div v-for="m of lesson.materials" class="d-flex material-item" @click="formatResourcePreviewData(m)">
        <i class="pad icon" :class="'ft-'+m.fileType"></i>
        <span class="flex-1">{{m.title}}</span>
        <span class="text-muted">{{classKindMap[m.classKind]}}</span>
      </div>
    </div>
    <page-modal
      v-if="openVideoUrlModal"
      @close="openVideoUrlModal=false"
      class="modal-padded video-url-modal">
      <div slot="content">
        <p class="ui small header">为 {{lesson.title}} 课时关联/替换视频链接</p>
        <p class="text-muted">仅支持本系统服务器上的视频链接，如果你不知道这是什么，请不要使用此功能。</p>
        <div class="ui small form">
          <div class="field required">
            <label>输入地址</label>
            <input type="text" v-model="videoUrlToAdd" placeholder="不带 scheme 和 host，单路径，如 /holidaycourse/2ptkl.mp4">
          </div>
          <button class="ui tiny blue button" @click="postUpdateLessonVideoUrl($event)">保存修改</button>
          <span class="text-muted" id="video-checking-info"></span>
        </div>
        <video id="v-placeholder-for-duration" style="display:none;"></video>
      </div>
    </page-modal>
    <edit-lesson-modal
      @updateLesson="lessonData=>$emit('updateLesson', lessonData)"
      :course="course"
      :lesson="lesson"
      @close="openEditBasicModal=false"
      v-if="openEditBasicModal"
      ></edit-lesson-modal>
    <material-uploader
      :course="course"
      :lesson="lesson"
      @close="openMaterialUploader=false"
      v-if="openMaterialUploader"
      ></material-uploader>
    <edit-material-list
      @updateLesson="lessonData=>$emit('updateLesson', lessonData)"
      :course="course"
      :lesson="lesson"
      @close="openEditMaterialListModal=false"
      v-if="openEditMaterialListModal"
      ></edit-material-list>
    <resource-preview :previewItem="previewItem" @close="previewItem=null"></resource-preview>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import Modal from 'components/modal'
import { NORMAL_SUBJECTS, SUBJECT_MIXED, OSS_REGION, OSS_BUCKET, CLASS_KIND_MAP, K_PAD_DOCUMENT } from '@/assets/js/consts/common'
import ProgressBar from 'components/progress-bar'
import ResourcePreview from 'components/resource-preview'
import PageModal from 'components/page-modal'
import { PadDateTime } from 'assets/js/utils'
import EditLessonModal from './edit-lesson-modal'
import MaterialUploader from './material-uploader'
import EditMaterialList from './edit-material-list'

const qs = require('qs')
const subjects = Object.assign({[SUBJECT_MIXED]: '综合'}, NORMAL_SUBJECTS)

export default {
  name: 'hc-edit-lesson',
  data() {
    return {
      subjects,
      files: [],
      showProgressBar: false,
      percent: 0,
      progressLabel: '',
      video: {
        seconds: 0,
        file: null,
        sizeM: null,
      },
      openEditBasicModal: false,
      openDeleteModal: false,
      openMaterialUploader: false,
      openEditMaterialListModal: false,
      classKindMap: CLASS_KIND_MAP,
      previewItem: null,
      openVideoUrlModal: false,
      videoUrlToAdd: '',
    }
  },
  props: {
    course: { type: Object, required: true },
    lesson: { type: Object, required: true },
  },
  components: { PageModal, Modal, ProgressBar, EditLessonModal, MaterialUploader, ResourcePreview, EditMaterialList },
  mounted() {
    this.uploaderEventLisener()
  },
  computed: {
    ...mapState(['teacher', 'school']),
  },
  watch: {
    lesson: function() {
      this.uploaderEventLisener()
    }
  },
  methods: {
    toOpenVideoUrlModal() {
      this.openVideoUrlModal = true
      this.videoUrlToAdd = ''
    },
    postUpdateLessonVideoUrl(event) {
      this.videoUrlToAdd = this.videoUrlToAdd.trim()
      if (!this.videoUrlToAdd) {
        this.$toasted.error('地址不能为空')
        return
      }

      $(event.target).addClass('loading')
      axios.get('/pad/j/misc/aliyun/sts').then(resp => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail, { duration: 2000, position: 'bottom-center' })
          $(event.target).removeClass('loading')
          return
        }
        const ossBucket = new OSS.Wrapper({ region: OSS_REGION, bucket: OSS_BUCKET, ...resp.data.sts })
        ossBucket.head(this.videoUrlToAdd)
          .then(r => {
            if (r.res.headers['content-type'] !== 'video/mp4') {
              this.$toasted.error('文件必须是 .mp4 格式的视频')
            } else {
              const checkingInfoEl = this.$el.querySelector('#video-checking-info')
              checkingInfoEl.textContent = '文件地址和类型正确，正在获取其他信息...'
              const fullUrl = r.res.requestUrls[0]
              const videoEle = this.$el.querySelector('#v-placeholder-for-duration')

              const that = this

              function loadedmetadataHandler() {
                const seconds = parseInt(videoEle.duration + 0.5)
                if (seconds <= 5) {
                  that.$toasted.error('视频时长过短（小于5秒）或解析时长错误。请重试')
                  checkingInfoEl.textContent = ''
                  videoEle.removeEventListener('loadedmetadata', loadedmetadataHandler)
                  $(event.target).removeClass('loading')
                  return
                }
                const params = {
                  lessonId: that.lesson.id,
                  videoPath: that.videoUrlToAdd,
                  seconds,
                  action: 'update-video',
                }
                axios.post('/pad/j/holiday-course/lesson/update/', qs.stringify(params)).then(response => {
                  if (response.data.status === 0) {
                    that.$toasted.info('成功更新视频')
                    that.$emit('updateLesson', response.data.lesson)
                    that.openVideoUrlModal = false
                  } else {
                    that.$toasted.error('更新视频失败')
                    checkingInfoEl.textContent = ''
                  }
                  $(event.target).removeClass('loading')
                })
              }
              videoEle.src = fullUrl
              videoEle.addEventListener('loadedmetadata', loadedmetadataHandler)
            }
          })
          .catch(error => {
            if (error.status === 404) this.$toasted.error('地址文件不存在，请注意填写是否规范。')
            else if (error.status === 403) this.$toasted.error('未知错误，请先检查电脑本地时间是否校准。')
            else this.$toasted.error(error)
            $(event.target).removeClass('loading')
          })
      })
    },
    uploaderEventLisener() {
      const vDropZone = this.$el.querySelector('#video-dropzone')
      const vFileInput = this.$el.querySelector('#video-selector')
      const btnFileInput = this.$el.querySelector('#btn-video-selector')

      if (vDropZone) {
        vDropZone.addEventListener('dragover', (e) => {
          vDropZone.classList.add('drag-over')
          e.stopPropagation()
          e.preventDefault()
          e.dataTransfer.dropEffect = 'copy'
        })
        vDropZone.addEventListener('drop', (e) => {
          if (this.lesson.startTime < new PadDateTime(new Date()).formatDateTime()) {
            this.$toasted.error('这节课已过上课时间，不能上传/修改视频')
            return
          }
          vDropZone.classList.remove('drag-over')
          e.stopPropagation()
          e.preventDefault()
          this.validateAndUploadVideo(e.dataTransfer.files)
        })
      }
      if (vFileInput) {
        vFileInput.addEventListener('change', (e) => {
          if (this.lesson.startTime < new PadDateTime(new Date()).formatDateTime()) {
            this.$toasted.error(
              '这节课已过上课时间，不能上传/修改视频', { duration: 1000, position: 'bottom-center' })
            return
          }
          this.validateAndUploadVideo(e.target.files)
        })
      }
      if (btnFileInput) {
        btnFileInput.addEventListener('change', () => {
          if (this.lesson.startTime < new PadDateTime(new Date()).formatDateTime()) {
            this.$toasted.error(
              '这节课已过上课时间，不能上传/修改视频', { duration: 1000, position: 'bottom-center' })
            return
          }
          this.validateAndUploadVideo(btnFileInput.files)
        })
      }
    },
    formatResourcePreviewData(m) {
      const data = {
        fileType: m.fileType,
        fileUrl: m.filePath,
        kind: K_PAD_DOCUMENT,
      }
      this.previewItem = data
    },
    handleAliOssException(error) {
      if (error.name === 'RequestTimeTooSkewedError') {
        this.$toasted.error('请求发起的时间有错。请确保电脑本地时间处于校准状态。', {
          duration: 2000,
          position: 'bottom-center',
        })
      } else {
        this.$toasted.error(error, { duration: 2000, position: 'bottom-center' })
      }
      this.showProgressBar = false
      this.$nextTick(() => {
        this.percent = 0
        this.progressLabel = ''
      })
    },
    uploadToAliyun() {
      axios.get('/pad/j/misc/aliyun/sts').then(resp => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail, { duration: 2000, position: 'bottom-center' })
          return
        }
        const ext = this.video.file.name.split('.').pop().toLowerCase()
        this.showProgressBar = true
        this.progressLabel = '正在上传课时视频...请不要关闭本页。'
        const ossBucket = new OSS.Wrapper({ region: OSS_REGION, bucket: OSS_BUCKET, ...resp.data.sts })
        const padDate = new PadDateTime(new Date())
        const fileKey = `holidaycourse/${padDate.formatDate('/')}/${Math.random().toString(36).substr(2, 15)}.${ext}`

        const that = this
        ossBucket.multipartUpload(fileKey, this.video.file, {
          parallel: 4,
          partSize: 1024 * 1024,
          *progress(p) {
            that.percent = parseInt(p * 100)
          },
        }).then(response => {
          if (response.res.status === 200) {
            this.progressLabel = '上传成功'
            const params = {
              lessonId: this.lesson.id,
              videoPath: fileKey,
              seconds: parseInt(this.video.seconds + 0.5),
              action: 'update-video',
            }
            axios.post('/pad/j/holiday-course/lesson/update/', qs.stringify(params)).then(response => {
              if (response.data.status === 0) {
                this.$toasted.info('成功更新视频', { duration: 1000, position: 'bottom-center' })
                this.$emit('updateLesson', response.data.lesson)
              } else {
                this.$toasted.error('更新视频失败', { duration: 1000, position: 'bottom-center' })
              }
              this.video = { seconds: 0, file: null, sizeM: null }
            })
          } else {
            this.$toasted.error('上传文件失败', { duration: 1000, position: 'bottom-center' })
            this.video = { seconds: 0, file: null, sizeM: null }
          }
          this.showProgressBar = false
          this.$nextTick(() => {
            this.percent = 0
            this.progressLabel = ''
          })
        }).catch(error => this.handleAliOssException(error))
      }).catch(error => this.handleAliOssException(error))
    },
    checkVideoSize() {
      const videoEle = document.createElement('video')
      const fileUrl = URL.createObjectURL(this.video.file)
      videoEle.preload = 'metadata'
      videoEle.addEventListener('loadedmetadata', () => {
        this.video.seconds = videoEle.duration
        const avgSize = this.video.sizeM / (this.video.seconds / 60)
        if (this.video.sizeM > 50 && avgSize > 15) {
          this.$toasted.error('文件体积过大，请注意大小限制说明', { duration: 800, position: 'bottom-center' })
          this.video = { seconds: 0, file: null, sizeM: 0 }
        } else {
          this.uploadToAliyun()
        }
      })
      videoEle.src = fileUrl
    },
    validateAndUploadVideo(files) {
      if (!files.length) return
      if (files.length > 1) {
        this.$toasted.error('一个课时只能有一个视频文件。', { duration: 800, position: 'bottom-center' })
        return
      }
      const v = files[0]
      const ext = v.name.split('.').pop().toLowerCase()
      if (v.name.length <= (ext + 1)) { return null }
      if (ext !== 'mp4') {
        this.$toasted.error('仅支持 mp4 文件。', { duration: 800, position: 'bottom-center' })
        return
      }
      this.video.file = v
      this.video.sizeM = v.size / 1024 / 1024
      this.checkVideoSize()
    },
    deleteLesson() {
      const url = `/pad/j/holiday-course/lesson/${this.lesson.id}/delete/`
      axios.post(url).then((response) => {
        if (response.data.status === 0) {
          this.$toasted.info('已删除', { duration: 800, position: 'bottom-center' })
          this.$emit('lessonDeleted')
          this.openDeleteModal = false
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
      })
    },
  },
}
</script>
<style lang="less">
  @import "~assets/css/file-icon.less";
</style>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";


.info-section {
  .pad-dnd {
    border: none;
    &:hover, &:focus, &:active {
      .link.link-primary {
        color: white;
        background-color: @color-blue;
      }
    }
  }
}

.ui.form {
  padding: .5rem 0;
  min-height: 320px;
  .text-muted {
    font-size: .9rem;
  }
}

.video-url-modal .ui.form {
  min-height: 100px;
}
.ui.form .inline.field {
  display: flex;
  > label {
    position: relative;
    min-width: 5em;
    max-width: 5em;
    .line-wrap {
      position: absolute;
      top: 1.5em;
      left: 0;
    }
  }
  > div {
    flex: 1;
    flex-start: left;
  }
  .display-block {
    display: block;
    .ui.radio.checkbox {
      margin-top: 2px;
      height: 2rem;
      display: inline-block;
      label {
        width: 8rem;
      }
    }
    .text-muted {
      margin-left: .5rem;
      line-height: 24px;
    }
  }

  input {
    min-width: 24rem;
  }
  input[type=number] {
    min-width: 10rem;
    &.input-small {
      width: 5em;
      min-width: 3em;
      margin: 0 .5em;
      font-size: .8em;
      padding: 2px 1em;
      line-height: 1;
    }
  }

  .ui.dropdown {
    min-width: 24em;
  }
  .ui.checkbox label {
    min-width: 6em;
  }
  .ui.mini.checkbox {
    margin-bottom: .5em;
    label {
      color: #777;
      padding-left: 1.5em;
    }
  }
}

// 课时详情
.lesson-detail {
  padding-left: 2rem;
  margin-left: -2px;
  border-left: solid 2px #22242610;
  max-width: 60rem;
  .info-section {
    color: #888;
    .lesson-title {
      font-size: 1.2rem;
      margin: 1em 0 .3em 0;
      color: #444;
    }
  }
  .video-section {
    flex: 1;
    height: auto;
    background-color: white;
    video {
      background-color: #282A36;
      width: 100%;
      max-height: 30rem;
      box-shadow: 1px 1px 7px 0px rgba(0, 0, 0, 0.3);
    }
    .pad-dnd {
      padding: 2rem 0;
      height: 15rem;
    }
  }
  .lesson-materials {
    margin: 3rem 0 1rem;
    border-top: solid 1px #f4f5f6;
    position: relative;
    .action-section {
      position: absolute;
      left: 0;
      top: -1.5em;
    }
  }
  .material-item {
    align-items: center;
    padding: .5rem 0;
    cursor: pointer;
    &:hover, &:focus, &:active {
      background-color: #f4f5f6;
    }
    .pad.icon {
      height: 1.5em;
      width: 1.5em;
      padding: .2em;
      border-radius: 3px;
      box-shadow: -1px 1px 2px rgba(0, 0, 0, 0.15);
      margin-right: 1em;
    }
  }
}

</style>



