<template>
  <div>
    <div class="container-header">
      <div class="title-section">
        <h2 class="container-title">行政班列表</h2>
        <div class="actions" v-if="teacher.isManager">
          <button class="ui primary tiny blue button" @click="openCreateClassModal()">创建行政班</button>
          <button class="ui primary tiny blue button" @click="openBatchCreateClassModal()">批量创建行政班</button>
        </div>
      </div>
      <div class="filter-section">
        <div class="pill-filter filter-row" v-if="teacher.isManager">
          <span class="filter-row-label">年级：</span>
          <div>
            <span
              class="filter-label"
              @click="query.gradeId=0"
              :class="query.gradeId===0?'active':''">全部</span>
            <span
              class="filter-label"
              v-for="gid of school.gradeIds"
              @click="query.gradeId=gid"
              :class="query.gradeId===gid?'active':''">{{GRADES[gid]}}</span>
          </div>
        </div>
        <div class="pill-filter filter-row">
          <span class="filter-row-label">状态：</span>
          <div>
            <template v-for="(label, status) in classStatusMap">
              <span
                class="filter-label"
                @click="query.status=parseInt(status)"
                v-if="visibleStatuses.includes(parseInt(status))"
                :class="query.status==status?'active':''">{{label}}</span>
            </template>
          </div>
        </div>
        <div class="filter-row input-align" v-if="teacher.isManager">
          <span class="filter-row-label">搜索：</span>
          <div class="search ui small icon input">
            <input
              type="text"
              placeholder="班级名.."
              v-model.lazy="query.searchClassName">
            <i class="ui search icon"></i>
          </div>
        </div>
      </div>
    </div>
    <div class="class-list">
      <loader v-if="loading"></loader>
      <div class="content-empty" v-if="!loading && classes.length===0">
        <img :src="require('assets/img/content-empty.png')">
        <p>
          什么也没有，试试更改筛选条件
        </p>
      </div>
      <div class="flex-1">
        <table class="pad ui small table" v-if="!loading && classes.length"> <thead>
            <tr>
              <th class="left aligned">ID</th>
              <th class="left aligned">行政班</th>
              <th class="center aligned">年级</th>
              <th class="center aligned">班主任</th>
              <th class="center aligned">人数</th>
              <th class="center aligned">创建学年</th>
              <th class="center aligned">状态</th>
              <th class="right aligned">操作</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="c of classes"
              :key="'class-row-' + c.id"
              class="content-row"
              @click="openClassInNewTab(c)">
              <td class="left aligned">{{c.id}}</td>
              <td class="left aligned row-center-title" @click=""> {{c.name}} </td>
              <td class="center aligned">{{GRADES[c.gradeId]}}</td>
              <td class="center aligned">{{c.teacher.name}}</td>
              <td class="center aligned">{{c.nStudent}}</td>
              <td class="center aligned">{{c.year}}</td>
              <td class="center aligned"> {{classStatusMap[c.status]}}</td>
              <td class="right aligned">
                <span v-if="c.isNormal && teacher.isManager" class="link link-primary" @click.stop="openEditClassModal(c)">编辑</span>
                <span class="link link-primary" @click.stop="getClassExportFile(c)">导出学生</span>
                <span v-if="c.isNormal && teacher.isManager" class="link link-danger" @click.stop="klassToDelete=c">删除</span>
              </td>
            </tr>
          </tbody>
        </table>
        <pagination
          v-show="!loading"
          class="pagination"
          :totalPage="totalPage"
          :curPage="curPage"
          @update:selected="(page) => {fetchClasses(page)}"></pagination>
      </div>
    </div>
    <page-modal
      v-if="createClassModalOpened"
      key="create-class-modal"
      @close="createClassModalOpened=false"
      class="modal-small modal-padded">
      <div slot="content">
        <h3 class="ui header">新建行政班</h3>
        <p class="text-muted">起名参考：高三22班、17级高二02班 等...；行政班学年属性会根据创建时间自动生成。</p>
        <div class="ui small form">
          <div class="field required">
            <label>名称</label>
            <input type="text" v-model="newClass.name" placeholder="行政班名称不能为空">
          </div>
          <div class="field required">
            <label>班主任</label>
            <span class="text-danger" v-if="!teachers.length">系统里还没有老师，行政班需要分配班主任。请先去创建老师。</span>

            <div class="ui search selection dropdown" v-else>
              <input type="hidden" v-model="newClass.teacherId">
              <i class="dropdown icon"></i>
              <div class="text"></div>
              <div class="menu">
                <div
                  class="item"
                  v-for="t of teachers"
                  @click="newClass.teacherId=t.id"
                  :data-value="t.id"> {{t.name}} </div>
              </div>
            </div>
          </div>
          <div class="field required">
            <label>年级（创建后不可修改）</label>
            <div class="ui selection dropdown">
              <i class="dropdown icon"></i>
              <div class="default text"></div>
              <div class="menu">
                <div
                  class="item"
                  :data-value="gid"
                  @click="newClass.gradeId=gid"
                  v-for="gid in school.gradeIds">{{GRADES[gid]}}</div>
              </div>
            </div>
          </div>
          <button class="ui tiny blue button" @click="postCreateClass($event)">新建行政班</button>
        </div>
     </div>
    </page-modal>
    <page-modal
      v-if="editClassModalOpened && klassToEdit"
      key="edit-klass"
      @close="editClassModalOpened=false"
      class="modal-padded modal-small">
      <div slot="content">
        <h3 class="ui header">编辑行政班：{{klassToEdit.name}}</h3>
        <p class="text-muted">年级不可更改</p>
        <div class="ui small form">
          <div class="field required">
            <label>名称</label>
            <input type="text" v-model="klassToEdit.name" placeholder="行政班名称不能为空">
          </div>
          <div class="field required">
            <label>班主任</label>
            <span class="text-danger" v-if="!teachers.length">系统里还没有老师，行政班需要分配班主任。请先去创建老师。</span>
            <div class="ui search selection dropdown" v-else>
              <i class="dropdown icon"></i>
              <div class="text">{{klassToEdit.teacher.name}}</div>
              <div class="menu">
                <div class="item" v-for="t of getSuitableTeachers(klassToEdit.sourceKlass)" @click="klassToEdit.teacher=t"> {{t.name}} </div>
              </div>
            </div>
          </div>
          <button class="ui tiny blue button" @click="postEditClass($event)">保存修改</button>
        </div>
      </div>
    </page-modal>

    <page-modal
      v-if="klassToDelete"
      key="delete-klass"
      @close="klassToDelete=null"
      class="modal-padded modal-small">
      <div slot="content">
        <p>确定要删除 {{klassToDelete.name}} 吗？所关联的教学班也将一并删除。</p>
        <p class="text-muted">如本学年结束，行政班的学习任务结束，应该选择归档（而非删除）。（归档功能即将开放）</p>
        <p>
          <button class="ui tiny red button right floated" @click="postDeleteClass($event)">删除</button>
        </p>
      </div>
    </page-modal>
    <page-modal
      v-if="batchCreateClassModalOpened"
      key="batch-create-class-modal"
      @close="batchCreateClassModalOpened=false"
      class="modal-padded">
      <div slot="content" class="excel-import-modal">
        <h3 class="ui header">批量创建行政班</h3>
        <p class="text-muted">通过 excel 模版批量导入，一次创建不多于30个行政班。
          <a class="link link-primary" href="https://padmedia.oss-cn-beijing.aliyuncs.com/excel/模版-批量创建行政班.xlsx"><i class="download icon"></i> 点此下载导入模版</a><br>
          <span class="text-danger">请注意查看模版第二页工作表中的填写说明</span>
        </p>
        <div v-if="!importSucceed || errorInfos.length">
          <span class="file-input-wrapper">
            <button class="ui tiny button">选择要导入的文件</button>
            <label for="btn-excel-import"></label>
            <input type="file" id="btn-excel-import" @change="processExcelFile($event)">
            <span class="text-muted">{{excelFile ? excelFile.name : ''}}</span>
          </span>
          <button class="ui tiny primary button right floated" v-if="excelFile" @click="postBatchCreateClass($event)">上传并导入</button>
          <div v-if="errorInfos.length" class="error-infos">
            <p class="text-danger">文件数据或格式不正确，请修改后重新上传文件导入：</p>
            <ul>
              <li v-for="info of errorInfos">{{info}}</li>
            </ul>
          </div>
        </div>
        <div v-else>
          <p>导入成功！请关闭弹窗</p>
        </div>
      </div>
    </page-modal>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import PageModal from 'components/page-modal'
import Pagination from 'components/pagination'
import Loader from 'components/loader'
import Klass from 'assets/js/class'
import {
  CLASS_STATUS_MAP,
  CLASS_STATUS_NORMAL,
  CLASS_STATUS_ARCHIVED,
  GRADES,
  SUBJECTS,
} from '@/assets/js/consts/common'

const qs = require('qs')

export default {
  name: 'org-class-list',
  data() {
    return {
      GRADES,
      SUBJECTS,
      classes: [],

      curPage: 1,
      totalPage: 0,
      loading: false,
      visibleStatuses: [CLASS_STATUS_NORMAL, CLASS_STATUS_ARCHIVED],
      classStatusMap: CLASS_STATUS_MAP,
      teachers: null,

      query: {
        status: 1,
        gradeId: 0,
        searchClassName: '',
      },

      createClassModalOpened: false,
      newClass: {
        name: '',
        gradeId: 0,
        teacherId: 0,
      },

      editClassModalOpened: false,
      klassToEdit: null,

      klassToDelete: null,

      batchCreateClassModalOpened: false,
      excelFile: null,
      importSucceed: false,
      errorInfos: []
    }
  },
  components: { Pagination, Loader, PageModal },
  mounted() {
    this.fetchClasses()
    if (this.teacher.isManager) {
      this.fetchTeachers()
    }
  },
  watch: {
    'query.status': function() {
      this.fetchClasses()
    },
    'query.gradeId': function() {
      this.fetchClasses()
    },
    'query.searchClassName': function(val, oldVal) {
      if (val && this.query.gradeId) {
        this.query.gradeId = 0
        return
      }
      this.fetchClasses()
    },
  },
  computed: {
    ...mapState(['school', 'teacher']),
  },
  methods: {
    getSuitableTeachers(klass) {
      if (!klass || !this.teachers) return []
      return this.teachers.filter(t => t.gradeId === klass.bigGrade)
    },
    openBatchCreateClassModal() {
      this.excelFile = null
      this.errorInfos = []
      this.importSucceed = false
      this.batchCreateClassModalOpened = true
    },
    processExcelFile(event) {
      const file = event.target.files[0]
      const ext = file.name.split('.').pop().toLowerCase()
      if (!['xls', 'xlsx'].includes(ext)) {
        this.$toasted.error('文件格式不支持')
        return
      }
      this.excelFile = file
    },
    postBatchCreateClass (event) {
      if (!this.excelFile) return
      const formData = new FormData()
      formData.append('excel', this.excelFile)
      const url = `/pad/j/management/classes/batch_create/`
      $(event.target).addClass('loading')
      axios.post(url, formData, { headers: { 'Content-Type': 'multipart/form-data' } }).then(resp => {
        if (resp.data.status === 1) {
          if ('errorInfos' in resp.data) {
            this.errorInfos = resp.data.errorInfos
          } else {
            this.$toasted.error(resp.data.detail)
            this.errorInfos = []
          }
        } else if (resp.data.status === 0) {
          this.errorInfos = []
          this.fetchClasses()
          this.importSucceed = true
        }
        $(event.target).removeClass('loading')
      })
    },
    openClassInNewTab(klass) {
      window.open(this.$router.resolve({ name: 'org-class-detail', params: { id: klass.id } }).href, '_blank')
    },
    getClassExportFile(klass) {
      window.open(`/pad/manage/classes/${klass.id}/export`, '_blank')
    },
    fetchTeachers() {
      const url = '/pad/j/management/teachers'
      return axios.get(url).then(resp => {
        if (resp.data.status === 0) {
          this.teachers = resp.data.teachers
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
      })
    },
    postCreateClass(event) {
      if (!(this.newClass.name && this.newClass.gradeId && this.newClass.teacherId)) {
        this.$toasted.error('信息填写不完整。')
        return
      }
      const url = '/pad/j/management/classes/create/'
      $(event.target).addClass('loading')
      axios.post(url, qs.stringify(this.newClass)).then(resp => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('创建成功')
          this.classes.splice(0, 0, new Klass(resp.data.klass))
          this.createClassModalOpened = false
        }
        $(event.target).removeClass('loading')
      })
    },
    openCreateClassModal() {
      if (!this.teachers) {
        this.$toasted.info('正在获取老师数据，请稍后几秒后重试...')
        return
      }
      this.newClass.name = ''
      this.newClass.gradeId = 0
      this.newClass.teacherId = 0
      this.createClassModalOpened = true
      this.$nextTick(() => {
        $(this.$el.querySelectorAll('.dropdown')).dropdown()
      })
    },
    openEditClassModal(klass) {
      if (!this.teachers) {
        this.$toasted.info('正在获取老师数据，请稍后几秒后重试...')
        return
      }
      this.klassToEdit = { teacher: klass.teacher, name: klass.name, sourceKlass: klass }
      this.editClassModalOpened = true
      this.$nextTick(() => {
        $(this.$el.querySelectorAll('.dropdown')).dropdown()
      })
    },
    postDeleteClass(event) {
      const url = `/pad/j/management/classes/${this.klassToDelete.id}/delete/`
      $(event.target).addClass('loading')
      axios.post(url).then(resp => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('已删除')
          if (this.classes.length <= 1) {
            this.fetchClasses()
          } else {
            const index = this.classes.indexOf(this.klassToDelete)
            if (index >= 0) this.classes.splice(index, 1)
          }
          this.klassToDelete = false
        }
        $(event.target).removeClass('loading')
      })
    },
    postEditClass(event) {
      this.klassToEdit.name = this.klassToEdit.name.trim()
      if (!(this.klassToEdit.teacher && this.klassToEdit.name)) {
        this.$toasted.error('信息填写不完整。')
        return
      }
      const url = `/pad/j/management/classes/${this.klassToEdit.sourceKlass.id}/update/`
      $(event.target).addClass('loading')
      const params = { teacherId: this.klassToEdit.teacher.id, name: this.klassToEdit.name }
      axios.post(url, qs.stringify(params)).then(resp => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('创建成功')
          const index = this.classes.indexOf(this.klassToEdit.sourceKlass)
          this.editClassModalOpened = false
          if (index >= 0) this.classes.splice(index, 1, new Klass(resp.data.klass))
        }
        $(event.target).removeClass('loading')
      })
    },
    fetchClasses(page = 1) {
      // 搜索的时候不分页
      this.query.searchClassName = this.query.searchClassName.trim()
      const url = this.query.searchClassName ? '/pad/j/management/classes/all' : '/pad/j/management/classes'
      const params = {
        fields: ['id', 'name', 'status', 'year', 'gradeId', 'teacher', 'nStudent'],
        page,
        ...this.query,
      }

      this.loading = true
      axios.get(url, { params }).then(resp => {
        if (resp.data.status === 0) {
          this.classes = []
          for (const classData of resp.data.classes) this.classes.push(new Klass(classData))
          if (this.query.searchClassName) {
            this.curPage = 1
            this.totalPage = 1
          } else {
            this.totalPage = resp.data.totalPage
            this.curPage = resp.data.page
          }
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
        this.loading = false
      })
    },
  },
}
</script>

<style lang="less" scoped>
.class-list {
  position: relative;
  margin: 2rem 2rem;
  min-height: 20rem;
  .ui.table {
    color: #555;
  }
}

.excel-import-modal {
  .file-input-wrapper {
    display: inline-block;
    position: relative;
    input {
      display: none;
    }
    label {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      width: 100%;
    }
  }
}

.error-infos {
  margin-top: 2rem;
  border-top: solid 1px #ddd;
  font-size: .9rem;
  ul {
    list-style-type: none;
    padding-left: 1rem;
    color: #777;
  }
}
</style>



