<template>
  <div>
    <div class="container-header bordered">
      <div class="title-section">
        <h2 class="container-title">创建名校课程</h2>
      </div>
    </div>
    <div class="ui form">
      <div class="inline field required">
        <label for="course-title">课程名称</label>
        <input type="text" id="course-title" v-model="course.title" placeholder="不超过100个字符">
      </div>
      <div class="inline field required">
        <label for="course-subject-id">学科</label>
        <div>
          <div class="ui selection dropdown" id="course-subject-id">
            <i class="dropdown icon"></i>
            <span class="text">{{subjects[course.subjectId]}}</span>
            <div class="menu">
              <div
                class="item"
                v-for="(subjectLabel, subjectId) in subjects"
                v-if="parseInt(subjectId) !== course.subjectId"
                :data-value="subjectId"
                :key="'subject-'+subjectId">{{subjects[subjectId]}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="inline field required">
        <label>展示设备</label>
        <span>
          <div class="ui radio checkbox" v-for="(label, key) in DEVICE_TYPE_MAP">
            <input type="radio" :id="'dy-' + key" :value="parseInt(key)" v-model="course.deviceType">
            <label :for="'dy-'+key">{{label}}</label>
          </div>
        </span>
      </div>
      <div class="inline field required" v-if="school.qingbeiPeiyouOpened">
        <label>课程类型</label>
        <span>
          <div class="ui radio checkbox" v-for="(label, key) in COURSE_TYPE_MAP">
            <input type="radio" :id="'ct-' + key" :value="parseInt(key)" v-model="course.courseType">
            <label :for="'ct-' + key">{{label}}</label>
          </div>
        </span>
      </div>
      <div class="inline field required">
        <label for="main-teacher-name">主讲老师</label>
        <input type="text" id="main-teacher-name" v-model="course.mainTeacherName" placeholder="不超过30个字符">
      </div>
      <div class="inline field required">
        <label for="date-range">起止日期</label>
        <input type="text" class="date-range" id="date-range">
      </div>
  <!--     <div class="inline field required">
        <label for="start-time">上课时间</label>
        <div>
          <time-picker
            place-holder-text="一节课的默认开始时间"
            :canClear="false"
            :noCalendar="true"
            dateFormat="H:i"
            minDate="00:00"
            maxDate="23:99"
            v-model="course.startTime"></time-picker>
          <span class="text-muted">作为新建课时的默认开始时间，可在课时处具体修改。</span>
        </div>
      </div> -->
      <div class="inline field required">
        <label for="main-teacher-name">上课年级</label>
        <div class="display-block">
          <div class="d-flex">
            <div class="ui radio checkbox" v-for="gid of JUNIOR_HIGH_SCHOOL_GRADES">
              <input type="radio" :id="'course-gid-'+gid" :value="gid" v-model="course.gradeId">
              <label :for="'course-gid-'+gid">{{GRADES[gid]}}</label>
            </div>
          </div>
          <div class="d-flex">
            <div class="ui radio checkbox" v-for="gid of SENIOR_HIGH_SCHOOL_GRADES">
              <input type="radio" :id="'course-gid-'+gid" :value="gid" v-model="course.gradeId">
              <label :for="'course-gid-'+gid">{{GRADES[gid]}}</label>
            </div>
          </div>
        </div>
      </div>
      <br>
      <div class="inline field required">
        <label>报名范围</label>
        <span>
          <div class="ui radio checkbox">
            <input type="radio" id="range-all-student" :value="false" v-model="course.nativeStudentOnly">
            <label for="range-all-student">全部学生</label>
          </div>
          <div class="ui radio checkbox">
            <input type="radio" id="range-native-student" :value="true" v-model="course.nativeStudentOnly">
            <label for="range-native-student">只限本校学生</label>
          </div>
        </span>
      </div>
      <template v-if="school">
        <div class="inline field" v-show="course.nativeStudentOnly">
          <label>参与权限</label>
          <div>
            <div>
              <div class="ui radio checkbox">
                <input type="radio" id="all-native-student" :value="1" v-model="course.applyCondition.targetKind">
                <label for="all-native-student">全校</label>
              </div>
              <div class="ui radio checkbox">
                <input type="radio" id="by-grade" :value="2" v-model="course.applyCondition.targetKind">
                <label for="by-grade">年级</label>
              </div>
              <div class="ui radio checkbox">
                <input type="radio" id="by-class" :value="3" v-model="course.applyCondition.targetKind">
                <label for="by-class">行政班级</label>
              </div>
            </div>
            <div class="grades-selector" v-show="course.applyCondition.targetKind === 2">
              <div class="ui mini checkbox" v-for="gid in validGradeIds" v-if="validGradeIds.length">
                <input type="checkbox" tabindex="0" class="hidden" :value="gid" v-model="applyGradeIds"
                  :id="'gid-'+gid">
                <label :for="'gid-'+gid">{{GRADES[gid]}}</label>
              </div>
              <template v-else>
                当前没有年级
              </template>
            </div>
            <div class="classes-selector" v-show="course.applyCondition.targetKind === 3">
              <div class="ui mini checkbox" v-for="c in school.classes" v-if="school.classes.length">
                <input type="checkbox" tabindex="0" class="hidden" :value="c.id" v-model="applyClassIds"
                  :id="'cid-'+c.id">
                <label :for="'cid-'+c.id">{{c.name}}</label>
              </div>
              <template v-else>
                没有可选的行政班
              </template>
            </div>
          </div>
        </div>
      </template>
      <br>
      <div class="inline field required">
        <label>
          课程说明
          <span class="line-wrap link link-primary" @click="previewHtml=course.descriptionHtml">预览</span>
        </label>
        <div>
          <text-editor v-model="course.descriptionRich" class="textarea"></text-editor>
        </div>
      </div>
      <div class="inline field required">
        <label>纸质教辅</label>
        <div>
          <div class="ui radio checkbox">
            <input type="radio" id="has-pm-true" :value="true" v-model="course.hasPaperMaterial">
            <label for="has-pm-true">有</label>
          </div>
          <div class="ui radio checkbox">
            <input type="radio" id="has-pm-false" :value="false" v-model="course.hasPaperMaterial">
            <label for="has-pm-false">无</label>
          </div>
        </div>
      </div>
      <div class="inline field required" v-if="course.hasPaperMaterial">
        <label for="pm-title">教辅名称</label>
        <input type="text" id="pm-title" v-model="course.paperMaterialTitle" placeholder="不超过100个字符">
      </div>
      <div class="inline field required">
        <label>回放设置</label>
        <div class="display-block">
          <div class="d-flex">
            <div class="ui radio checkbox">
              <input type="radio" id="playback-absolute" :value="1" v-model="course.playbackMethod">
              <label for="playback-absolute">统一时间点</label>
            </div>
            <time-picker place-holder-text="选择日期"
              key="playback-absolute-time"
              v-model="course.playbackAbsoluteDate"
              :enable-time="false"
              date-format="Y-m-d"
              alt-format="Y-m-d"
              :can-clear="false"
              min-date="today"
              v-show="course.playbackMethod===1"
              ></time-picker>
            <span
              v-show="course.playbackMethod===1"
              class="text-muted"
              >该时间前都不能回放，之后所有课时都可回放</span>
          </div>
          <div class="d-flex">
            <div class="ui radio checkbox">
              <input type="radio" id="playback-relative" :value="2" v-model="course.playbackMethod">
              <label for="playback-relative">相对课时</label>
            </div>
            <span v-show="course.playbackMethod===2">
              <input
                type="number"
                :value="course.playbackRelativeDays"
                @change="changePlaybackRelativeDays"
                class="input-small">天后可以回放
            </span>
          </div>
        </div>
      </div>
      <div class="inline field required">
        <label for="original-price">价格</label>
        <input
          type="number" id="original-price"
          min="0"
          max="9999"
          :value="course.originalPrice"
          @change="changeCoursePrice($event)" placeholder="0 即为免费">
      </div>
      <div class="inline field action-section">
        <label></label>
        <div>
          <button class="ui primary button" @click="createCourse">创建新课程</button>
          <span class="text-muted">创建后再上传视频与资料</span>
        </div>
      </div>
    </div>
    <html-preview-modal
      :preview-html="previewHtml"
      @close="previewHtml=null"
      ></html-preview-modal>
  </div>
</template>

<script>
import { Mandarin } from 'flatpickr/dist/l10n/zh'
import { mapState } from 'vuex'
import axios from 'axios'
import Icon from 'components/icons/Icon'
import {
  NORMAL_SUBJECTS,
  GRADES,
  JUNIOR_HIGH_SCHOOL_GRADES,
  SENIOR_HIGH_SCHOOL_GRADES,
} from '@/assets/js/consts/common'
import {
  COURSE_TYPE_MAP,
  DEVICE_TYPE_MAP,
} from '@/assets/js/consts/holiday-course'
import { HolidayCourse } from '@/assets/js/holiday-course'
import TimePicker from 'components/time-picker'
import TextEditor from 'components/simple-text-editor'
import HtmlPreviewModal from 'components/html-preview-modal'

const qs = require('qs')
const flatpickr = require('flatpickr')
const subjects = Object.assign({99: '综合（可在课时中单独设置学科)'}, NORMAL_SUBJECTS)

export default {
  name: 'hc-create-course',
  data() {
    return {
      subjects,
      GRADES,
      course: new HolidayCourse({ school: this.school }),
      applyGradeIds: [],
      applyClassIds: [],
      dateRangeElement: null,
      JUNIOR_HIGH_SCHOOL_GRADES,
      SENIOR_HIGH_SCHOOL_GRADES,
      COURSE_TYPE_MAP,
      DEVICE_TYPE_MAP,
      previewHtml: null,
    }
  },
  props: {
    activeTab: { type: String },
  },
  components: { Icon, TimePicker, TextEditor, HtmlPreviewModal },
  mounted() {
    $(this.$el).find('.ui.dropdown').dropdown({
      action: 'hide',
      onChange: value => { this.course.subjectId = parseInt(value) },
    })
    this.dateRangeElement = this.$el.querySelector('.date-range')
    flatpickr(this.dateRangeElement, {
      locale: Mandarin,
      endableTime: false,
      minDate: 'today',
      mode: 'range',
      dateFormat: 'Y-m-d',
      altFormat: 'Y-m-d',
      onChange: (selectedDates) => {
        if (selectedDates.length === 2) {
          this.course.startDate = selectedDates[0]
          this.course.endDate = selectedDates[1]
        }
      },
    })
  },
  computed: {
    validGradeIds() {
      if (!this.school) return []
      const gradeIds = []
      for (let c of this.school.classes) {
        if (!gradeIds.includes(c.gradeId)) {
          gradeIds.push(c.gradeId)
        }
      }
      return gradeIds
    },
    ...mapState(['school']),
  },
  watch: {
    applyGradeIds: function (val) {
      if (this.course.applyCondition.targetKind === 2) {
        this.course.applyCondition.targetIds = val
      }
    },
    applyClassIds: function (val) {
      if (this.course.applyCondition.targetKind === 3) {
        this.course.applyCondition.targetIds = val
      }
    },
    'course.applyCondition.targetKind': function (val) {
      if (val === 1) {
        this.course.applyCondition.targetIds = [this.school.id]
      } else if (val === 2) {
        this.course.applyCondition.targetIds = this.applyGradeIds
      } else if (val === 3) {
        this.course.applyCondition.targetIds = this.applyClassIds
      }
    },
    'course.nativeStudentOnly': function (val) {
      if (val && this.course.applyCondition.targetKind === 0) {
        this.course.applyCondition.targetKind = 1
      } else if (!val) {
        this.course.applyCondition.targetKind = 0
      }
    },
  },
  methods: {
    changePlaybackRelativeDays(event) {
      let days = parseInt(event.target.value)
      if (days < 0) days = 0
      else if (days > 999) days = 999
      event.target.value = days
      this.course.playbackRelativeDays = days
    },
    changeCoursePrice(event) {
      let price = event.target.value

      if (price < 0) {
        price = 0
      } else if (price > 9999) {
        price = 9999
      }
      event.target.value = parseFloat(price).toFixed(2)
      this.course.originalPrice = event.target.value
    },
    createCourse() {
      const [result, msg] = this.course.validate()
      if (!result) {
        this.$toasted.error(msg, { duration: 800, position: 'bottom-center' })
        return
      }
      const url = '/pad/j/holiday-course/create/'
      axios.post(url, qs.stringify({ courseData: this.course.stringify() })).then((response) => {
        if (response.data.status === 0) {
          this.$toasted.info('创建成功', { duration: 800, position: 'bottom-center' })
          this.$router.push({ name: 'hc-course-list' })
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
      })
    },
  },
}
</script>

<style src="flatpickr/dist/themes/material_blue.css"></style>
<style lang="less" scoped>
@import "~assets/css/base-variables.less";
.ui.form {
  margin: 1rem 2rem;
  .field.action-section {
    margin-top: 2rem;
  }
  .text-muted {
    font-size: .9rem;
  }
}
.ui.form .inline.field {
  display: flex;
  > label {
    position: relative;
    min-width: 5em;
    max-width: 5em;
    .line-wrap {
      position: absolute;
      top: 1.5em;
      left: 0;
    }
  }
  > div {
    flex: 1;
    flex-start: left;
  }
  .display-block {
    display: block;
    .ui.radio.checkbox {
      margin-top: 2px;
      height: 2rem;
      display: inline-block;
      label {
        width: 8rem;
      }
    }
    .text-muted {
      margin-left: .5rem;
      line-height: 24px;
    }
  }

  input {
    min-width: 32rem;
    max-width: 50rem;
  }
  input[type=number] {
    min-width: 10rem;
    &.input-small {
      width: 5em;
      min-width: 3em;
      margin: 0 .5em;
      font-size: .8em;
      padding: 2px 1em;
      line-height: 1;
    }
  }

  .textarea {
    max-width: 50rem;
  }
  .ui.dropdown {
    min-width: 20em;
  }
  .classes-selector, .grades-selector {
    margin-top: 1em;
    padding: 1em 1em .5em 1em;
    background-color: #f8f8f8
  }
  .ui.checkbox label {
    min-width: 6em;
  }
  .ui.mini.checkbox {
    margin-bottom: .5em;
    label {
      color: #777;
      padding-left: 1.5em;
    }
  }
}

</style>



