<template>
  <div>
    <div class="container-header">
      <div class="title-section">
        <h2 class="container-title">教室列表</h2>
        <div class="actions"></div>
      </div>
    </div>
    <div class="classroom-list">
      <table class="ui pad small table">
        <thead>
          <tr>
            <th>ID</th>
            <th>名称</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="classroom in classrooms">
            <td>{{classroom.id}}</td>
            <td>{{classroom.name}}</td>
            <td>
              <span class="link link-primary" @click="openClassroomInNewTab(classroom)">设置周课表</span>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'

/*
 * 进入课表的临时入口
 */

export default {
  name: 'classroom-list',
  data() {
    return {
      classrooms: [],
    }
  },
  mounted() {
    this.fetchClassrooms()
  },
  computed: {
    ...mapState(['teacher']),
  },
  methods: {
    fetchClassrooms(page = 1) {
      const url = '/pad/j/classrooms'
      const params = { page }
      this.loading = true
      return axios.get(url, { params }).then(resp => {
        if (resp.data.status === 0) {
          this.classrooms = resp.data.classrooms
        } else if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        }
        this.loading = false
      })
    },
    openClassroomInNewTab(classroom) {
      window.open(this.$router.resolve({ name: 'classroom-detail', params: { id: classroom.id } }).href, '_blank')
    },
  },
}
</script>

<style lang="less" scoped>
.classroom-list {
  position: relative;
  margin: 2rem 2rem;
  min-height: 20rem;
  .ui.table {
    color: #555;
    .content {
      width: 50%;
    }
  }
}
.ui.small.table .classroom-row {
  .text-muted {
    font-size: .95em;
  }
  .classroom-content {
    h3 {
      font-size: 1.1rem;
      margin: .6em 0;
      color: #333;
    }
    p {
      font-size: 1rem;
      margin: .5em 0;
      line-height: 1.5;
    }
  }
  .classroom-action {
    width: 20em;
  }
}
</style>



