<template>
  <div class="ui large vertical menu" v-if="teacher && school">
    <div class="item">
      <div class="header">人员/班级管理</div>
      <div class="menu">
        <a
          v-if="teacher.isManager || teacher.isHeadTeacher"
          class="item"
          :class="$route.path.startsWith('/classes')?'active':''"
          @click="$router.push({name: 'org-class-list'})">行政班</a>
        <a
          v-if="teacher.isManager || teacher.isCourseTeacher"
          class="item" href="/pad/manage/courses">教学班</a>
        <a
          v-if="teacher.isManager"
          class="item" href="/pad/manage/teachers">教师</a>
        <a
          v-if="teacher.isManager"
          class="item"
          :class="$route.path.startsWith('/students')?'active':''"
          @click="$router.push({name: 'org-student-list'})">学生</a>
      </div>
    </div>
    <div class="item">
      <div class="header">教务管理</div>
      <div class="menu">
        <template v-if="teacher.isManager">
          <a class="item" href="/pad/manage/phone_control">电话管控</a>
          <a class="item" href="/pad/manage/cloud_note">云笔记管控</a>
          <a class="item" href="/pad/manage/message_control">家校沟通管理</a>
        </template>
        <a
          class="item"
          :class="$route.path.startsWith('/notices')?'active':''"
          @click="$router.push({name: 'feat-notice-list'})">通知管理</a>
      </div>
    </div>
    <div class="item" v-if="teacher.isManager">
      <div class="header">增值服务管理</div>
      <div class="menu">
        <a
          v-if="school.holidayCourseOpened"
          class="item"
          :class="$route.path.startsWith('/holiday-courses')?'active':''"
          @click="$router.push({name: 'hc-course-list'})">名校课程管理</a>
        <a class="item" href="/pad/manage/apps">应用管理</a>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'vertical-navbar',
  data() {
    return {}
  },
  props: {
    activeTab: { type: String },
  },
  mounted() {
  },
  computed: {
    ...mapState(['teacher', 'school']),
  },
}
</script>

<style lang="less" scoped>
@color-blue: #42A5F5;

.ui.vertical.menu {
  border-radius: 0;
  width: ~"calc(6rem + 80px)";
  margin: 0;
  border: none;
  padding-top: 1rem;
  padding-bottom: 1rem;
  box-shadow: none;
  background-color: transparent;
  &>.item {
    padding-top: 1rem;
  }

  .item .menu .active.item {
    color: @color-blue;
    background-color: #f9fafb;
  }
  .item:before {
    background: none;
  }
}

</style>



