<template>
<modal @close="$emit('close')" class="modal-default modal-large">
  <h3 slot="header">编辑课时资料</h3>
  <div slot="body" class="modal-body-container">
    <form class="ui form">
      <div class="field">
        <table class="ui small very basic celled table">
          <thead>
            <tr>
              <th>编辑文件名</th>
              <th class="center aligned">扩展名</th>
              <th class="center aligned">课堂类型</th>
              <th class="center aligned">操作<br></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="m of materials">
              <td class="d-flex">
                <i class="pad icon" :class="'ft-'+m.fileType"></i>
                <input type="text" v-model="m.name" maxlength="50" required="">
              </td>
              <td class="center aligned">{{m.ext}}</td>
              <td class="center aligned">
                <div class="ui slection dropdown">
                  <i class="dropdown icon"></i>
                  <span class="text">{{classKindMap[m.classKind]}}</span>
                  <div class="menu">
                    <div
                      v-for="(label, key) in classKindMap"
                      v-if="parseInt(key) !== m.classKind"
                      @click="selectClassKind(m, parseInt(key), $event)"
                      :key="'classkind-'+key"
                      class="item">{{label}}</div>
                  </div>
                </div>
              </td>
              <td class="center aligned">
                <span class="link link-primary" @click="formatResourcePreviewData(m)">预览</span>
                <span class="link link-danger" @click="remove(m)">删除</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </form>
  <resource-preview :previewItem="previewItem" @close="previewItem=null"></resource-preview>
  </div>
  <div slot="footer">
    <button class="ui small primary button" @click="saveChange">保存</button>
  </div>
</modal>
</template>

<script>
import Modal from 'components/modal'
import axios from 'axios'
import { CLASS_KIND_MAP, K_PAD_DOCUMENT } from 'assets/js/consts/common'
import { HCLessonMaterial } from '@/assets/js/holiday-course'
import ResourcePreview from 'components/resource-preview'

const qs = require('qs')

export default {
  name: 'hc-edit-material-list',
  props: {
    course: { type: Object, required: true },
    lesson: { type: Object, required: true },
  },
  data() {
    // 复制资料
    const materials = []
    for (const m of this.lesson.materials) {
      let name = ''
      let ext = ''
      const parts = m.title.split('.')
      if (parts.length === 1) {
        ext = ''
        name = parts[0]
      } else {
        ext = parts.pop().toLowerCase()
        name = m.title.slice(0, -(ext.length + 1))
      }

      const newM = new HCLessonMaterial(m)
      newM.name = name
      newM.ext = ext
      materials.push(newM)
    }
    return {
      classKindMap: CLASS_KIND_MAP,
      materials,
      previewItem: null,
    }
  },
  components: { Modal, ResourcePreview },
  mounted() {
    $(this.$el).find('.ui.dropdown').dropdown({ action: 'hide' })
  },
  methods: {
    remove(m) {
      const index = this.materials.indexOf(m)
      if (index >= 0) this.materials.splice(index, 1)
    },
    formatResourcePreviewData(m) {
      const data = {
        fileType: m.fileType,
        fileUrl: m.filePath,
        kind: K_PAD_DOCUMENT,
      }
      this.previewItem = data
    },
    selectClassKind(f, classKind) {
      f.classKind = classKind
      $(this.$el).find('.ui.dropdown').dropdown('hide')
    },
    sizeDisplay(size) {
      if (size > 1000000) return `${(size / 1000000).toFixed(1)}MB`
      if (size > 1000) return `${(size / 1000).toFixed(1)}KB`
      return `${size}B`
    },
    saveChange() {
      const url = '/pad/j/holiday-course/material/batch/edit/'
      const params = {
        lessonId: this.lesson.id,
        materialData: JSON.stringify(this.materials),
      }
      axios.post(url, qs.stringify(params)).then(response => {
        if (response.data.status === 0) {
          this.$toasted.info('保存成功', { duration: 800, position: 'bottom-center' })
          this.$emit('updateLesson', response.data.lesson)
          this.$emit('close')
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
      })
    },
  },
}
</script>


<style lang="less" scoped>
.ui.form input[type=text] {
  padding: .5em 1em;
}

.ui.table tfoot th.text-danger {
  color: #dd4b39;
}

.ui.form {
  flex: 1;
  padding: 1.5em 1em;
  border-radius: 0;
  min-height: 80vh;
  .d-flex {
    align-items: center;
  }
  .pad.icon {
    height: 1.5em;
    width: 1.5em;
    padding: .2em;
    border-radius: 3px;
    box-shadow: -1px 1px 2px rgba(0, 0, 0, 0.15);
    margin-right: 1em;
  }
}
.ui.celled.table tr td, .ui.celled.table tr th, .ui.table thead th {
  border-left: none;
  border-top: none;
  border-bottom: none;
}

</style>



